<template>
    <div class="tactics_list" ref="tactics_list">
        <div class="tactics_container">
            <div class="part" v-for="(p,item,id) in tacticsDataArr" :key="p.index">
                <div class="border"></div>
                <div class="topic">
                    {{ topic[id] }}
                </div>
                <div class="tactics_box" v-for="t in p" :key="t.index" v-show="ShowTacticsRule(t)" @click="SelectTactics(t)">
                    <div class="icon">
                        <TacticsIcon :tacticsData="t"/>
                    </div>
                    <div class="load_mask" v-if="ShowTacticsMaskRule(t)">
                        <div class="warrior_name">{{ GetWarriorName(t.isload) }}</div>
                    </div>
                    <div class="battle_load_mark cover" v-if="IsLoadBattleTeam(t)">已学习</div>
                </div>
            </div>
        </div>
        <TopNav title="战法列表"/>
        <!-- 路由 -->
        <router-view/>
    </div>
</template>

<script>
import TopNav from '@/components/container/TopNav.vue'
import TacticsIcon from '../../components/style/TacticsIcon.vue'
export default {
    name : 'TacticsList',
    components:{
        TopNav,
        TacticsIcon
    },
    data(){
        return{
            tacticsDataArr:this.$store.tactics,
            model : '',
            topic : ['指挥战法',"主动战法","追击战法","被动战法"],
            routeArr : [],
        }
    },
    methods:{
        GetWarriorName(wId){
            let str = `${this.$store.warriors[this.$store.warriorIdxTable[wId]].camp}-
            ${this.$store.warriors[this.$store.warriorIdxTable[wId]].name}-
            ${this.$store.warriors[this.$store.warriorIdxTable[wId]].arms}`
            return str;
        },
        IsLoadBattleTeam(tactics){
            if(this.routeArr[0] != "battle") return false;
            for(let i in this.$store.buffer.curBattleTeam){
                for(let j in this.$store.buffer.curBattleTeam[i].tacticsArr){
                    if(this.$store.buffer.curBattleTeam[i].tacticsArr[j].name == tactics.name){
                        return true;
                    }
                }
            }
            return false;
        },
        //战法遮罩层是否显示
        ShowTacticsMaskRule(data){
            if(this.routeArr[0] == 'battle') return false;
            if(data.isload != -1) return true;
            return false
        },
        //战法是否显示
        ShowTacticsRule(data){
            if(this.routeArr[0] == 'battle') return true
            if(data.lock) return false;
            return true;
        },
        //战法的图像
        TactImgUrl(t){
            switch(t.type){
                case "command" : return require('../../assets/ui/ui (92).png');
                case "active" : return require('../../assets/ui/ui (93).png');
                case "combo" : return require('../../assets/ui/ui (94).png');
                case "passive" : return require('../../assets/ui/ui (91).png');
            }
        },
        //选中战法放入对应的战法槽里
        SelectTactics(tacticsData){
            if(this.IsLoadBattleTeam(tacticsData)){
                let that = this;
                let f = function(){
                    that.$store.buffer.curTactics = tacticsData
                    that.$router.push({
                        path: `${that.$route.path}/tactics_info`,
                        query:{
                            model:'load',
                        }
                    })
                }
                let params = {
                    msg : `该战法已被其他武将学习，是否继续学习该战法？`,
                    CallBack : f,
                }
                this.$store.Prompt(params)
            }
            else{
                this.$store.buffer.curTactics = tacticsData
                this.$router.push({
                    path: `${this.$route.path}/tactics_info`,
                    query:{
                        model:'load',
                    }
                })
            }
            
        },
    },
    mounted(){
        let path = this.$route.path
        this.routeArr = path.split("/")
        this.routeArr.shift()
        if(this.routeArr[0] == 'battle') this.tacticsDataArr = this.$store.allTactics;
        this.$store.AutoStyle(this.$refs.tactics_list)
    }
    
}
</script>

<style lang="less" scoped>
.tactics_list{
    position: absolute;left: 0; top: 0;
