                background: green;
            }
            .border:nth-of-type(2){
                background: rgb(100, 0, 0);
            }
            .border:nth-of-type(3){
                background: rgb(55, 1, 54);
            }
            .border:nth-of-type(4){
                background: rgb(2, 5, 94);
            }
            .tactics_box{
                position: relative;
                width: 16vw;
                height: 16vw;
                float: left;
                margin: 0 0 1.8vw 1.8vw;
                background: rgba(0, 0, 0, 0.2);
                .battle_load_mark{
                    background: rgba(0, 0, 0, 0.7);
                    font-family: "FZLBJW";
                    line-height: 16vw;
                }
                .icon{
                    position: absolute;
                    left: 2vw; top: 1.3vw;
                    width: 12vw; height: 12vw;
                    font-size: 2.8vw;
                }
                .load_mask{
