                        line-height: 3vw;
                        font-size: 2vw;
                        font-weight: bold;
                        color: rgb(215, 215, 215);
                    }
                }
            }
        }
        //战法图像
        .skill_image,.skill_mask{
            position: absolute; top: 0; left: 1.5vw;
            width: 12vw;
            height: 12vw;
        }
        
        //遮罩层
        .skill_mask{
            text-align: right;
            line-height: 12vw;
            font-size: 2vw;
            color: rgb(122, 122, 122);
            border-radius: 6vw;
            background: rgba(0, 0, 0, 0.9);
            box-shadow: 0 0 2vw rgb(64, 64, 64);
            transition: all .2s;
        }
        .skill_mask_transition-enter{
            opacity: .9;
        }
        .skill_mask_transition-enter-to{
            opacity: 1;
        }
        .skill_mask_transition-leave{
            transform: scale(1);
            opacity: 1;
        }
        .skill_mask_transition-leave-to{
            transform: scale(1.4);
            opacity: 0;
        }
        //标题
        .title{
            position: relative;
            width: 100%;
            height: 7vw;
            background: rgba(0, 0, 0, 0.2);
            float: left;
            // text-indent: 3vw;
            margin: 5vw 0 0 0;
            .text{
                width: 20vw;
            }
        }
        
        

    }
}
