class Report{
    /**
     * 战报
     * @param {string} fromType 来源类型，可以是"战斗","兵种特性","宝物特性","开局信息"
     */
    constructor(fromType){
        this.fromType = "";
        if(fromType && fromType.includes('兵种特性')) this.fromType = "兵种特性"
        else if(fromType && fromType.includes('宝物特性')) this.fromType = "宝物特性"
        else if(fromType == "主动" || fromType == "指挥" || fromType == "被动" || fromType == "追击" || fromType == "满级兵营"
            || fromType == "巨武神像" || fromType == "满级兵营"
        ) this.fromType = "状态信息";
        else if(fromType) this.fromType = fromType;
        else this.fromType = "战斗"
        this.type =  '';//战报类型
        this.initBelong = "";//发起者所属的对象是我还是敌军
        this.recBelong = "";//接受者所属的对象是我还是敌军
        this.initImg = "";//发起者图像
        this.recImg = "";//接受者图像

        this.level = 0,//战报级别

        this.text = "";
        this.text1 = '';//文本信息1
        this.text2 = '';//文本信息2
        this.text3 = "";
        this.text4 = "";
        this.text5 = "";
    }
    //初始化系统消息
    InitSysMsg(reportHeap,type,text,isPutReportHeap = true){
        this.type = type;
        this.text = text;
        this.level = 0;
        if(isPutReportHeap) reportHeap.push(this);
        else return this;
    }
    //初始化执行式战报
    InitExecuteReport(level,target,text,isPutReportHeap = true,type='执行'){
        this.level = level;
        this.type = type;
        this.initBelong = target.belong;
        this.initImg = target.img.head;
        this.text = text;
        if(isPutReportHeap) target.global.ReportHeap.push(this);
        else return this;
    }
    //初始化两目标式战报
    InitTwoTargetReport(level,target1,target2,text1,text2,isPutReportHeap = true){
        this.type = "两目标";
        this.level = level;
        this.initBelong = target1.belong;
        this.recBelong = target2.belong;
        this.initImg = target1.img.head;
        this.recImg = target2.img.head;
        this.text = text1;
        this.text1 = text2;
        if(isPutReportHeap) target1.global.ReportHeap.push(this);
        else return this;
    }
    //将战报放入战报堆区
    PutReportToHeap(reportHeap,target){
        reportHeap.push(target);
    }


    //初始化一条战报,type为战报类型，attacker为发起者，receiver为接受者，damage为造成伤害
    InitAttackReport(type,attacker,receiver,damage){
        
        this.level = 1;
        if(type == '普通攻击'){
            //战报类型是普通攻击
            //战报例子【A】对【B】发动普通攻击，{造成damage伤害，【B】还剩B。current_attr.hp点兵力。}，{}内为文本信息
            this.type = '普通攻击';
            this.attacker_img = attacker.img.head;
            this.receiver_img = receiver.img.head;
            this.text1 = ``
            this.text2 = `损失了${damage}兵力(${receiver.current_attr.hp})`
        }else if(type == 'ballistic_attack'){
            this.type = 'ballistic_attack';
            this.attacker_img = attacker.img.head;
            this.receiver_img = receiver.img.head;
            this.text1 = `造成${damage}点伤害。`
            this.text2 = `还剩(${receiver.current_attr.hp})兵力`
        }
    }

    
    
    
    
    
    
    
    
    
    
    
    //初始化阵容信息
    InitTeamInfo(target){
        this.type = 'info',
        this.level = 0;
        this.initBelong = target.initBelong;
        this.attacker_img = target.img.head;
        this.text1 = `【${target.camp}-${target.name}-${target.arms}】兵力:${target.origin_attr.hp}`
    }

    //武将战法准备阶段执行
    InitTacticsPrepareExecute(target,index){
        this.level = 0;
        this.type = 'info',
        this.initBelong = target.initBelong;
        this.attacker_img = target.img.head;
        this.text1 = `${target.TACTICS_STACK[index].info}`;
    }


    

}

export default Report;