<template>
    <div class="artifact_list">
        <div class="container">
            <div class="artifact_item" ref="artifact_item" v-for="art,index in artifactsArr" :key="art.index"
            @touchstart="$refs.artifact_item[index].classList.add('click')"
            @touchend="$refs.artifact_item[index].classList.remove('click')"
            @click="LoadArtifact(art)"
            v-show="ShowRule(art)">
                <ArtifactCard :artifactData="art" />
            </div>
        </div>
        <div class="filter_control" v-if="routeArr[0] == 'battle'">
            <div :class="`item ${navIdx == 0?'current':''}`"
            @click="ExchangeNavIdx(0)">
                <div class="cur_bg" v-if="navIdx == 0"></div>
                <div class="cover">我的宝物</div>
            </div>
            <div :class="`item ${navIdx == 1?'current':''}`"
            @click="ExchangeNavIdx(1)">
                <div class="cur_bg" v-if="navIdx == 1"></div>
                <div class="cover">所有宝物</div>
            </div>
        </div>
        <TopNav title="宝 物"/>
    </div>
</template>

<script>
import TopNav from '@/components/container/TopNav.vue';
import ArtifactCard from '@/components/style/ArtifactCard.vue';
import Artifact from '@/util/artifact';
export default {
    name : 'ArtifactList',
    components:{
        TopNav,
        ArtifactCard
    },
    data(){
        return{
            routeArr : [],
            artifactsArr : this.$store.userArtifacts,
            // artifactsArr : this.$store.allArtifacts,
            navIdx:0,
        }
    },
    computed:{
        // AritifactImgIcon(){
        //     switch(this.artifactData.effQualityIdx){
        //         case 0 :
        //         case 1 : return require('../../assets/ui/ui (65).png');
        //         case 2 : return require('../../assets/ui/ui (73).png');
        //     }
        // },
        // ForgeEffImgIcon(){
        //     switch(this.artifactData.effQualityIdx){
        //         case 0 : return require('../../assets/ui/ui (182).png');
        //         case 1 : return require('../../assets/ui/ui (184).png');
        //         case 2 : return require('../../assets/ui/ui (183).png');
        //     }
        // }
    },
    methods:{
        LoadArtifact(artData){
            if(this.routeArr[0] == 'battle'){
                let newArt = new Artifact();
                newArt.CopyData(artData);
                newArt.warriorId = -1;
                this.$store.buffer.curWarrior.artifact = newArt;
            }
            else{
                this.$store.buffer.curArtifact = artData;
                if(this.$store.buffer.curWarrior.artifact && this.$store.buffer.curWarrior.artifact.warriorId  != -1){
                    this.$store.buffer.curWarrior.artifact.warriorId = -1;
                }
                this.$store.buffer.curWarrior.artifact = artData;
                this.$store.buffer.curArtifact.warriorId = this.$store.buffer.curWarrior.id;
                this.$store.dirty.warrior = true;
            }
            this.$router.back();
        },
        ExchangeNavIdx(idx){
            this.navIdx = idx
            if(idx == 0) this.artifactsArr = this.$store.userArtifacts
            else if(idx == 1) this.artifactsArr = this.$store.allArtifacts
        },
        ShowRule(art){
            if(this.routeArr[0] == 'battle') return true;
            else if(art.warriorId == -1) return true;
            return false;
        }
    },
    mounted(){
        let path = this.$route.path
        this.routeArr = path.split("/")
        this.routeArr.shift()
        if(this.routeArr[0] == 'battle') this.ExchangeNavIdx(1)
    }
}
</script>

<style lang="less" scoped>
.artifact_list{
    position: absolute;left: 0; top: 0;
    width: 100vw;
    height: 100vh;
    background: url('../../assets/background/bg (1).jpg')right bottom no-repeat;
    background-size: cover;
    .container{
        position: absolute; left: 5vw; top : 15vh;
        width: 90vw;
        height: 70vh;
        overflow-y: scroll;
        overflow-x: hidden;
        // background: #000;
        .artifact_item{
            position: relative; float: left;
            width: 16vw; height: 20vw;
            margin: 0 0 2vw 1.7vw;
        }
    }
    .filter_control{
        position: absolute; bottom: 5vh; left: 5vw;
        width: 90vw; height: 8vw;
        background: rgba(0, 0, 0, 0.5);
        .item{
            position: relative; float: left;
            width: 20vw; height: 8vw;
            line-height: 8vw;
            color: gray;
            .cur_bg{
                width: 100%; height: 100%;
                background: url('../../assets/background/bg\ \(10\).png');
                background-size: 100% 100%;
            }
        }
        .item.current{
            color: white;
        }
    }
}

</style>