<template>
    <div class="battle_warrior_container" ref="battle_warrior_container">
        <div class="warrior_assemble_container">
            <div class="warriors_head" ref="warriors_head" v-for="(w,index) in warriorDataArr" :key="w.index"
            @touchstart="$refs.warriors_head[index].classList.add('click')"
            @touchend="$refs.warriors_head[index].classList.remove('click')" 
            @click="LoadWarrior(w)"
            v-show="ShowRule(w)">
                <WarriorHeadIcon :warriorData="w" :showSign="this.showSign" :showStar="this.showStar" :showUpMark="showUpMark"/>
            </div>
        </div>
        <TopNav title="武将列表" />
    </div>
</template>

<script>
import TopNav from '@/components/container/TopNav.vue'
import WarriorHeadIcon from '../../components/style/WarriorHeadIcon.vue'
export default {
    name : 'WarriorList',
    components:{
        TopNav,
        WarriorHeadIcon,
    },
    data(){
        return{
            warriorDataArr: this.$store.warriors,
            //是否展示武将红星
            showStar : true,
            //是否展示武将的标志
            showSign : true,
            //是否展示武将觉醒状态
            showUpMark : true,
            //是否展示所有武将
            showAll : this.$route.query.showAll || false,
            //是否展示被配置到队伍的武将
            showLoadWarrior : this.$route.query.showLoadWarrior ? this.$route.query.showLoadWarrior : false,
            //是否展示侍卫
            showGuard : this.$route.query.showGuard ? this.$route.query.showGuard : false,
            //是否展示用户已拥有的武将
            showOwnWarrior : this.$route.query.showOwnWarrior ? this.$route.query.showOwnWarrior : true,
            loadWarriorModel : this.$route.query.loadWarriorModel ? this.$route.query.loadWarriorModel : "link", // "link", "copy"
            routeArr : [],
        }
    },
    methods:{
        //武将头像选择规则
        ShowRule(w){
            if(this.routeArr[0] == 'battle' && w.name != '侍卫') return true
            if(this.showOwnWarrior && !w.own) return false
            if(this.showLoadWarrior && w.configTeamIdx != -1) return false
            return true
        },
        //武将头像组件展示规则
        ShowWarriorHeadCpt(){
            if(this.routeArr[0] == 'battle'){
                this.showStar = false;
                this.showUpMark = false;
            }
        },
        Camp(camp,name){
            switch(camp){
                case "汉" : return name ? "bg_han name" : "background_loop bg_han cover";
                case "魏" : return name ? "bg_wei name" : "background_loop bg_wei cover";
                case "蜀" : return name ? "bg_shu name" : "background_loop bg_shu cover";
                case "吴" : return name ? "bg_wu name" : "background_loop bg_wu cover";
                case "群" : return name ? "bg_wei name" : "background_loop bg_qun cover";
                case "晋" : return name ? "bg_jin name" : "background_loop bg_jin cover";
            }
        },
        //选择这个武将
        LoadWarrior(tgt){
            let tgtTeam = this.$store.teams
            if(this.routeArr[0] == 'battle'){
                tgtTeam = this.$store.battle.teams
                tgtTeam.LoadNativeWarrior(this.$store.buffer.editTeamIdx,this.$store.buffer.editTeamWarriorIdx,tgt)
            }
            else{
                let cost = 0;
                for(let i in this.$store.teams.list[this.$store.buffer.editTeamIdx]){
                    if(this.$store.teams.list[this.$store.buffer.editTeamIdx][i].id != -1) cost += this.$store.teams.list[this.$store.buffer.editTeamIdx][i].cost;
                }
                if(cost + tgt.cost > 10){
                    this.$store.Alert('统帅值超过10');
                    return false;
                }
                tgtTeam.PutWarriorIntoTeam(this.$store.buffer.editTeamIdx,this.$store.buffer.editTeamWarriorIdx,tgt)
            }

            this.$router.back()
        }
    },
    mounted(){
        let path = this.$route.path
        this.routeArr = path.split("/")
        this.routeArr.shift()
        if(this.routeArr[0] == 'battle') this.warriorDataArr = this.$store.allWarriors;
        //更新武将头像组件显示规则
        this.ShowWarriorHeadCpt();
        this.$store.AutoStyle(this.$refs.battle_warrior_container)
    }
}
</script>

<style lang="less">
.battle_warrior_container{
    position: absolute;left: 0; top: 0;
    width: 100vw;
    height: 100vh;
    background: url('../../assets/background/bg (1).jpg')right bottom no-repeat;
    background-size: cover;
    

    .warrior_assemble_container{
        position: absolute; bottom: 5vh; left: 0;
        width: 100%;height: 80vh;
        overflow-y: scroll;overflow-x: hidden;
        .warriors_head{
            float: left;
            position: relative;
            margin: 6vw 0 2vw 3.4vw;
            width: 16vw;
            height: 16vw;
        }
    }
    
}
.battle_warrior_container.pad{
    position: absolute;left: 0; top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 10;
    background: url('../../assets/background/bg (1).jpg')right bottom no-repeat;
    background-size: cover;
    .warrior_assemble_container{
        position: absolute; bottom: 5vh; left: 0;
        width: 100%;height: 80vh;
        overflow-y: scroll;overflow-x: hidden;
        .warriors_head{
            margin: 5vw 0 2vw 3.1vw;
            width: 13vw;
            height: 13vw;
            font-size: 2.5vw;
            transition: all .1s;
            cursor: pointer;
            &:hover{
                transform: scale(1.1);
            }
        }
        

    }
    
}
</style>