import Report from "../js/report";


class DamageStatusVarBlock{
    /**
     * 特殊状态伤害增减块
     * @param {*} applicator 
     * @param {*} tactics 
     * @param {*} damageType 伤害基础类型：攻击，谋略
     * @param {*} statusType 伤害类型：燃烧，妖术，恐慌。。。
     * @param {*} isMakeDamage 是否是造成伤害的一方
     * @param {*} value 增减伤数值
     * @param {*} isOnceAttack 是否只作用一次
     * @param {*} duration 持续时间
     * @param {*} waitTime 等待时间
     */
    constructor(applicator,tactics,damageType,statusType,isMakeDamage,value,isOnceAttack,duration,waitTime,workTactics){
        this.applicator = applicator;
        this.tactics = tactics;
        this.damageType = damageType;
        this.statusType = statusType
        this.isMakeDamage = isMakeDamage
        this.value = value;
        this.isOnceAttack = isOnceAttack;
        this.duration = duration;
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        this.workTactics = workTactics || null;
        this.isBenefit = false;
        if(this.isMakeDamage && this.value > 0) this.isBenefit = true;
        else if(!this.isMakeDamage && this.value < 0) this.isBenefit = true;
    }
    Init(target,isNeedReport=false,reportLevel=3){
        for(let i in target.STATUS_DAMAGE_VAR){
            if(target.STATUS_DAMAGE_VAR[i].tactics.type == this.tactics.type
            && this.statusType == target.STATUS_DAMAGE_VAR[i].statusType
            && this.isMakeDamage == target.STATUS_DAMAGE_VAR[i].isMakeDamage
            ){
                let r = new Report();
                r.InitExecuteReport(3,target,`已存在${this.isMakeDamage ? "造成":"受到"}${this.statusType}伤害${this.value > 0 ? "提高":"降低"}效果`);
                return false;
            }
        }
        target.STATUS_DAMAGE_VAR.push(this);
        if(isNeedReport){
            let r = new Report();
            let txt = `${this.isMakeDamage ? "造成":"受到"}${this.statusType}伤害${this.value > 0 ? "提高":"降低"}${Math.abs(this.value)}%`
            r.InitExecuteReport(reportLevel,target,txt,true)
        }
        return true;
    }
}

export default DamageStatusVarBlock