import Report from "../js/report";



class CureVarBlock{
    constructor(applicator,tactics,value,isOutput,isOnceCure,duration,waitTime,workTactics){
        this.applicator = applicator;
        this.tactics = tactics;
        this.value = value;
        this.isOutput = isOutput;
        this.isOnceCure = isOnceCure;
        this.duration = duration;
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        this.workTactics = workTactics || null;
    }
    Init(target){
        for(let i in target.CURE_VAR){
            if(target.CURE_VAR[i].tactics.type == this.tactics.type
            && target.CURE_VAR[i].isOutput == this.isOutput
            ){
                let report = new Report();
                report.InitExecuteReport(3,target,`已存在${target.CURE_VAR[i].isOutput?"造成":"受到"}恢复${this.value>0?"提高":"降低"}效果`);
                return false;
            }
        }
        target.CURE_VAR.push(this);
        return true;
    }
}

export default CureVarBlock;