//战法类，其中四大营，兵种，宝物的特性均可使用此类
class Tactics{
    /**
     * 战法类，其中四大营，兵种，宝物的特性均可使用此类
     * @param {*} owner 战法持有者
     * @param {*} data 战法数据，是复制的warrior文件夹中qun_s.js等战法基础数据
     */
    constructor(owner,data){
        this.owner = owner || null;
        //战法名称
        this.name = data ? data.name : "";
        //战法类型
        this.type = data ? data.type : "";
        //战法品质
        // this.quality = data ? data.quality : "";
        //战法发动几率
        this.odd = data ? data.odd : 100;
        //战法范围
        this.range = data ? data.range :0;
        //是否伤害型战法
        this.isDamageMaker = data ? data.isDamageMaker :false;
        this.damageMakeType = data ? data.damageMakeType : [];
        //是否控制型
        this.isControlMaker = data ? data.isControlMaker :false;
        //是否治疗形战法
        this.isCureMaker = data ? data.isCureMaker :false;
        //指定友军
        this.selectFri = data ? data.selectFri :false;
        //指定自己
        this.selectSelf = data ? data.selectSelf :false;
        //指定敌军
        this.selectEnemy = data ? data.selectEnemy :false;
        //作用目标数量
        this.targetNumber = data ? data.targetNumber :0;
        //战法作用兵种
        this.arms = data ? data.arms :['骑','步','弓'];
        //战法等待时间
        this.wait = data ? data.wait : 0;
        //统计
        this.STATISTICS = {
            damage : 0,
            cure : 0,
            times : 0,
        }
        //执行函数
        this.execute = data ? data.execute : null;
    }
}
export default Tactics;