<template>
    <div class="style_warrior_head_icon" v-if="warriorData.id != -1">
        <div :class="`style_warrior_head_icon_bg_${GetCampString(warriorData.camp)}`"></div>
        <div class="style_warrior_head_icon_bg"></div>
        <div class="style_warrior_head_icon_img">
            <img :src="warriorData.originSkin.head" alt="" class="cover">
        </div>
        <div :class="`style_warrior_head_icon_name_bg_${GetCampString(warriorData.camp)}`"></div>
        <div class="style_warrior_head_icon_name">{{ warriorData.name }}</div>
        <!-- <img :src="GetMarkImg(warriorData)" class="style_warrior_head_icon_up_mark" alt="" v-if="warriorData && showUpMark && (warriorData.isUp || warriorData.isUpWork)"> -->
        <div class="style_warrior_head_icon_warrior_sign" v-if="warriorData && showSign">
            <!-- <img :src="GetSignImg(warriorData)" class="cover" alt=""> -->
            <img src="../../assets/ui/ui (66).png" alt="" v-if="warriorData.sign == 's2'" class="cover">
            <img src="../../assets/ui/ui (67).png" alt="" v-if="warriorData.sign == 's3'" class="cover">
            <img src="../../assets/ui/ui (68).png" alt="" v-if="warriorData.sign == 'sp'" class="cover">
            <img src="../../assets/ui/ui (69).png" alt="" v-if="warriorData.sign == 'xp'" class="cover">
        </div> 
    </div>
</template>

<script>
export default {
    name : 'WarriorHeadIcon',
    props: {
        warriorData: null,
        showStar: {
            type:[String, Boolean],
            default: false
        },
        showSign: {
            type:[String, Boolean],
            default: false
        },
        showUpMark: {
            type:[String, Boolean],
            default: false
        }
    },
    data(){
        return{

        }
    },
    methods:{
        GetSignImg(w){
            switch(w.sign){
                case "s2" : return require("../../assets/ui/ui (66).png");
                case "s3" : return require("../../assets/ui/ui (66).png");
                case "sp" : return require("../../assets/ui/ui (66).png");
                case "xp" : return require("../../assets/ui/ui (66).png");
            }
        },
        GetMarkImg(w){
            if(w.isUp && w.isUpWork) return require("../../assets/ui/ui (73).png")
            else if(w.isUp) return require("../../assets/ui/ui (65).png")
            else if(w.isUpWork) return require("../../assets/ui/ui (84).png")
        },
        GetCampString(str){
            let res = ""
            switch(str){
                case "吴" : res += "wu"; break;
                case "蜀" : res += "shu"; break;
                case "群" : res += "qun"; break;
                case "魏" : res += "wei"; break;
                case "汉" : res += "han"; break;
                case "晋" : res += "jin"; break;
            }
            return res
        },
    }
}
</script>

<style lang="less">
@color_han_dark : rgb(25, 0, 31);
@color_han : rgb(61, 0, 75);
@color_wei_dark : rgb(0, 8, 24);
@color_wei : rgb(0, 24, 76);
@color_shu_dark : rgb(5, 28, 0);
@color_shu : rgb(13, 78, 0);
@color_wu_dark : rgb(58, 0, 0);
@color_wu : rgb(70, 0, 0);
@color_qun_dark : rgb(23, 23, 23);
@color_qun : rgb(71, 71, 71);
@color_jin_dark : rgb(0, 31, 32);
@color_jin : rgb(0, 61, 65);
.style_warrior_head_icon{
    position: absolute; left: 0; top: 0;
    width: 100%; height: 100%;
    font-size: 100%;
    .style_warrior_head_icon_bg_wei,.style_warrior_head_icon_bg_shu,.style_warrior_head_icon_bg_wu,.style_warrior_head_icon_bg_qun,.style_warrior_head_icon_bg_han,
    .style_warrior_head_icon_bg_jin{
        left: 0; top: 0;
        width: 100%; height: 100%;
        box-shadow: 0 0 1vw rgb(32, 32, 32);
    }
    .style_warrior_head_icon_name_bg_han,
    .style_warrior_head_icon_name_bg_wei,
    .style_warrior_head_icon_name_bg_shu,
    .style_warrior_head_icon_name_bg_wu,
    .style_warrior_head_icon_name_bg_qun,
    .style_warrior_head_icon_name_bg_jin{
        left: 0; bottom: -30%;
        width: 100%; height: 25%;
        box-shadow: 0 0 1vw rgb(32, 32, 32);
    }
    .style_warrior_head_icon_name_bg_han{
        background: @color_han;
    }
    .style_warrior_head_icon_name_bg_wei{
        background: @color_wei;
    }
    .style_warrior_head_icon_name_bg_shu{
        background: @color_shu;
    }
    .style_warrior_head_icon_name_bg_wu{
        background: @color_wu;
    }
    .style_warrior_head_icon_name_bg_qun{
        background: @color_qun;
    }
    .style_warrior_head_icon_name_bg_jin{
        background: @color_jin;
    }
    .style_warrior_head_icon_bg_han{
        background:linear-gradient(to top left, @color_han, @color_han_dark);
    }
    .style_warrior_head_icon_bg_wei{
        background:linear-gradient(to top left,@color_wei,@color_wei_dark);
    }
    .style_warrior_head_icon_bg_shu{
        background:linear-gradient(to top left,@color_shu,@color_shu_dark);
    }
    .style_warrior_head_icon_bg_wu{
        background:linear-gradient(to top left,@color_wu,@color_wu_dark);
    }
    .style_warrior_head_icon_bg_qun{
        background:linear-gradient(to top left,@color_qun,@color_qun_dark);
    }
    .style_warrior_head_icon_bg_jin{
        background:linear-gradient(to top left,@color_jin,@color_jin_dark);
    }
    .style_warrior_head_icon_bg{
        left: 0; top: 0;
        width: 100%; height: 100%;
        background: url('../../assets/ui/ui\ \(173\).png')no-repeat;
        background-size: 100% 100%;
    }
    .style_warrior_head_icon_img{
        left: 7%; top: 7%;
        width: 86%; height: 86%;
        .style_warrior_head_icon_img_star_list{
            left: 0; top: auto; bottom: 0;
            width: 100%;height: 25%;
            background:linear-gradient(to top,rgba(0, 0, 0, 0.9),rgba(0, 0, 0, 0));
            li{
                float: left;
                position: relative;
                width: 18%;height: 72%;
                margin: 2% 0 0 0;
            }
            li:nth-of-type(1){
                margin: 2% 0 0 5%;
            }
        }
    }
    .style_warrior_head_icon_name{
        left: 0; bottom: -30%;
        width: 100%; height: 25%;
        background: url('../../assets/ui/ui\ \(178\).png')no-repeat;
        background-size: 100% 100%;
        line-height: 140%;
        font-size: 100%;
    }
    .style_warrior_head_icon_up_mark{
        position: absolute; right: -12%; top: -12%;
        width: 34%; height: 34%;
    }
    .style_warrior_head_icon_warrior_sign{
        position: absolute; left: -5%; bottom: -13%;
        width: 30%; height: 30%;
    }
    
}
</style>