<template>
    <div class="prompt_optional_container" ref="prompt_optional_container">
        <div class="mask cover" @click="Close"></div>
        
        <div class="container">
            <TopicTitle :title="$store.promptOptional.title" :closeFunc="Close" />
            <div class="text_container">
                <div class="text">{{ $store.promptOptional.msg }}</div>
            </div>
            <div class="optional_btn_list">
                <div class="btn" ref="btn"
                @touchstart="$refs.btn[bIndex].classList.add('click')"
                @touchend="$refs.btn[bIndex].classList.remove('click')"
                @click="Confirm(bIndex)"
                 v-for="b,bIndex in $store.promptOptional.optionBtnNames" :key="b.index">{{ b }}</div>
            </div>
            <!-- <div class="confirm_btn" ref="confirm_btn"
            @touchstart="$refs.confirm_btn.classList.add('click')"
            @touchend="$refs.confirm_btn.classList.remove('click')" 
            @click="Confirm">确 定</div> -->
        </div>
    </div>
</template>

<script>
import TopicTitle from '../style/TopicTitle.vue';
export default {
    name : 'OptionalPrompt',
    components:{
        TopicTitle
    },
    methods:{
        Close(){
            this.$store.promptOptional.status = false;
        },
        Confirm(idx){
            this.$store.promptOptional.optionCallBack[idx]();
            this.Close();
        }
    },
    mounted(){
        this.$store.AutoStyle(this.$refs.prompt_optional_container)
    }
}
</script>

