                line-height: 5vw;
                background: white;
                color: black;
                font-weight: bold;
            }
        }
        
    }
    
}
.prompt_container.pad{
    z-index: 100;
    position: fixed; left: 0; top: 0;
    width: 100vw; height: 100vh;
    .mask{
        background:radial-gradient(rgba(0, 0, 0, 0.5),rgb(0, 0, 0));
    }
    .container{
