<template>
    <div class="main" ref="main">
        <!-- logo -->
        <div class="logo"></div>
        <!-- 导航区 -->
        <div class="main_navigation">
            <ul>
                <li v-for="b,bIdx in btnMap" :key="bIdx" ref="btn"
                @touchstart="$refs.btn[bIdx].classList.add('touch')"
                @touchend="$refs.btn[bIdx].classList.remove('touch')" 
                @click="EnterPage(b.path)">
                    {{ b.name }}
                </li>
            </ul>
        </div>     
        <!-- 路由 -->
        <router-view/>
        
        <!-- 提示面板 -->
        <Prompt v-if="$store.prompt.status" />
        <OptionalPrompt v-if="$store.promptOptional.status" />
        <Alert v-if="$store.alertQueue" :alertQueue="$store.alertQueue"/>
    </div>
</template>

<script>
import Alert from './Alert.vue';
import Prompt from './Prompt.vue';
import Load from '../../util/load.js';
import OptionalPrompt from './OptionalPrompt.vue'
export default {
    name : 'MainPanel',
    components:{
        Prompt,
        Alert,
        OptionalPrompt
    },
    data(){
        return{
            btnMap:{
                0:{
                    name : "武 将",
                    path : '/my_warriors'
                },
                1:{
                    name : "库 藏",
                    path : '/my_inventory'
                },
                2:{
                    name : "配 队",
                    path : '/my_teams'
                },
                3:{
                    name : "模拟对局",
                    path : '/battle'
                },
                4:{
                    name : "设 置",
                    path : '/setting'
                },
            }
        }
    },
    methods:{
        EnterPage(path){
            this.$router.push({
                path : path
            })
        },
    },
    
    mounted(){
        let load = new Load(this.$store);
        load.Init();
        //对界面进行响应式
        let ratio = window.innerHeight/window.innerWidth;
        if(ratio >= 1.3 && ratio <= 1.6){
            this.$store.device = "pad";
        }
        if(window.innerWidth/window.innerHeight > 1.4){
            // // this.$refs.app.classList.add('hoverEvent');
            // this.$store.state.equipment = "computer";
        }
        this.$store.AutoStyle(this.$refs.main)
    }
}
</script>

<style lang="less" scoped>
.main{
    position: absolute; left: 0; top : 0;
    width: 100%; height: 100%;
    background: url('../../assets/background/bg (2).jpg')center right no-repeat;
    background-size: cover;
    .logo{
        left: 50%; top : 25vh;
        transform : translate(-50%,-50%);
        width: 50vw;height: 17vw;
        background: url('../../assets/background/logo.webp')no-repeat;
        background-size: 100% 100%;
    }

    //主导航
    .main_navigation{
        left: 0vw; bottom: 8vw;
        width: 40vw;height: 78vw;
        ul{
            position:absolute; left: 0; top : 0;
            width : 100%; height : 100%;
            li{
                position: relative; float: left;
                margin: 3vw 0 0 0;
                width: 40vw;height: 10vw;
                line-height: 10vw;
                text-indent: 8vw;
                text-align: left;
                font-size: 3.5vw;
                background: url('../../assets/ui/ui (140).png')center no-repeat;
                background-size: 100% 100%;
                color: white;
                font-family: "FZLBJW";
                font-weight: bold;
                &.touch{
                    opacity: .8;
                    color: yellow;
                }
            }
        }
    }
}
.main.pad{
    //主导航
    .main_navigation{
        left: 0vw; bottom: 4vh;
        width: 22vh;height: 39vh;
        ul{
            li{
                margin: 1.5vh 0 0 0;
                width: 22vh;height: 5vh;
                line-height: 5vh;
                text-indent: 6vh;
                font-size: 1.75vh;
                cursor: pointer;
                &:hover{
                    transform: none;
                    color: yellow;
                    opacity: .8;
                }

            }
            
        }
    }
}
</style>