import artifacBaseEfftData from '../data/artifact_base_eff.json';
class Artifact{
    /**
     * 宝物类
     * @param {object} data 原始文件数据
     * @param {object} baseeEffMap 基础效果映射表
     * @param {object} forgeEffMap 锻造效果映射表
     */
    constructor(data,baseeEffMap,forgeEffMap){
        const timestamp = Date.now().toString(36); // 转换为36进制缩短长度
        const random = Math.random().toString(36).substring(2, 8); // 取6位随机字符
        const randNum = Math.floor(Math.random()*1000);
        this.id = `${timestamp}${random}${randNum}`;
        this.name = data ? data.name : "";
        this.type = data ? data.type : "";
        this.describe = data ? data.describe : "";
        this.quality = data ? data.quality : "";
        this.effQuality = ["普通","藏品","名器"];
        this.effQualityIdx = 0;
        this.baseEffArr = [];
        if(data){
            for(let i in data.base_effect){
                let item = {
                    name : data.base_effect[i].name,
                    eff : baseeEffMap[data.base_effect[i].name][data.base_effect[i].idx]
                }
                this.baseEffArr.push(item);
            }
        }
        this.forgeEffArr = [];
        if(data){
            for(let i in data.forge_effect){
                let item = {
                    name : data.forge_effect[i],
                    eff : forgeEffMap[data.forge_effect[i]]
                }
                this.forgeEffArr.push(item);
            }
        }
        this.forgeChoseIdx = 0;
        this.warriorId = -1; //配置该宝物的武将id
        this.img = data ? require(`../static/artifact/${data.img}`) : require(`../static/artifact/art (1).jpg`);
    }
    GetId(){
        const timestamp = Date.now().toString(36); // 转换为36进制缩短长度
        const random = Math.random().toString(36).substring(2, 8); // 取6位随机字符
        const randNum = Math.floor(Math.random()*1000);
        this.id = `${timestamp}${random}${randNum}`
    }
    CopyData(data) {
        // 辅助函数：深合并对象
        const DeepAssign = (target, source) => {
            for (const key in source) {
                if (typeof source[key] === 'object' && source[key] !== null) {
                    if (!target[key]) target[key] = Array.isArray(source[key]) ? [] : {};
                    DeepAssign(target[key], source[key]);
                } else {
                    target[key] = source[key];
                }
            }
            return target;
        };
        // 1. 先清空当前实例（可选，根据需求）
        Object.keys(this).forEach(key => delete this[key]);

        // 2. 深合并数据
        DeepAssign(this, data);

        // 3. 兜底默认值（确保关键属性存在）
        // this.attr = this.attr || {
        //     ad: 0, de: 0, tact: 0, speed: 0, demolition: 0, moral: 100
        // };
        // this.grow = this.grow || {
        //     ad: 0, de: 0, tact: 0, speed: 0, demolition: 0, moral: 100
        // };
    }
    Init(){
        
    }
}

export default Artifact;