class ActiveTacticsBlock{
    /**
     * 战法的释放前后执行函数块
     * @param {*} applicator  来自谁
     * @param {*} tactics  施加该效果的战法
     * @param {*} execute 执行函数
     * @param {*} duration 持续时间
     * @param {*} waitTime 块生效的等待时间
     * @param {boolean} isAfterExecute 是战法发动前调用还是战法发动后调用（默认发动后)
     * @param {*} tacticsType 
     * @param {boolean} isLaunchedExecute 是否为成功发动后执行还是未发动后执行，默认成功发动后执行
     */
    constructor(applicator,tactics,execute,duration,waitTime,isAfterExecute,tacticsType,isLaunchedExecute){
        this.applicator = applicator;
        this.tactics = tactics;
        this.isAfterExecute = isAfterExecute == undefined ? true : isAfterExecute;
        this.duration = duration;
        this.waitTime = waitTime
        this.isLaunchedExecute = isLaunchedExecute == undefined ? true : isLaunchedExecute;


        //触发该效果的战法本体
        this.triggerTactics = null;
        //该块的持有者
        this.beExecuted = null;
        //执行函数
        this.execute = execute;
        //是发动什么类型战法后执行，默认主动
        this.tacticsType = tacticsType == undefined ? "主动" : tacticsType;
    }
    Init(target,prior = false){
        this.beExecuted = target;
        prior ? target.ACTIVE_TACTICS_EXECUTE.unshift(this) : target.ACTIVE_TACTICS_EXECUTE.push(this);
        return true;
    }
    Execute(triggerTactics){
        this.triggerTactics = triggerTactics;
        this.execute();
    }
}

export default ActiveTacticsBlock;