import AttrBlock from "../block/attr";
import CounterAttackBlock from "../block/counterAtk";
import DamageBlock from "../block/damage";
import ExecuteBlock from "../block/execute";
import BeforeMakeDamageBlock from "../block/execute2";
import AttackExecuteBlock from "../block/execute4";
import ExecuteSysBlock from "../block/executeSys";
import RangeBlock from "../block/range";
import StatusBlock from "../block/status";
import Judge from "../js/judge";
import Query from "../js/query";
import Report from "../js/report";
import Util from "../js/util";
import Tactics from "./tactics";

//兵种特性
class ArmSpec {
    constructor() {
        this.specType = "兵种特性"
        this.armTypeMap = {
            "禁卫": "步",
            "长枪兵": "步",
            "重步兵": "步",
            "藤甲兵": "步",
            "蛮兵": "步",
            "长弓兵": "弓",
            "弩兵": "弓",
            "死士": "弓",
            "轻骑兵": "骑",
            "重骑兵": "骑",
            "铁骑兵": "骑",
            "弓骑兵": "骑",
            "象兵": "骑"
        };
    }
    //处理高级兵种特性
    ProcessEliteUnit(target, specName) {
        let spec = new Tactics(target);
        spec.type = '高级' + this.specType + specName;
        spec.name = specName;
        if (specName == "长柄") {
            let value = 8;
            let block = new DamageBlock(target, spec, '所有', true, value, false, false, 99, 0);
            let work = block.Init(target, null, false, 2);
            if (work) {
                let r = new Report("兵种特性")
                r.InitExecuteReport(2, target, `【长柄】效果已施加，造成所有伤害提高${value}%`);
            }
        }
        else if (specName == "护主") {
            let value = 8;
            if (self.position == '2') {
                value = 12;
            }
            let block = new DamageBlock(target, spec, '追击', false, -value, false, false, 99, 0);
            let work = block.Init(target, null, false, 2);
            if (work) {
                let r = new Report("兵种特性")
                r.InitExecuteReport(2, target, `【护主】效果已施加，受到追击伤害降低${value}%`);
            }
        }
        else if (specName == '铁意') {
            let value = 6;
            let block = new DamageBlock(target, spec, '所有', false, -value, false, false, 99, 0);
            let work = block.Init(target, null, false, 2);
            if (work) {
                let r = new Report("兵种特性")
                r.InitExecuteReport(2, target, `【铁意】效果已施加，受到所有伤害降低${value}%`);
            }
        }
        else if (specName == '负重') {
            let value = 15;
            let block = new AttrBlock(target, spec, '速度', -value, 99, false);
            let work = block.Init(target, false);
            if (work) {
                let r = new Report("兵种特性")
                r.InitExecuteReport(2, target, `【负重】效果已施加，速度属性降低${value}`);
            }
        }
        else if (specName == '披甲') {
            let value = 12;
            let block1 = new DamageBlock(target, spec, '主动', false, -value, false, false, 99, 0);
            let work1 = block1.Init(target, null, false, 2);
            if (work1) {
                let r = new Report("兵种特性")
                r.InitExecuteReport(2, target, `【披甲】效果已施加，受到主动战法伤害降低${value}%`);
            }
        }
        else if (specName == '惧火') {
            let block = new DamageBlock(target, spec, '所有', false, -30, false, false, 99, 0);
            let work = block.Init(target, null, false);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【藤甲】效果已施加`);
            }
            let action = function () {
                let tgt = this.beExecuted;
                for (let i in tgt.SPECIAL_STATUS) {
                    if (tgt.SPECIAL_STATUS[i].type == '燃烧' || tgt.SPECIAL_STATUS[i].type == '火攻') {
                        let block = new StatusBlock(target, spec, '暴走', 0, 0);
                        let work = block.Init(target, false);
                        if (work) {
                            let r = new Report("兵种特性");
                            r.InitTwoTargetReport(2, target, target, `【惧火】令`, `陷入暴走`);
                            break;
                        }
                    }
                }
            }
            let block1 = new ExecuteBlock(target, spec, '行动开始', action, 99);
            block1.Init(target, false);
            let r = new Report("兵种特性");
            r.InitExecuteReport(2, target, `【惧火】效果已施加`);
        }
        else if (specName == '藤甲') {
            let value = 30;
            let action = function () {
                if(this.statusDamageType == null || (this.statusDamageType != "燃烧" && this.statusDamageType != "火攻")){
                    let block = new DamageBlock(target,spec,'所有',false,-value,true,false,0,0);
                    let work =  block.Init(target,null,false);
                    if(work){
                        let r = new Report("兵种特性")
                        r.InitTwoTargetReport(3,target,target,`【藤甲】令`,`受到所有伤害降低${value}%`)
                    }
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, false);
            let work = block.Init(target);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【藤甲】效果已施加`);
            }

        }
        else if (specName == '野战') {
            let value = 18;
            let block = new DamageBlock(target, spec, '所有', true, value, false, false, 99, 0);
            let work = block.Init(target, null, false, 2);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【野战】效果已施加，造成所有伤害提高${value}%`);
            }
        }

        else if (specName == '先发') {
            let block = new StatusBlock(target, spec, '先手', 2, 0);
            let work = block.Init(target, false);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【先发】效果已施加`);
            }
        }
        else if (specName == "远离") {
            let block = new RangeBlock(target, spec, true, 1, 99, 0);
            let work = block.Init(target, false);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【远离】效果已施加，攻击距离+1`);
            }
        }
        else if (specName == "弩弓") {
            let value = 8;
            let block = new DamageBlock(target, spec, '所有', true, value, false, false, 99, 0);
            let work = block.Init(target, null, false, 2);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【弩弓】效果已施加，造成所有伤害提高${value}%`);
            }
        }
        else if (specName == "短兵") {
            let block = new RangeBlock(target, spec, true, -1, 99, 0);
            let work = block.Init(target, false);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【短兵】效果已施加，攻击距离-1`);
            }
        }
        else if (specName == "无畏") {
            let value = 18;
            for (let i = 0; i < 3; i++) {
                let attrType = ''
                switch (i) {
                    case 0: attrType = '攻击'; break;
                    case 1: attrType = '防御'; break;
                    case 2: attrType = '谋略'; break;
                }
                let block = new AttrBlock(target, spec, attrType, value, 99, false);
                block.Init(target, false);
            }
            let r = new Report("兵种特性");
            r.InitExecuteReport(2, target, `【无畏】效果已施加，攻击，防御，谋略属性提高${value}`);
        }

        else if (specName == '轻装') {
            let value = 18;
            let block = new AttrBlock(target, spec, '速度', value , 99, false);
            block.Init(target, false);
            let r = new Report("兵种特性");
            r.InitExecuteReport(2, target, `【轻装】效果已施加，速度属性提高${value}%`);
        }
        else if (specName == '冲阵') {
            let value = 18;
            for (let i = 0; i < 4; i++) {
                let block = new DamageBlock(target, spec, '所有', true, value, true, false, 99, 0);
                block.Init(target, null, false);
            }
            let r = new Report("兵种特性");
            r.InitExecuteReport(2, target, `【冲阵】效果已施加`);
        }
        else if (specName == '不屈') {
            let value = 25;
            let action = function () {
                let j = new Judge();
                if(j.IsGetStatus(target,"混乱") || j.IsGetStatus(target,"暴走") || j.IsGetStatus(target,"犹豫") || j.IsGetStatus(target,"怯战")){
                    let block = new DamageBlock(target,spec,'所有',false,-value,true,false,0,0);
                    let work =  block.Init(target,null,false);
                    if(work){
                        let r = new Report("兵种特性")
                        r.InitTwoTargetReport(3,target,target,`【不屈】令`,`受到所有伤害降低${value}%`)
                    }
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, false);
            let work = block.Init(target);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【不屈】效果已施加`);
            }
        }
        else if (specName == '野性') {
            let block = new StatusBlock(target, spec, '不受指挥战法影响', 99, 0);
            block.Init(target, false);
            let block1 = new DamageBlock(target, spec, '所有', false, -10, false, false, 99, 0);
            let work1 = block1.Init(target, null, false, 2);
            if (work1) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【野性】效果已施加`);
            }
        }
        else if (specName == "迎击") {
            let value = 75;
            let block = new CounterAttackBlock(target, spec, '反击', value, 100, 2, 0);
            block.Init(target, false);
            let r = new Report("兵种特性");
            r.InitExecuteReport(2, target, `【迎击】效果已施加`);
        }
        else if (specName == "骑射") {
            let block = new RangeBlock(target, spec, false, 1, 99, 0);
            block.Init(target, false);
            let r = new Report("兵种特性");
            r.InitExecuteReport(2, target, `【骑射】效果已施加`);
        }
    }
    //处理可选的兵种特性
    ProcessOptionalUnit(target, specName) {
        let spec = new Tactics(target);
        spec.type = this.specType + specName;
        spec.name = specName;
        if (specName == "文韬") {
            let value = 15;
            let action = function () {
                if (target.driver.turn >= 3) {
                    let block = new DamageBlock(target, spec, '谋略', true, value, true, false, 1, 0);
                    let work = block.Init(target, null, false);
                    if (work) {
                        let r = new Report("兵种特性");
                        r.InitTwoTargetReport(0, target, target, '【文韬】令', `下一次谋略伤害提高${value}%`);
                    }
                }
            }
            let block = new ExecuteSysBlock(target, spec, "回合开始", action, 99);
            let work = block.Init(target);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【文韬】效果已施加`);
            }
        }
        else if (specName == "勇毅") {
            let value = 15;
            let action = function () {
                if ((this.attacker.unitType == '弓' || this.attacker.unitType == '步') && this.damageType == '谋略') {
                    let block = new DamageBlock(target, spec, '谋略', false, -value, true, false, 0, 0);
                    let work = block.Init(target, null, false);
                    if(work){
                        let r = new Report("兵种特性")
                        r.InitTwoTargetReport(3,target,target,`【勇毅】令`,`受到所有伤害降低${value}%`)
                    }
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, false);
            let work = block.Init(target);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【勇毅】效果已施加`);
            }
        }
        else if (specName == "利刃") {
            let count = 0;
            for (let i in target.TacticsArr) {
                if (target.TacticsArr[i].type != 'active') {
                    count++;
                }
            }
            let value = 22 * count;
            let block = new AttrBlock(target, spec, "攻击", value, 99, true);
            block.Init(target, false);

            if (value > 0) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【利刃】效果已施加，攻击属性提升${value}`);
            }

        }
        else if (specName == "列阵") {
            let value = 10;
            let block = new DamageBlock(target, spec, '主动', true, value, false, false, 99, 0);
            let work = block.Init(target, spec, false);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【列阵】效果已施加，主动战法伤害提高${value}%`);
            }
        }
        else if (specName == "守备") {
            let value = 16;
            let block = new DamageBlock(target, spec, '所有', false, -value, false, false, 99, 0);
            let work = block.Init(target, spec, false);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【守备】效果已施加，受到所有伤害降低16%`);
            }
        }

        else if (specName == "挫锐") {
            let j = new Judge();
            let value = 18;
            let action = function () {
                if (j.IsMaxTroopsInTeam(this.receiver)) {
                    let block = new DamageBlock(target, spec, '所有', true, value, true, false, 0, 0);
                    block.Init(target, null, false);
                    let r = new Report("兵种特性");
                    r.InitTwoTargetReport(3, target, target, `【挫锐】令`, `造成所有伤害提高${value}%`);
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, true);
            let work = block.Init(target);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【挫锐】效果已施加`);
            }
        }
        else if (specName == "绕后") {
            let j = new Judge();
            let value = 10;
            let action = function () {
                if (this.receiver.position != 2) {
                    let block = new DamageBlock(target, spec, '所有', true, value, true, false, 0, 0);
                    block.Init(target, null, false);
                    let r = new Report("兵种特性");
                    r.InitTwoTargetReport(3, target, target, `【绕后】令`, `造成所有伤害提高${value}%`);
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, true);
            let work = block.Init(target);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【绕后】效果已施加`);
            }
        }
        else if (specName == "急行") {
            let block = new StatusBlock(target, spec, '先手', 3, 0);
            let work = block.Init(target, false);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【急行】效果已施加，获得先手`);
            }
        }
        else if (specName == "出奇") {
            let value = 85;
            let action = function(){
                let block = new DamageBlock(target, spec, '所有', false, -value, false, false, 1, 0);
                let work = block.Init(target, null, false);
                if (work) {
                    let r = new Report("兵种特性");
                    r.InitTwoTargetReport(1,target,target,`【出奇】令`,`受到所有伤害降低${value}%`);
                }
            }
            let exeBlock = new ExecuteBlock(target,spec,'回合开始',action,1);
            exeBlock.Init(target,true);
        }
        else if (specName == "难测") {
            let value = 15;
            let action = function () {
                if (this.isCommonAttack) {
                    let block = new DamageBlock(target, spec, '普通攻击', false, -value, true, false, 0, 0);
                    block.Init(target, null, false);
                }
                else if (this.originTactics.type == '追击') {
                    let block = new DamageBlock(target, spec, '追击', false, -value, true, false, 0, 0);
                    block.Init(target, null, false);
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, false);
            let work = block.Init(target);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【难测】效果已施加`);
            }
        }

        else if (specName == "齐射") {
            let value = 15;
            let block = new DamageBlock(target, spec, '所有', true, value, false, false, 3, 0);
            let work = block.Init(target, null, false);
            if (work) {
                let r = new Report("兵种特性")
                r.InitExecuteReport(2, target, `【齐射】效果已施加`);
            }
        }
        else if (specName == "地利") {
            let value = 24;
            let str = "防御"
            if (target.position == 2) {
                let block = new AttrBlock(target, spec, "防御", value, 99, true, true);
                block.Init(target, false);
            }
            else if (target.position == 1) {
                value = 10;
                let block1 = new AttrBlock(target, spec, "防御", value, 99, true, true);
                block1.Init(target, false);
                let block2 = new AttrBlock(target, spec, "谋略", value, 99, true, true);
                block2.Init(target, false);
                str += "，谋略"
            }
            else {
                value = 6;
                let strList = ["防御", "谋略", "攻击"];
                for (let i in strList) {
                    let block = new AttrBlock(target, spec, strList[i], value, 99, true, true);
                    block.Init(target, false);
                }
                str += '，谋略，攻击'
            }
            let r = new Report("兵种特性")
            r.InitExecuteReport(2, target, `【地利】${str}属性提升效果已施加`);
        }
        else if (specName == "迂回") {
            let value = 10;
            let block = new DamageBlock(target,spec,'所有',false,-value,false,false,99,0);
            let work =  block.Init(target,null,false);
            if(work){
                let r = new Report("兵种特性");
                r.InitExecuteReport(2,target,`【迂回】效果已施加，受到所有伤害降低${value}%`);
            }
        }
        else if (specName == "直射") {
            let q = new Query();
            let value = 10;
            let action = function () {
                let distance = q.GetDistance(this.attacker,this.receiver);
                let finalValue = value - 2 * distance;
                if(finalValue > 0){
                    let block = new DamageBlock(target, spec, '所有', true, finalValue, true, false, 0, 0);
                    block.Init(target, null, false);
                    let r = new Report("兵种特性");
                    r.InitTwoTargetReport(3, target, target, `【直射】令`, `造成所有伤害提高${finalValue}%`);
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, true);
            let work = block.Init(target);
            if (work) {
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【直射】效果已施加`);
            }
        }
        else if (specName == "散射") {
            let damageValue = 40;//分兵伤害率
            let block = new CounterAttackBlock(target,spec,"分兵",damageValue,100,99,0);
            let work = block.Init(target,false);
            if(work){
                let r = new Report("兵种特性");
                r.InitExecuteReport(2, target, `【散射】效果已施加`);
            }
            let action = function(){
                block.SelfDestroy();
                let r = new Report("兵种特性");
                r.InitExecuteReport(3,target,`【散射】的分兵效果结束了`)
                this.SelfDestroy();
            }
            let exeBlock = new AttackExecuteBlock(target,spec,action,99,0,true,"分兵后");
            exeBlock.Init(target,true);
            
        }

    }
    Init(warrior) {
        //转换武将的高级兵种的兵种属性
        warrior.unitType = this.armTypeMap[warrior.upArms[warrior.choseUpArmsIdx]];
        //配置高级兵种基础特性
        for (let i in warrior.armsSpec[warrior.choseUpArmsIdx]) {
            this.ProcessEliteUnit(warrior, warrior.armsSpec[warrior.choseUpArmsIdx][i].name);
        }
        //配置可选的兵种特性
        for (let i in warrior.armsCommonSpec[warrior.choseUpArmsIdx]) {
            if (i == warrior.choseArmsCharacterIdx[0] || i == warrior.choseArmsCharacterIdx[1])
                this.ProcessOptionalUnit(warrior, warrior.armsCommonSpec[warrior.choseUpArmsIdx][i].name)
        }
    }


}

export default ArmSpec;