            let index2 = 25;//主动战法增伤
            if(self.driver.version == '平衡'){
                value = 150;
                index2 = 30;
            }

            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,2,false,true,this,"伤害");
