//app核心战法对象
class Tactics{
    constructor(data){
        this.id = data ? data.id : -1;
        this.name = data ? data.name : "";
        this.quality = data ? data.quality : "";
        this.type = data ? data.type : "";
        this.describe = data ? data.describe : [];
        this.odds = data ? data.odds : "";
        this.range = data ? data.range : "";
        this.arms = data ? data.arms : "";
        this.tgtNumber = data ? data.tgt_number : "";
        this.sign = data ? data.sign : "";
        this.version = data ? data.version : []
        //是否锁住
        this.lock = true;
        // 是否被装载
        this.isload = -1;
    }
    CopyData(data){
        this.id = data ? data.id : -1;
        this.name = data ? data.name : "";
        this.quality = data ? data.quality : "";
        this.type = data ? data.type : "";
        this.describe = data ? data.describe : [];
        this.odds = data ? data.odds : "";
        this.range = data ? data.range : "";
        this.arms = data ? data.arms : "";
        this.tgtNumber = data ? data.tgtNumber : "";
        this.sign = data ? data.sign : "";
        this.version = data ? data.version : []
        this.lock = data ? data.lock : true ;
        this.isload = data ? data.isload : -1;
    }
}

export default Tactics