<template>
    <div class="style_warrior_card" v-if="warriorData.id != -1">
        <img :src="warriorData.img.body" alt="" loading="lazy" :class="'cover '+(filterGray?' filter_gray':'')" v-if="cardStyle == '2_3'">
        <img :src="warriorData.img.preview" alt="" loading="lazy" class="cover" v-if="cardStyle == '2_3_sml_pic'">
        <img src="../../assets/ui/ui (60).png" alt="" class="cover">
        <div class="style_warrior_card_black_tape"></div>
        <ul :class="`style_warrior_card_star_list ${filterGray?' filter_gray':''}`">
            <li v-for="s in warriorData.red" class="style_warrior_card_star_list_star" :key="s.index">
                <img src="../../assets/ui/ui (74).png" alt="" class="cover">
            </li>
            <li v-for="s in (5-warriorData.red)" class="style_warrior_card_star_list_star" :key="s.index">
                <img src="../../assets/ui/ui (76).png" alt="" class="cover">
            </li>
        </ul>
        <div class="style_warrior_card_name" >{{warriorData.name}}</div>
        <div class="style_warrior_card_hp_num">{{ warriorData.hp || null }}</div>
        <div class="style_warrior_card_camp" v-if="warriorData.camp == '蜀'"><img src="../../assets/ui/ui (75).png" alt="" class="cover"></div>
        <div class="style_warrior_card_camp" v-if="warriorData.camp == '吴'"><img src="../../assets/ui/ui (78).png" alt="" class="cover"></div>
        <div class="style_warrior_card_camp" v-if="warriorData.camp == '魏'"><img src="../../assets/ui/ui (77).png" alt="" class="cover"></div>
        <div class="style_warrior_card_camp" v-if="warriorData.camp == '群'"><img src="../../assets/ui/ui (72).png" alt="" class="cover"></div>
        <div class="style_warrior_card_camp" v-if="warriorData.camp == '汉'"><img src="../../assets/ui/ui (64).png" alt="" class="cover"></div>
        <div class="style_warrior_card_camp" v-if="warriorData.camp == '晋'"><img src="../../assets/ui/ui (150).png" alt="" class="cover"></div>


        <img src="../../assets/ui/ui (65).png" alt="" class="style_warrior_card_up_mark" v-if="warriorData.isUp">
        <img src="../../assets/ui/ui (84).png" alt="" class="style_warrior_card_up_mark" v-if="warriorData.isUpWork">
        <img src="../../assets/ui/ui (73).png" alt="" class="style_warrior_card_up_mark" v-if="warriorData.isUp && warriorData.isUpWork">

        
        <div class="style_warrior_card_bottom">
            <div class="style_warrior_card_bottom_level">Lv.{{ warriorData.level }}</div>
            <div class="style_warrior_card_bottom_cost_img"></div>
            <div class="style_warrior_card_bottom_cost_num">{{warriorData.cost}}</div>


            <div class="style_warrior_card_bottom_arm" v-if="!showUpArms && warriorData.id != -1">
                <img src="../../assets/ui/ui (81).png"  v-if="!warriorData.isUpArms" class="cover">
                <img src="../../assets/ui/ui (82).png"  v-if="warriorData.isUpArms" class="cover">
                <img src="../../assets/ui/ui (79).png"  v-if="warriorData.arms == '步' && !warriorData.isUpArms" class="style_warrior_card_bottom_arm_icon">
                <img src="../../assets/ui/ui (70).png"  v-if="warriorData.arms == '骑' && !warriorData.isUpArms" class="style_warrior_card_bottom_arm_icon">
                <img src="../../assets/ui/ui (62).png"  v-if="warriorData.arms == '弓' && !warriorData.isUpArms" class="style_warrior_card_bottom_arm_icon">

                <img src="../../assets/ui/ui (71).png"  v-if="warriorData.arms == '骑' && warriorData.isUpArms" class="style_warrior_card_bottom_arm_icon">
                <img src="../../assets/ui/ui (80).png"  v-if="warriorData.arms == '步' && warriorData.isUpArms" class="style_warrior_card_bottom_arm_icon">
                <img src="../../assets/ui/ui (63).png"  v-if="warriorData.arms == '弓' && warriorData.isUpArms" class="style_warrior_card_bottom_arm_icon">
                <div class="arm_num">{{warriorData.range}}</div>
            </div>
            <div class="up_bottom_arm" v-if="showUpArms && warriorData.id != -1">
                <img src="../../assets/ui/ui (81).png"  v-if="!warriorData.isUpArms" class="cover">
                <img src="../../assets/ui/ui (82).png"  v-if="warriorData.isUpArms" class="cover">
                <img :src="UpArmsIconImg"  alt="" class="cover">
                <div class="arm_num">{{warriorData.range}}</div>
            </div>
        </div>

        <div class="style_warrior_card_sign">
            <img src="../../assets/ui/ui (66).png" alt="" v-if="warriorData.sign == 's2'" class="cover">
            <img src="../../assets/ui/ui (67).png" alt="" v-if="warriorData.sign == 's3'" class="cover">
            <img src="../../assets/ui/ui (68).png" alt="" v-if="warriorData.sign == 'sp'" class="cover">
            <img src="../../assets/ui/ui (69).png" alt="" v-if="warriorData.sign == 'xp'" class="cover">
        </div>
    </div>
</template>

<script>
export default {
    name : 'WarriorCard',
    props: {
        warriorData: null,
        cardStyle: {
            default : "2_3",
            validator(value) {
                return ["2_3", "2_3_sml_pic"].includes(value);
            }
        },
        filterGray:{
            type:Boolean,
            default : false,
        },
        showUpArms:{
            type:Boolean,
            default : false,
        }
    },
    computed:{
        //高级兵种图标
        UpArmsIconImg(){
            return require(`../../assets/${this.warriorData.isUpArms?"up_arms":"arms"}/${this.$store.armsMap[this.warriorData.upArms[this.warriorData.choseUpArmsIdx]]}.png`)
        },
    }
}
</script>

<style lang="less" scoped>
@color_han_dark : rgb(50, 0, 61);
@color_han : rgb(61, 0, 75);
@color_wei_dark : rgb(0, 23, 72);
@color_wei : rgb(0, 24, 76);
@color_shu_dark : rgb(10, 58, 0);
@color_shu : rgb(13, 78, 0);
@color_wu_dark : rgb(58, 0, 0);
@color_wu : rgb(70, 0, 0);
@color_qun_dark : rgb(59, 59, 59);
@color_qun : rgb(71, 71, 71);
@color_jin_dark : rgb(0, 50, 53);
@color_jin : rgb(0, 61, 65);
.style_warrior_card{
    position: absolute; left: 0; top: 0;
    width: 100%; height: 100%;
    font-size: 100%;
    //变灰
    .filter_gray{
        filter: grayscale(1);
    }
    .style_warrior_card_camp{
        left: 2%; top: 2.5%;
        width: 18%;height: 12%;
    }
    .style_warrior_card_up_mark{
        right: -9%; top: -7.5%;
        width: 24%; height: 16%;
    }
    .style_warrior_card_black_tape{
        right: 1.5%; top: 1.3%;
        width: 80%; height: 10%;
        background: linear-gradient(to bottom,black,rgba(0, 0, 0, 0));
    }
    .style_warrior_card_star_list{
        right: 6%; top: 3%;
        width: 50%; height: 10%;
        .style_warrior_card_star_list_star{
            position: relative; float: left;
            width: 19%; height: 62%;
        }
    }
    .style_warrior_card_name{
        top: 16%; left: 5.5%;
        width: 1%; height: 50%;
        font-size: 70%;
        line-height: 130%;
        color: rgb(203, 206, 162);
    }
    .style_warrior_card_hp_num{
        left: 20%; top: 0;
        width: 22%; height: 13%;
        text-align: left;
        font-size: 60%;
        display: flex;
        align-items: center;
        // background: #000;
        // box-shadow:  0 0 1px black;
    }
    .style_warrior_card_bottom{
        left: 2%; bottom: 1%;
        width: 96%; height: 9.5%;
        font-size: 80%;
        // background: #000;
        color: rgb(217, 221, 135);
        .style_warrior_card_bottom_level{
            left: 0; top: 0;
            width: 20%; height: 100%;
            font-size: 100%;
            // font-weight: 500;
            display: flex;
            align-items: center;
            text-shadow: 0 0 1px black;
        }
        .style_warrior_card_bottom_cost_img{
            left: 31%; top: 5%;
            width: 23%; height: 80%;
            background: url('../../assets/ui/ui (61).png') center  no-repeat;
            background-size: 100% 100%;
        }
        .style_warrior_card_bottom_cost_num{
            left: 55%;
            width: 20%; height: 100%;
            font-size: 100%;
            display: flex;
            align-items: center;
            text-align: left;
            text-shadow: 0 0 1px black;
        }
        .style_warrior_card_bottom_arm,.up_bottom_arm{
            right: 0; top: auto; bottom: 0;
            width: 24%; height: 150%;
            font-size: 100%;
            .style_warrior_card_bottom_arm_icon{
                left: 0; top: 0;
                width: 100%; height: 100%;
            }
            .arm_num{
                right: 0; bottom: 0;
                width: 25%; height: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 100%;
            }
        }
        .up_bottom_arm{
            right: -2%; top: auto; bottom: 0;
            width: 30%; height: 200%;
            font-size: 100%;
            .arm_num{
                right: 8%;
                bottom: -5%;
            }
        }
    }
    .style_warrior_card_sign{
        top: auto;
        bottom: 12%; left: 3%;
        width: 21%; height: 14%;
    }


}
</style>