

export class Util {
    constructor() {

    }

    CampBondBonus(team) {
        let map = {}
        for (let i in team) {
            if (map[team[i].camp]) {
                map[team[i].camp] += 1;
            }
            else map[team[i].camp] = 1;
        }
        for (let key in map) {
            if (map[key] > 1) {
                return key;
            }
        }
        return false;
    }

}

