<template>
    <div class="prompt_container" ref="prompt_container">
        <div class="mask cover" @click="Close"></div>
        
        <div class="container">
            <TopicTitle :title="$store.prompt.title" :closeFunc="Close" :titleFontSize="5.5" />



            <div class="msg_container">
                <div class="text">{{ $store.prompt.msg }}</div>
            </div>
            <div class="confirm_btn" ref="confirm_btn"
            @touchstart="$refs.confirm_btn.classList.add('click')"
            @touchend="$refs.confirm_btn.classList.remove('click')" 
            @click="Confirm">确 定</div>
        </div>
    </div>
</template>

<script>
import TopicTitle from '../style/TopicTitle.vue';
export default {
    name : 'Prompt',
    components:{
        TopicTitle
    },
    methods:{
        Close(){
            this.$store.prompt.status = false;
        },
        Confirm(){
            this.$store.prompt.CallBack();
            this.Close();
        }
    },
    mounted(){
        this.$store.AutoStyle(this.$refs.prompt_container)
    }
}
</script>

<style lang="less">
.prompt_container{
