        background:radial-gradient(rgba(0, 0, 0, 0.5),rgb(0, 0, 0));
    }
    .container{
        left: 0; top: 50%;
        width: 100vw; height: 60vw;
        margin: -30vw 0 0 0;
        background: url('../../assets/background/bg (7).png')#393b3e bottom right no-repeat;
        background-size: cover;
        animation: lively_emerge .12s linear 1;
        .msg_container{
            position: absolute; 
            top: 15vw; left: 5vw;
            width: 90vw; height: 30vw;
            background: rgba(0, 0, 0, 0.3);
            .text{
                left: 50%; top: 50%;
                font-size: 3vw;
                width: 70vw; 
                color: white;
                transform: translate(-50%,-50%);
                // letter-spacing: .8vw;
            }
        }
        
        .confirm_btn{
            left: 39vw; top: auto;
            bottom: 5vw;
            width: 22vw; height: 6vw;
            line-height: 6vw;
            background: white;
            color: black;
            font-weight: bold;
        }
    }
    
}
.prompt_container.pad{
    z-index: 100;
    position: fixed; left: 0; top: 0;
    width: 100vw; height: 100vh;
    .mask{
        background:radial-gradient(rgba(0, 0, 0, 0.5),rgb(0, 0, 0));
    }
    .container{
        left: 0; top: 50%;
        width: 100vw; height: 30vh;
        margin: -15vh 0 0 0;
        background: url('../../assets/background/bg (7).png')#393b3e bottom right no-repeat;
        background-size: cover;
        animation: lively_emerge .12s linear 1;
        .msg_container{
            position: absolute; 
            top: 15vw; left: 5vw;
            width: 90vw; height: 30vw;
            background: rgba(0, 0, 0, 0.3);
            .text{
                left: 50%; top: 50%;
                font-size: 2vh;
                width: 70vw; 
                color: white;
                transform: translate(-50%,-50%);
            }
        }
        
        .confirm_btn{
