<template>
    <div class="lineup_panel" ref="lineup_panel">
        <div class="control_bar">
            <div class="search_part">
                <div class="title">搜索</div>
                <input type="text" class="search_ipt" placeholder="武将或队伍名称" ref="user_name" v-model="searchValue">
                
                
            </div>
            <div class="group_mark" v-if="$store.battle.currentGroup != null">
                <div class="group_visible_btn" ref="group_visible_btn"
                @touchstart="$refs.group_visible_btn.classList.add('touch')"
                @touchend="$refs.group_visible_btn.classList.remove('touch')">
                    <div class="title">组</div>
                    <div class="group_name">{{ $store.battle.currentGroup.groupName }}</div>
                    <div class="color_tape" :style="`background-color:${$store.battle.currentGroup.groupColor};`"></div>
                    <div class="close_btn" @click="RemoveGroupSelect()"></div>
                </div>
            </div>
        </div>
        <div class="container">
            <div class="box" v-for="team,index in $store.battle.visibleLineups" :key="team.index" ref="box" 
            @touchstart="$refs.box[index].classList.add('click')"
            @touchend="$refs.box[index].classList.remove('click')"
            v-show="ShowRule(team)"
            @click="OpenTeamDetailPanel(team)">
                <div class="title_bg"></div>
                <div class="title">{{ team.lineupName == '' ? "未命名" : team.lineupName }}</div>
                <div class="group_mark_list">
                    <div class="g_color_cirle" v-for="gid in team.groupIds" :key="gid">
                        <div class="color cover" :style="`background-color:${GetGroupColor(gid)};`"></div>
                    </div>
                </div>
                <div class="warrior_list">
                    <div class="warrior_box" v-for="w in team.list" :key="w.index">
                        <div class="warrior_card">
                            <WarriorElongatedCard :warriorData="w" cardStyle="2_1" :showUpArms="true" />
                        </div>
                    </div>
                </div>
                <div class="number">{{ index + 1 }}</div>
                <div class="create_time">创建时间：{{ team.createTime }}</div>
            </div>
            <div class="empty_tip" v-if="$store.battle.lineupList.length == 0">还没有阵容保存</div>
        </div>
        <!-- 队伍详情面板 -->
        <div class="detail_panel" ref="detail_panel" v-if="display">
            <div class="mask" @click="display = false"></div>
            
            <div class="container"  >
                <TopicTitle :title="curTeam.lineupName ? curTeam.lineupName : '未命名'" :closeFunc="CloseDetailPanel" :titleFontSize="4"  />
                <div class="warrior_list" ref="team_config_container">
                    <div class="warrior_box" v-for="w in curTeam.list" :key="w.index">
                        <div class="warrior_card">
                            <WarriorCard :warriorData="w" :showUpArms="true" cardStyle="2_3" />
                        </div>
                        <div class="other_list">
                            <div class="tactics_box" v-for="t,tIdx in w.tacticsArr" :key="t" v-show="t.id != -1 && tIdx">
                                <div class="icon">
                                    <TacticsIcon :tacticsData="t" />
                                </div>
                            </div>
                            <div class="block"></div>
                            <div class="arms_spec" v-for="spec,spId in w.armsCommonSpec[w.choseUpArmsIdx]" :key="spec.index" 
                            v-show="spId == w.choseArmsCharacterIdx[0] || spId == w.choseArmsCharacterIdx[1]">
                                <img :src="ArmSpecIconImg(spec.type)" alt="" class="spec_img">
                                <div class="text">{{ spec.name }}</div>
                            </div>
                            <div class="block"></div>
                            
                            <div class="artifact_box" v-if="w.artifact">
                                <!-- <img :src="AritifactImgIcon(w.artifact)" alt="" class="art_up_sign">
                                <div class="name">{{ w.artifact.name }}</div>
                                <div class="forge_eff">
                                    <img :src="ForgeEffImgIcon(w.artifact)" class="cover">
                                    <div class="text cover">{{ w.artifact.forgeEffArr[w.artifact.forgeChoseIdx].name }}</div>
                                </div> -->
                                <ArtifactCard :artifactData="w.artifact" />
                            </div>
                            <div class="plus_box" v-for="plusValue,attrKey in  w.plus" :key="attrKey.index" v-show="plusValue > 0">
                                {{ `${attrMap[attrKey]}加点+${plusValue}` }}
                            </div>
                        </div>


                    </div>
                </div>
                <div class="group_mark_list">
                    <div class="g_color_cirle" v-for="gid in curTeam.groupIds" :key="gid">
                        <div class="color cover" :style="`background-color:${GetGroupColor(gid)};`"></div>
                    </div>
                </div>
                <div class="btn_list">
                    <div class="delete_btn" ref="delete_btn" 
                    @touchstart="$refs.delete_btn.classList.add('click')"
                    @touchend="$refs.delete_btn.classList.remove('click')"
                    @click="DeleteThis()"
                    >删除队伍</div>
                    <div class="download_config_img" ref="download_config_img"
                    @touchstart="$refs.download_config_img.classList.add('click')"
                    @touchend="$refs.download_config_img.classList.remove('click')"
                    @click="DownloadLineupPic()">下载配置图</div>
                    
                    <div class="apply_btn_right" ref="apply_btn_right" 
                    @touchstart="$refs.apply_btn_right.classList.add('click')"
                    @touchend="$refs.apply_btn_right.classList.remove('click')"
                    @click="ApplyLineup()">
                    应用配置</div>
                    <div class="add_to_group_btn" ref="add_to_group_btn"
                    @touchstart="$refs.add_to_group_btn.classList.add('click')"
                    @touchend="$refs.add_to_group_btn.classList.remove('click')"
                    @click="OpenGroupSelectPanel()">
                    添加到组
                    </div>
                    
                    
                </div>
            </div>
        </div>

        

        <div class="apply_lineup_random_btn" ref="apply_lineup_random_btn"
        @touchstart="$refs.apply_lineup_random_btn.classList.add('click')"
        @touchend="$refs.apply_lineup_random_btn.classList.remove('click')"
        @click="ApplyLineupByRandom()">
        随机应用
        </div>
        <div class="group_btn" ref="group_btn"
        @touchstart="$refs.group_btn.classList.add('click')"
        @touchend="$refs.group_btn.classList.remove('click')"
        @click="EnterLineupGroup()">
        分组
        </div>
        <div class="clear_all_btn" ref="clear_all_btn" @click="ClearAll()"
        @touchstart="$refs.clear_all_btn.classList.add('click')"
        @touchend="$refs.clear_all_btn.classList.remove('click')"
        v-if="$store.battle.lineupList.length > 0"
        >清空阵容</div>

        <div class="group_select_panel" v-if="groupSelectDisplay">
            <div class="mask cover" @click="CloseGroupSelect()"></div>

            <div class="group_container">
                <div class="container">
                    <div class="group" ref="group" 
                    @touchstart="$refs.group[gId].classList.add('click')"
                    @touchend="$refs.group[gId].classList.remove('click')"
                    v-for="g,gId in $store.battle.lineupGroups" :key="g.index"
                    @click="AddToGroup(g)">
                        <div class="color_tape" :style="`background-color:${g.groupColor==''?'black':g.groupColor};`"></div>
                        <div class="name">{{ g.groupName }}</div>
                        <div class="loadmark" v-if="curTeam==null?false:curTeam.groupIds.includes(g.gid)">已添加</div>
                    </div>


                </div>
                <TopicTitle title="添加到组" :titleFontSize="5"  :closeFunc="CloseGroupSelect" />
            </div>
        </div>


        <TopNav title="阵 容" />
        <router-view/>
    </div>
</template>

<script>
import TopicTitle from '@/components/style/TopicTitle.vue';
import WarriorElongatedCard from '@/components/style/WarriorElongatedCard.vue';
import TopNav from '@/components/container/TopNav.vue';
import WarriorCard from '@/components/style/WarriorCard.vue';
import TacticsIcon from '@/components/style/TacticsIcon.vue';
import ArtifactCard from '@/components/style/ArtifactCard.vue';
import { BattleUtil } from '@/util/battle';
import Warrior from '@/util/warrior';

import html2canvas from 'html2canvas';
import Save from '@/util/save';
export default {
    name : 'Lineup',
    components:{
        TopNav,
        TopicTitle,
        WarriorElongatedCard,
        WarriorCard,
        TacticsIcon,
        ArtifactCard
    },
    data(){
        return{
            display : false,
            groupSelectDisplay:false,
            curTeam : null,
            attrMap:{
                ad : "攻击",
                de : "防御",
                speed : "速度",
                tact : "谋略",
            },


            searchValue : "",
            searchModel : true,
            // 用于存储定时器ID
            saveTimer:null,
        }
    },
    computed:{
        
    },
    methods:{
        GetGroupColor(gid){
            for(let i in this.$store.battle.lineupGroups){
                if(this.$store.battle.lineupGroups[i].gid == gid){
                    return this.$store.battle.lineupGroups[i].groupColor;
                }
            }
            return false;
        },
        //移除组选择
        RemoveGroupSelect(){
            this.$store.battle.visibleLineups = this.$store.battle.lineupList;
            this.$store.battle.currentGroup = null;
        },
        //将阵容添加到组
        OpenGroupSelectPanel(){
            //清理掉多余的组号
            this.groupSelectDisplay = true;
        },
        //选择组号添加
        AddToGroup(groupData){
            for(let i in this.curTeam.groupIds){
                if(this.curTeam.groupIds[i] == groupData.gid){
                    this.curTeam.groupIds.splice(i,1);
                    this.$store.Alert(`已移除出${groupData.groupName}分组`);
                    return;
                }
            }
            this.curTeam.groupIds.unshift(groupData.gid);

            //保存数据到本地
            clearTimeout(this.saveTimer)
            this.saveTimer = setTimeout(() => {
                let s = new Save(this.$store)
                s.SaveBattleData()
            }, 2000);
        },
        //关闭组选择界面
        CloseGroupSelect(){
            this.groupSelectDisplay = false;
        },
        //进入阵容分组界面
        EnterLineupGroup(){
            this.$router.push({
                path : `${this.$route.path}/lineup_group`,
            })
        },
        //数据显示规则，用于筛选
        ShowRule(teamData){
            if(this.searchModel && this.searchValue){
                for(let i in teamData.list){
                    if(teamData.list[i].name && teamData.list[i].name.includes(this.searchValue)) return true;
                }
                if(teamData.lineupName.includes(this.searchValue)) return true;
                return false;
            }
            return true;
            
        },
        //下载队伍配置图
        async DownloadLineupPic(){
            const target = this.$refs.team_config_container;
            if (!target) {
                console.error('未找到目标元素');
                return;
            }
            
            // 保存原始样式
            target.classList.add('download');
            try {
                // 等待所有资源加载完成
                await document.fonts.ready;
                
                // 调用 html2canvas 转换 DOM 为 canvas
                const canvas = await html2canvas(target, {
                    useCORS: true,
                    scale: 2,
                    backgroundColor: '#393b3e',
                    logging: false,
                    letterRendering: true,
                    // 增加允许跨域的配置
                    allowTaint: false, // 不允许污染
                    // ignoreElements: (element) => {
                    //     // 忽略可能引起跨域的元素
                    //     if (element.tagName === 'IMG' && !element.src.startsWith(window.location.origin)) {
                    //         console.warn('忽略跨域图片:', element.src);
                    //         return true;
                    //     }
                    //     return false;
                    // }
                });

                // 将 canvas 转为图片 URL
                const imgUrl = canvas.toDataURL('image/png');

                // 创建下载链接并触发下载
                const link = document.createElement('a');
                link.href = imgUrl;
                let timeStr = new Date().getTime()
                link.download = `${this.curTeam.lineupName}阵容截图${timeStr}.png`;
                link.click();
                this.$store.Alert(`${this.curTeam.lineupName}阵容截图 已保存`)
            } catch (error) {
                console.error('截图失败：', error);
                this.$store.Alert(`阵容截图下载失败！`)
            } finally {
                // 恢复目标元素原有的样式
                target.classList.remove('download');
            }
        
        },
        AritifactImgIcon(art){
            switch(art.effQualityIdx){
                case 0 :
                case 1 : return require('../../assets/ui/ui (65).png');
                case 2 : return require('../../assets/ui/ui (73).png');
            }
        },
        ForgeEffImgIcon(art){
            switch(art.effQualityIdx){
                case 0 : return require('../../assets/ui/ui (182).png');
                case 1 : return require('../../assets/ui/ui (184).png');
                case 2 : return require('../../assets/ui/ui (183).png');
            }
        },
        //查看队伍配置详情
        OpenTeamDetailPanel(data){
            this.curTeam = data;
            this.display = true;
        },
        // 关闭配置详情面板
        CloseDetailPanel(){
            this.display = false
        },
        //高级兵种特性图标
        ArmSpecIconImg(type){
            switch(type){
                case "步" : return require('../../assets/ui/ui (124).png')
                case "骑" : return require('../../assets/ui/ui (122).png')
                case "弓" : return require('../../assets/ui/ui (123).png')
            }
        },
        //随机应用某个阵容到敌方或者是我方的队伍中
        ApplyLineupByRandom(){
            let that = this;
            //应用到我方
            let f1 = function(){
                let length = that.$store.battle.visibleLineups.length;
                let rand = Math.floor(Math.random()*length);
                for(let i in that.$store.battle.teams.list[0]){
                    let warrior = new Warrior()
                    warrior.CopyData(that.$store.battle.visibleLineups[rand].list[i]);
                    that.$store.battle.teams.list[0][i] = warrior;
                }
                that.$store.Alert(`已导入${that.$store.battle.visibleLineups[rand].lineupName}配置到我方`);
                that.$router.back();
            }
            //应用到敌方
            let f2 = function(){
                let length = that.$store.battle.visibleLineups.length;
                let rand = Math.floor(Math.random()*length);
                for(let i in that.$store.battle.teams.list[1]){
                    let warrior = new Warrior()
                    warrior.CopyData(that.$store.battle.visibleLineups[rand].list[i]);
                    that.$store.battle.teams.list[1][i] = warrior;
                }
                that.$store.Alert(`已导入${that.$store.battle.visibleLineups[rand].lineupName}配置到敌方`);
                that.$router.back();
            }


            let params = {
                title:"随机应用阵容",
                msg : `从阵容列表中，随机应用一队到配置界面`,
                optionBtnNames : ['配置到我方','配置到敌方'],
                optionCallBack : [f1,f2]
            }
            this.$store.OptionalPrompt(params)
        },
        //应用某个阵容
        ApplyLineup(){
            

            let that = this;
            let f = function(whichTeam){
                for(let i in whichTeam){
                    let warrior = new Warrior();
                    warrior.CopyData(that.curTeam.list[i]);
                    whichTeam[i] = warrior;
                }
                that.$store.Alert('应用成功');
                that.$router.back();
            }
            let f1 = function(){
                let whichTeam = that.$store.battle.teams.list[0]
                f(whichTeam)
            }
            let f2 = function(){
                let whichTeam = that.$store.battle.teams.list[1]
                f(whichTeam)
            }

            let params = {
                title:"应用阵容",
                msg : `应用队伍配置到...`,
                optionBtnNames : ['配置到我方队伍','配置到敌方队伍'],
                optionCallBack : [
                    f1,
                    f2
                ]
            }
            this.$store.OptionalPrompt(params)
        },
        //删除某个阵容
        DeleteThis(){
            if(this.$store.battle.lineupList.length == 0) return
            let that = this;
            let f = function(){
                let u = new BattleUtil(that.$store);
                let res = u.DeleteLineup(that.curTeam.list)
                if(res){
                    that.display = false;
                    that.$store.Alert('删除成功!');
                }
                else that.$store.Alert('删除失败!');
            }
            let params = {
                msg : `确定从阵容中删除这个队伍吗？`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        },
        //清除所有阵容
        ClearAll(){
            let that = this;
            let f = function(){
                let u = new BattleUtil(that.$store);
                u.ClearAllLineup();
                // that.$bus.$emit('alert','清除成功');
                that.$store.Alert('清除成功');
            }
            let params = {
                msg : `确定清空这${that.$store.battle.lineupList.length}支队伍吗`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        },
        
    },
    mounted(){
        this.$store.AutoStyle(this.$refs.lineup_panel)
    }

}
</script>

<style lang="less" scoped>
.lineup_panel{
    position: absolute;left: 0; top: 0;
    width: 100vw;
    height: 100vh;
    background: url('../../assets/background/bg (1).jpg')right bottom no-repeat;
    background-size: cover;
    .bg_command{
        background: url('../../assets/background/bg (44).png')#006000 bottom right no-repeat;
        background-size: cover;
    }
    .bg_active{
        background: url('../../assets/background/bg (44).png')#650000 bottom right no-repeat;
        background-size: cover;
    }
    .bg_combo{
        background: url('../../assets/background/bg (44).png')#6b0268 bottom right no-repeat;
        background-size: cover;
    }
    .bg_passive{
        background: url('../../assets/background/bg (44).png')#23008b bottom right no-repeat;
        background-size: cover;
    }
    .control_bar{
        position: absolute; top: 13vh; left: 5vw;
        width: 90vw; height: 7vw;
        background: rgba(0, 0, 0, 0.4);
        .search_part{
            left: 0; top: 0;
            width: 45vw; height: 100%;
            .title{
                position: relative; float: left;
                width: 10vw; height: 4vw;
                margin: 1.5vw 0 0 0;
                font-size: 2.9vw;
                line-height: 4vw;
                color: rgb(156, 156, 156);
            }
            .search_ipt{
                position: relative; float: left;
                width: 30vw; height: 4vw;
                background: rgba(0, 0, 0, 0.8);
                text-align: left;
                color: white;
                border: 0;
                font-size: 2.9vw;
                margin: 1.5vw 0 0 0;
                &:focus{
                    outline: none;
                }
            }
        }
        .group_mark{
            position: absolute;left: 45vw;top: 0;
            width: 45vw; height: 7vw;
            .group_visible_btn{
                top: 1vw; right: 1vw;
                width: 40vw; height: 5vw;
                background: #000000;
                .group_name{
                    left: 10vw; top: 0;
                    width: 30vw; height: 100%;
                    display: flex;
                    align-items: center;
                    text-align: left;
                    color: gray;
                    
                }
                .title{
                    left: 3vw; top: 0;
                    width: 6vw; height: 100%;
                    display: flex;
                    align-items: center;
                    text-align: left;
                }
                .color_tape{
                    left: 0; top: 0;
                    width: 1vw; height: 100%;
                }
                .close_btn{
                    right: 0;top: 0;left: auto;
                    width: 5vw; height: 5vw;
                    background: url('../../assets/ui/ui\ \(141\).png')center no-repeat;
                    background-size: 70% 70%;
                    opacity: .8;
                }
                &.touch{
                    background: #575757;
                }
            }
            
        }
    }
    .container{
        position: absolute; top: 15vh; left: 5vw;
        margin: 3vw 0 0 0;
        width: 90vw; height: 74vh;
        overflow: scroll;
        .box{
            position: relative;
            width: 90vw; height: 26vw;
            margin: 2vw 0 1vw 0;
            background: url('../../assets/ui/ui (174).png')rgba(24, 24, 24, 0.755) no-repeat;
            background-size: cover;
            .number{
                position : absolute;right: 0; top: 0;
                width: 4vw; height: 4vw;
                background: rgb(116, 0, 0);
                text-align: center;
                line-height: 4vw;
                color: rgb(217, 217, 217);
                font-size: 2.5vw;
            }
            .title{
                left: 5vw; top: 0;
                height: 7vw;
                line-height: 7vw;
                text-align: left;
                font-family: 'FZLBJW';
            }
            .title_bg{
                position: absolute;
                left: 0; top: 0;
                width: 40vw; height: 8vw;
                background: url('../../assets/ui/ui\ \(180\).png');
                background-size: 100% 100%;
            }
            .group_mark_list{
                left: 50vw; top: 2.5vw;
                width: 35vw; height: 2vw;
                // background: #000;
                .g_color_cirle{
                    position: relative; float: right;
                    width: 2vw; height: 2vw;
                    margin: 0 .5vw;
                    .color{
                        border-radius: 50%;
                        opacity: .7;
                    }
                }
            }
            .warrior_list{
                position: absolute; left: 3.5vw; top: 8vw;
                width: 83vw; height: 12vw;
                .warrior_box{
                    position: relative; float: left;
                    width: 24vw; height: 12vw;
                    margin-left: 3vw;
                    background: rgb(79, 79, 79);
                    .warrior_card{
                        position: absolute;
                        left: 0; top: 0;
                        width: 24vw; height: 12vw;
                        margin: 0 0 0 0;
                        transform: scale(.96);
                    }
                }
            }
            .create_time{
                left: 5vw; bottom: 0;
                width: 50vw; height: 5vw;
                line-height: 5vw;
                font-size: 2.5vw;
                text-align: left;
                color: rgb(112, 112, 112);
            }
            
        }
        .empty_tip{
            width: 90vw;
            height: 50vh;
            line-height: 50vh;
            font-family: 'FZLBJW';
            color: white;
            font-size: 4vw;
        }
    }
    .detail_panel{
        position: absolute;left: 0; top: 0;
        width: 100vw; height: 100vh;
        z-index: 1;
        .mask{
            width: 100%; height: 100%;
            background: #0000008d;
        }
        .container{
            left: 0; top: 50%;
            width: 100vw; height: 135vw;
            margin: -60vw 0 0 0;
            background: url('../../assets/background/bg (7).png')#393b3e bottom right no-repeat;
            background-size: cover;
            .group_mark_list{
                left: auto; top: 6.5vw; right: 15vw;
                width: 35vw; height: 2.5vw;
                // background: #000;
                .g_color_cirle{
                    position: relative; float: right;
                    width: 2.5vw; height: 2.5vw;
                    margin: 0 .5vw;
                    .color{
                        border-radius: 50%;
                        opacity: .7;
                    }
                }
            }
            .warrior_list{
                position: absolute;
                top: 10vw; left: 10vw; 
                padding: 5vw 0 0 0;
                width: 80vw; height: 95vw;
                // background: #000;
                // display: initial;
                .warrior_box{
                    position: relative; float: left;
