<template>
    <div class="warrior_info" ref="warrior_info">
        <div class="container" v-if="$store.buffer.curWarrior != null">
            <div class="warrior_post">
                <img :src="$store.buffer.curWarrior.img.post" alt="" class="post">
                <div class="shadow"></div>
                <div class="camp">
                    <img :src="CampImgUrl($store.buffer.curWarrior.camp)" class="cover" alt="">
                    <div class="name">
                        {{ $store.buffer.curWarrior.name }}
                    </div>
                </div>
                <div class="star_bar">
                    <li v-for="s in $store.buffer.curWarrior.red" :key="s.index"><img src="../../assets/ui/ui (74).png" alt="" class="cover"></li>
                    <li v-for="s in (5-$store.buffer.curWarrior.red)" :key="s.index"><img src="../../assets/ui/ui (76).png" alt="" class="cover"></li>
                </div>
                <div class="sign" v-if="$store.buffer.curWarrior.sign != null" >
                    <img :src="SignImgUrl()" class="cover" alt="">
                </div>
                <div class="nav_list">
                    <li ref="panel_nav_li"
                    v-for="(n,index) in navList" :key="index" @click="SwitchNav(index)">
                        <div class="current_img" v-if="index == navListIdx"></div>
                        <div class="text cover" v-if="index != navListIdx">{{ n }}</div>
                        <div class="text cover current" v-if="index == navListIdx">{{ n }}</div>
                        <div class="apart" v-if="index != navList.length-1"></div>
                    </li>
                </div>
            </div>

            <!-- 基本信息板块 -->
            <div class="base_info_panel" v-if="navListIdx == 0">
                <div class="base_info">
                    <div class="attr_title">基础属性
                        <div class="other_info">
                            <span>{{ `统帅值：${$store.buffer.curWarrior.cost}&nbsp&nbsp/&nbsp&nbsp` }}</span>
                            <span>{{ `攻击距离：${$store.buffer.curWarrior.range}&nbsp&nbsp/&nbsp&nbsp 兵种：${$store.buffer.curWarrior.arms}` }}</span>
                        </div>
                    </div>
                    <div class="attr_line" v-for="a in attrList" :key="a.index">
                        <div class="icon">
                            <img :src="AttrIconImg(a)" alt="" class="cover">
                        </div>
                        <div class="title">{{ a }}</div>
                        <div class="value">
                            <span>{{ CountAttr(a,1) }}</span>
                            <span style="color: #29be00;">{{ "(+" + GetWarriorAttr(a,false) + ')' }}</span>
                            <span style="color: gray;">>></span>
                            <span>{{ CountAttr(a,50) }}</span>
                        </div>
                    </div>
                </div>
                <div class="tactics_panel" >
                    <div class="tactics_box" v-for="t,index in $store.buffer.curWarrior.tacticsArr" :key="t.index"
                    v-show="index == 0 || index != 0 && $store.buffer.curWarrior.own"
                    @click="TacticsConfig(t,index)">
                        <div class="icon">
                            <TacticsIcon :tacticsData="t" />
                        </div>
                    </div>
                    <div class="artifact_box" @click="LoadArtifact()" v-if="$store.buffer.curWarrior.own">
                        <ArtifactCard :artifactData="$store.buffer.curWarrior.artifact" v-if="$store.buffer.curWarrior.artifact" />
                        <div class="unload_txt" v-if="$store.buffer.curWarrior.artifact == null">可授予</div>
                    </div>
                </div>
            </div>
            <!-- 进阶板块 -->
            <div class="adjust_panel" v-if="ShowRule('adjust_panel')">
                <div class="line" >
                    <div class="sub_red_btn" ref="sub_red_btn" @touchstart="$refs.sub_red_btn.classList.add('click')"
                    @touchend="$refs.sub_red_btn.classList.remove('click')"
                    @click="AdjustStar(false)">退阶</div>
                    <div class="add_red_btn" ref="add_red_btn" @touchstart="$refs.add_red_btn.classList.add('click')"
                    @touchend="$refs.add_red_btn.classList.remove('click')"
                    @click="AdjustStar(true)">进阶</div>
                    <div class="star_bar">
                        <li v-for="s in $store.buffer.curWarrior.red" :key="s.index"><img src="../../assets/ui/ui (74).png" alt="" class="cover"></li>
                    <li v-for="s in (5-$store.buffer.curWarrior.red)" :key="s.index"><img src="../../assets/ui/ui (76).png" alt="" class="cover"></li>
                    </div>
                </div>
                <div class="line">
                    <div class="up_sign" v-if="$store.buffer.curWarrior.isUp || $store.buffer.curWarrior.isUpWork" >
                        <img :src="UpImgUrl()" class="cover" alt="">
                    </div>
                    <div class="info_bar">
                        <div class="text">统帅值 : {{ $store.buffer.curWarrior.cost }}</div>
                    </div>
                    <div class="up_work_btn" ref="up_work_btn" @touchstart="$refs.up_work_btn.classList.add('click')"
                    @touchend="$refs.up_work_btn.classList.remove('click')"
                    @click="UpWarriorOpera('upWork',!$store.buffer.curWarrior.isUpWork)">
                        {{ $store.buffer.curWarrior.isUpWork ? "取消深造" : "深造" }}
                    </div>
                    <div class="up_btn" ref="up_btn" @touchstart="$refs.up_btn.classList.add('click')"
                    @touchend="$refs.up_btn.classList.remove('click')"
                    @click="UpWarriorOpera('up',!$store.buffer.curWarrior.isUp)">
                        {{$store.buffer.curWarrior.isUp ? "取消觉醒" : "觉醒"}}
                    </div>
                </div>
                <div class="line">
                    <div class="arms_sign">
                        <img :src="ArmsImageUrl" class="cover" alt="">
                    </div>
                    <div class="info_bar">
                        <div class="text">{{ $store.buffer.curWarrior.upArms[0] }}/{{ $store.buffer.curWarrior.upArms[1] }}</div>
                        <div class="text">攻击距离：{{ $store.buffer.curWarrior.range }}</div>
                    </div>
                    <div class="up_arms_btn" ref="up_arms_btn" 
                    @touchstart="$refs.up_arms_btn.classList.add('click')"
                    @touchend="$refs.up_arms_btn.classList.remove('click')"
                    @click="UpWarriorOpera('upArms',!$store.buffer.curWarrior.isUpArms)">
                        {{$store.buffer.curWarrior.isUpArms ? "取消进阶" : "进阶兵种"}}
                    </div>
                </div>
                <div class="line">
                    <div class="up_arms_icon">
                        <div class="icon_box" v-for="a,a_index in $store.buffer.curWarrior.upArms" :key="a.index"
                        @click="ExchangeArmsIndex(a_index)">
                            <img src="../../assets/ui/ui (81).png" :class="ArmIconClassName(a_index)" v-if="!$store.buffer.curWarrior.isUpArms" alt="">
                            <img src="../../assets/ui/ui (82).png" :class="ArmIconClassName(a_index)" v-if="$store.buffer.curWarrior.isUpArms" alt="">
                            <img :src="UpArmsIconImg($store.buffer.curWarrior,a)" alt="" :class="ArmIconClassName(a_index)">
                            <div class="name">{{ a }}</div>
                        </div>
                    </div>
                </div>
                <div class="arms_spec_container">
                    <div class="innate_spec_container">
                        <div class="title">
                            兵种特性
                            <div class="chose_spec_btn" ref="chose_spec_btn"
                            @touchstart="$refs.chose_spec_btn.classList.add('click')"
                            @touchend="$refs.chose_spec_btn.classList.remove('click')"
                            v-if="$store.buffer.curWarrior.isUpArms"
                            @click="armSpecOptionVisible = true"
                            >切换特性</div>
                        </div>
                        <div class="spec" v-for="sp in $store.buffer.curWarrior.armsSpec[$store.buffer.curWarrior.choseUpArmsIdx]" :key="sp.index">
                            <img :src="ArmSpecIconImg(sp.type)" alt="" class="spec_img">
                            <div class="name">{{ sp.name }}</div>
                            <div class="describe">{{ sp.describe }}</div>
                        </div>
                        <div class="spec" v-for="optionSp,opSIndex in $store.buffer.curWarrior.armsCommonSpec[$store.buffer.curWarrior.choseUpArmsIdx]"
                        v-show="ShowOptionArmSpec(opSIndex)"
                        >
                            <img :src="ArmSpecIconImg(optionSp.type)" alt="" class="spec_img">
                            <div class="name">{{ optionSp.name }}</div>
                            <div class="describe">{{ optionSp.describe }}</div>
                        </div>
                        
                    </div>
                </div>
            </div>

            <!-- 属性板块 -->
            <div class="attr_panel" v-if="navListIdx == 2">
                <div class="line">
                    <div class="title">武将等级</div>
                    <div class="adjust_btn" ref="adjust_level_btn"
                    @touchstart="$refs.adjust_level_btn[b-1].classList.add('click')"
                    @touchend="$refs.adjust_level_btn[b-1].classList.remove('click')"
                    v-for="b in 4" :key="b"
                    @click="AdjuestWarriorLevel(b)">
                        <span v-if="b == 1">-10</span>
                        <span v-if="b == 2">-</span>
                        <span v-if="b == 3">+</span>
                        <span v-if="b == 4">+10</span>
                    </div>
                    <div class="level_value">Lv.{{ $store.buffer.curWarrior.level }}</div>
                </div>
                <div class="line" v-for="a,a_index in attrList" :key="a.index">
                    <div class="title">{{ a }}</div>
                    <div class="adjust_btn" ref="adjust_btn"
                    @touchstart="AdjustAttrPlusTouchStart($refs.adjust_btn[a_index*4+b-1].classList,b,a)"
                    @touchend="AdjustAttrPlusTouchEnd($refs.adjust_btn[a_index*4+b-1].classList)"
                    v-for="b in 4" :key="b" 
                    v-show="a != '攻城'"
                    @click="AdjustAttrPlus(b,a)">
                        <span v-if="b == 1">空</span>
                        <span v-if="b == 2">-</span>
                        <span v-if="b == 3">+</span>
                        <span v-if="b == 4">满</span>
                    </div>
                    <div class="count_attr">
                        <span>{{CountAttr(a,$store.buffer.curWarrior.level)}}</span>
                        <span class="plus_number"> {{ `(+${$store.buffer.curWarrior.plus[attrMap[a]] || 0})` }}</span>
                        <div class="up_number">{{ `&nbsp&nbsp(+${GetWarriorAttr(a,false)})` }}</div>
                        
                    </div>
                </div>

                <div class="line">
                    <div class="title">剩余加点</div>
                    <div class="count_attr">
                        <span style="color: rgb(255, 186, 76);"> {{ CountAttrPlus }}</span>
                    </div>
                </div>
            </div>

            <!-- 画像板块 -->
            <div class="skin_chose_panel" v-if="navListIdx == 3">
                <div class="skin_container">
                    <li v-for="(s,index) in $store.buffer.curWarrior.skin" :key="s.index" @click="ChoseSkin(index)">
                        <img :src="require(`../../static/card_0/${s}`)" class="cover" alt="">
                        <div class="skin_chose_frame" v-if="$store.buffer.curWarrior.skinChoseIdx == index"></div>
                        <div class="text" v-if="$store.buffer.curWarrior.skinChoseIdx == index">使用中</div>
                    </li>
                </div>
            </div>

            <!-- 解锁按钮 -->
            <div class="lock_btn" ref="lock_btn" 
            @touchstart="$refs.lock_btn.classList.add('click')"
            @touchend="$refs.lock_btn.classList.remove('click')"  
            v-if="ShowRule('lock_btn') && !$store.buffer.curWarrior.own" @click="Lock(false)">
                解锁武将
            </div>
            <!-- 上锁按钮 -->
            <div class="unlock_btn" ref="unlock_btn" @touchstart="$refs.unlock_btn.classList.add('click')"
            @touchend="$refs.unlock_btn.classList.remove('click')"   v-if="ShowRule('lock_btn') && $store.buffer.curWarrior.own" @click="Lock(true)"></div>

            <div class="battle_unload_btn" ref="battle_unload_btn"
            @touchstart="$refs.battle_unload_btn.classList.add('click')"
            @touchend="$refs.battle_unload_btn.classList.remove('click')"  
            v-if="ShowRule('battle_unload_btn')"
            @click="BattleUnloadWarrior()">卸载武将</div>
        </div>
        <TopNav title="武将详情" />

        <div class="arm_spec_optional_panel" v-if="armSpecOptionVisible">
            <div class="mask cover" @click="armSpecOptionVisible = false"></div>
            <div class="main_container">
                <TopicTitle title="兵种特性" :closeFunc="CloseArmSpecSelect" />
                <div class="arm_spec_container">
                    <div class="spec_box" ref="spec_box"
                    v-for="sp,spIndex in $store.buffer.curWarrior.armsCommonSpec[$store.buffer.curWarrior.choseUpArmsIdx]" :key="sp.index"
                    @click="ChoseOptionalSpec(spIndex)">
                        <div class="icon">
                            <img :src="ArmSpecIconImg(sp.type)" alt="" class="cover">
                        </div>
                        <div class="name">{{ sp.name }}</div>
                        <div class="describe">{{ sp.describe }}</div>
                        <div class="current_mask" v-show="ShowOptionArmSpec(spIndex)"></div>
                    </div>
                </div>
            </div>
        </div>
        <!-- 路由 -->
        <router-view/>
    </div>
</template>

<script>
import TopNav from '@/components/container/TopNav.vue';
import TacticsIcon from '../../components/style/TacticsIcon.vue'
import ArtifactCard from '@/components/style/ArtifactCard.vue';
import TopicTitle from '@/components/style/TopicTitle.vue';
export default {
    name : 'WarriorInfo',
    components:{
        TopNav,
        TopicTitle,
        TacticsIcon,
        ArtifactCard
    },
    data(){
        return{
            navList : [
                "详 情",
                "进 阶",
                "属 性",
                "画 像"
            ],
            attrList : [
                "攻击","防御","策略","速度","攻城"
            ],
            attrMap:{
                "攻击":"ad",
                "防御":"de",
                "策略":"tact",
                "速度":"speed",
                "攻城":"demolition",
            },
            //计时器
            intervalTimer : null,
            touchTimer : null,
            //根路由
            routeArr : [],
            navListIdx : 0, 
            armSpecOptionVisible : false,
        }
    },
    computed:{
        
        //兵种图标
        ArmsImageUrl(){
            if(this.$store.buffer.curWarrior.arms == '弓'){
                if(this.$store.buffer.curWarrior.isUpArms) return require('../../assets/ui/ui (63).png');
                else return require('../../assets/ui/ui (62).png');
            }
            else if(this.$store.buffer.curWarrior.arms == '步'){
                if(this.$store.buffer.curWarrior.isUpArms) return require('../../assets/ui/ui (80).png');
                else return require('../../assets/ui/ui (79).png');
            }
            else {
                if(this.$store.buffer.curWarrior.isUpArms) return require('../../assets/ui/ui (71).png');
                else return require('../../assets/ui/ui (70).png');
            }
        },
        //属性评分
        AttrRate(){
            let score = 0;
            score += 0.25 * Math.floor(this.$store.buffer.curWarrior.attr.ad + 49 *  this.$store.buffer.curWarrior.grow.ad);
            score += 0.25 * Math.floor(this.$store.buffer.curWarrior.attr.de + 49 *  this.$store.buffer.curWarrior.grow.de);
            score += 0.25 * Math.floor(this.$store.buffer.curWarrior.attr.tact + 49 *  this.$store.buffer.curWarrior.grow.tact);
            score += 0.12 * Math.floor(this.$store.buffer.curWarrior.attr.speed + 49 *  this.$store.buffer.curWarrior.grow.speed);
            score += 0.1 * Math.floor(this.$store.buffer.curWarrior.attr.demolition + 49 *  this.$store.buffer.curWarrior.grow.demolition);
            if(this.$store.buffer.curWarrior.gender == '女') score += 15;
            return Math.floor(score);
        },
        //计算剩余加点
        CountAttrPlus(){
            let sum = 10
            if(this.$store.buffer.curWarrior.gender == '女') sum = 15
            sum = sum * Math.floor(this.$store.buffer.curWarrior.level/10)
            sum += this.$store.buffer.curWarrior.red * 10
            for(let attr in this.attrMap){
                sum -= this.$store.buffer.curWarrior.plus[this.attrMap[attr]] || 0
            }
            return sum;
        },
    },
    methods:{
        //关闭兵种特性选择
        CloseArmSpecSelect(){
            this.armSpecOptionVisible = false;
        },
        //安装宝物
        LoadArtifact(){
            if(this.$store.buffer.curWarrior.artifact){
                this.$store.buffer.curArtifact = this.$store.buffer.curWarrior.artifact
                this.$router.push({
                    path : `${this.$route.path}/artifact_info`,
                })
            }
            else{
                this.$router.push({
                    path : `${this.$route.path}/artifact_list`,
                })
            }
            
        },
        //切换导航
        SwitchNav(index){    
            if(!this.$store.buffer.curWarrior.own && index && this.routeArr[0] != 'battle' && (index == 1 || index == 2)){
                this.$store.Alert('请先解锁武将！')
                return
            }
            this.navListIdx = index
        },
        //兵种
        ArmIconClassName(index){
            let res = "arms_icon "
            if(index != this.$store.buffer.curWarrior.choseUpArmsIdx) res += 'filter_gray'
            return res
        },
        //组件显示规则
        ShowRule(component_name){
            if(component_name == 'lock_btn' && this.routeArr[this.routeArr.length-2] == 'my_warriors'){
                if(this.navListIdx == 0){
                    return true;
                }
            }
            if(component_name == 'adjust_panel' && this.navListIdx == 1){
                if(this.routeArr[0] == 'battle') return true
                if(this.$store.buffer.curWarrior.own){
                    return true;
                }
            }
            if(component_name == 'battle_unload_btn' && this.routeArr[0] == 'battle'){
                return true;
            }
            return false
        },
        //是否展示可选兵种特性
        ShowOptionArmSpec(index){
            if(this.$store.buffer.curWarrior.isUpArms && 
            (index == this.$store.buffer.curWarrior.choseArmsCharacterIdx[0] || index == this.$store.buffer.curWarrior.choseArmsCharacterIdx[1])) return true
            return false
        },
        //高级兵种图标
        UpArmsIconImg(warrior,str){
            return require(`../../assets/${warrior.isUpArms?"up_arms":"arms"}/${this.$store.armsMap[str]}.png`)
        },
        //高级兵种特性图标
        ArmSpecIconImg(type){
            switch(type){
                case "步" : return require('../../assets/ui/ui (124).png')
                case "骑" : return require('../../assets/ui/ui (122).png')
                case "弓" : return require('../../assets/ui/ui (123).png')
            }
        },
        //改变画像选择
        ChoseSkin(skinIdx){
            if(!this.$store.buffer.curWarrior.own && this.routeArr[0] != 'battle'){
                this.$store.Alert('请先解锁武将！')
                return;
            }
            else if(this.$store.buffer.curWarrior.skinChoseIdx == skinIdx) return;
            this.$store.buffer.curWarrior.ChangePortrayal(skinIdx);
            if(this.routeArr[0] != 'battle') this.$store.dirty.warrior = true; //脏位
            this.$store.Alert('画像切换成功')
        },
        //计算武将的满级属性
        CountAttr(type,level){
            let sum = this.$store.buffer.curWarrior.attr[this.attrMap[type]] + (level-1) * this.$store.buffer.curWarrior.grow[this.attrMap[type]]
            for(let attr in this.attrMap){
                if(type == attr){
                    sum += this.$store.buffer.curWarrior.plus[this.attrMap[attr]] || 0
                }
            }
            return Math.floor(sum)
        },
        //获得武将的属性
        GetWarriorAttr(type,origin){
            switch(type){
                case "攻击" : return origin ? this.$store.buffer.curWarrior.attr.ad : this.$store.buffer.curWarrior.grow.ad;
                case "防御" : return origin ? this.$store.buffer.curWarrior.attr.de : this.$store.buffer.curWarrior.grow.de;
                case "策略" : return origin ? this.$store.buffer.curWarrior.attr.tact : this.$store.buffer.curWarrior.grow.tact;
                case "速度" : return origin ? this.$store.buffer.curWarrior.attr.speed : this.$store.buffer.curWarrior.grow.speed;
                case "攻城" : return origin ? this.$store.buffer.curWarrior.attr.demolition : this.$store.buffer.curWarrior.grow.demolition;
            }
        },
        //属性图标
        AttrIconImg(attrType){
            switch(attrType){
                case "攻击" : return require('../../assets/ui/ui (152).png');
                case "防御" : return require('../../assets/ui/ui (153).png');
                case "策略" : return require('../../assets/ui/ui (155).png');
                case "速度" : return require('../../assets/ui/ui (154).png');
                case "攻城" : return require('../../assets/ui/ui (179).png');
            }
        },
        //战法类型图标
        TacticsImg(tacticsType){
            switch(tacticsType){
                case "command" : return require('../../assets/ui/ui (92).png');
                case "active" : return require('../../assets/ui/ui (93).png');
                case "combo" : return require('../../assets/ui/ui (94).png');
                case "passive" : return require('../../assets/ui/ui (91).png');
            }
        },
        //觉醒图标
        UpImgUrl(){
            if(this.$store.buffer.curWarrior.isUp && this.$store.buffer.curWarrior.isUpWork) return require('../../assets/ui/ui (73).png');
            else if(this.$store.buffer.curWarrior.isUp) return require('../../assets/ui/ui (65).png');
            else return require('../../assets/ui/ui (84).png');
        },
        //标记图标
        SignImgUrl(){
            switch(this.$store.buffer.curWarrior.sign){
                case 's2' : return require('../../assets/ui/ui (66).png');
                case 's3' : return require('../../assets/ui/ui (67).png');
                case 'sp' : return require('../../assets/ui/ui (68).png');
                case 'xp' : return require('../../assets/ui/ui (69).png');
            }
        },
        //阵营图标
        CampImgUrl(camp){
            switch(camp){
                case "汉" : return require('../../assets/ui/ui (159).png');
                case "群" : return require('../../assets/ui/ui (157).png');
                case "蜀" : return require('../../assets/ui/ui (160).png');
                case "吴" : return require('../../assets/ui/ui (161).png');
                case "魏" : return require('../../assets/ui/ui (158).png');
                case "晋" : return require('../../assets/ui/ui (162).png');
            }
        },
        //调整红星
        AdjustStar(addModel){
            if(addModel && this.$store.buffer.curWarrior.red < 5) this.$store.buffer.curWarrior.red += 1;
            else if(!addModel && this.$store.buffer.curWarrior.red > 0){
                if(this.CountAttrPlus < 10){
                    for(let key in this.$store.buffer.curWarrior.plus){
                        this.$store.buffer.curWarrior.plus[key] = 0;
                    }
                } 
                this.$store.buffer.curWarrior.red -= 1;
            }

            this.$store.dirty.warrior = true; //脏位
        },
        //调整武将兵种选择
        ExchangeArmsIndex(index){
            this.$store.buffer.curWarrior.ExchangeArms(index)
            this.$store.dirty.warrior = true
        },
        //调整加点按下
        AdjustAttrPlusTouchStart(dom,typeId,attr){
            dom.add('click')
            clearTimeout(this.intervalTimer);
            this.intervalTimer = setTimeout(() => {
                this.touchTimer = setInterval(() => {
                    this.AdjustAttrPlus(typeId,attr);
                }, 50);
            }, 200);
        },
        //调整加点松开
        AdjustAttrPlusTouchEnd(dom){
            dom.remove('click')
            clearTimeout(this.intervalTimer);
            clearInterval(this.touchTimer);
        },
        //调整加点
        AdjustAttrPlus(typeId,attr){
            if(typeId == 1){
                this.$store.buffer.curWarrior.plus[this.attrMap[attr]] = 0
            }
            else if(typeId == 2 && this.$store.buffer.curWarrior.plus[this.attrMap[attr]] > 0){
                this.$store.buffer.curWarrior.plus[this.attrMap[attr]] -= 1
            }
            else if(typeId == 3 && this.CountAttrPlus > 0){
                this.$store.buffer.curWarrior.plus[this.attrMap[attr]] += 1
            }
            else if(typeId == 4 && this.CountAttrPlus > 0){
                let value = this.CountAttrPlus
                this.$store.buffer.curWarrior.plus[this.attrMap[attr]] += value
            }
        },
        //调整武将等级
        AdjuestWarriorLevel(type){
            switch(type){
                case 1 : this.$store.buffer.curWarrior.level -= 10;break
                case 2 : 
                    if(this.CountAttrPlus < 10 && this.$store.buffer.curWarrior.level%10 == 0 && this.$store.buffer.curWarrior.level > 1){
                        for(let key in this.$store.buffer.curWarrior.plus){
                            this.$store.buffer.curWarrior.plus[key] = 0;
                        }
                    } 
                    this.$store.buffer.curWarrior.level -= 1;break
                case 3 : this.$store.buffer.curWarrior.level += 1;break
                case 4 : this.$store.buffer.curWarrior.level += 10;break
            }
            if(this.$store.buffer.curWarrior.level < 1){
                this.$store.buffer.curWarrior.level = 1
            }
            if(this.$store.buffer.curWarrior.level > 50){
                this.$store.buffer.curWarrior.level = 50
            }
            
            
        },
        //选择武将高级兵种特性
        ChoseOptionalSpec(index){
            for(let i in this.$store.buffer.curWarrior.choseArmsCharacterIdx){
                if(this.$store.buffer.curWarrior.choseArmsCharacterIdx[i] == index) return false
            }
            this.$store.buffer.curWarrior.choseArmsCharacterIdx.shift();
            this.$store.buffer.curWarrior.choseArmsCharacterIdx.push(index);
            this.$store.dirty.warrior = true
        },
        //战法调整
        TacticsConfig(t,index){
            this.$store.buffer.curTactics = t
            this.$store.buffer.editTactIdx = index
            //调整战法详情界面的设置
            let model = "show"
            if(index != 0 && t.id == -1) model = "load"
            else if(index != 0 && t.id != -1) model = "unload"
            // 表示卸载战法
            if(t.id != -1){
                this.$router.push({
                    path : `${this.$route.path}/tactics_info`,
                    query : {
                        model : model,
                    }
                })
            }
            // 表示安装战法
            else if(t.id == -1){
                this.$router.push({
                    path : `${this.$route.path}/tactics_list`,
                })
            }
        },
        //上锁解锁武将
        Lock(value){
            this.$store.dirty.warrior = true; //脏位
            //type为真表示上锁，反之表示解锁
            this.$store.buffer.curWarrior.own = !value;
            if(value){
                this.$store.buffer.curWarrior.isUp = false;
                this.$store.buffer.curWarrior.isUpWork = false;
                this.$store.buffer.curWarrior.isUpArms = false;
                this.$store.buffer.curWarrior.red = 0;
                //卸载战法
                for(let i=0;i<this.$store.buffer.curWarrior.tacticsArr.length;i++){
                    if(i > 0 && this.$store.buffer.curWarrior.tacticsArr[i].id != -1){
                        this.$store.buffer.curWarrior.UnLoadTactics(i,this.$store.buffer.curWarrior.tacticsArr[i])
                        this.$store.dirty.tactics = true;
                    }
                }
                //卸载宝物
                if(this.$store.buffer.curWarrior.artifact) this.$store.buffer.curWarrior.UnloadArtifact();
                this.Close();
            }
        },
        //对局模式卸载武将按钮
        BattleUnloadWarrior(){
            this.$store.battle.teams.UnloadWarrior(this.$store.buffer.editTeamIdx,this.$store.buffer.editTeamWarriorIdx)
            this.Close()
        },
        //武将觉醒，深造，进阶
        UpWarriorOpera(type,status){
            if(type == 'up') this.$store.buffer.curWarrior.isUp = status;
            else if(type == 'upWork') this.$store.buffer.curWarrior.isUpWork = status ;
            else  this.$store.buffer.curWarrior.isUpArms = status ;

            this.$store.dirty.warrior = true; //脏位
        },
        Close(){
            this.$router.back()
        }
        
    },
    mounted(){
        let path = this.$route.path
        this.routeArr = path.split("/")
        this.routeArr.shift()
        if(this.routeArr[0] == 'battle') this.navListIdx = 1
        this.$store.AutoStyle(this.$refs.warrior_info)
    }

}
</script>

