
class TeamSetting{
    constructor(){
        //兵营等级
        this.barracksLevel = 20;
        //神像等级
        this.giantWarriorStatueLevel = 0;
        //沙盘
        this.tacticsFormationLevel = 0;
        //是否进阶兵力
        this.isUpHp = false;
        //四大营
        this.fourMajorCampsLevel={
            jifen:10,
            shangwu:10,
            tiebi:10,
            junji:10,
        }
        //士气
        this.defMorale = 100;
    }
    ReturnDef(){
        //兵营等级
        this.barracksLevel = 20;
        //神像等级
        this.giantWarriorStatueLevel = 0;
        //沙盘
        this.tacticsFormationLevel = 0;
        //是否进阶兵力
        this.isUpHp = false;
        //四大营
        this.fourMajorCampsLevel={
            jifen:10,
            shangwu:10,
            tiebi:10,
            junji:10,
        }
        //士气
        this.defMorale = 100;
    }
    CopyData(data){
        const deepAssign = (target, source) => {
            for (const key in source) {
                if (typeof source[key] === 'object' && source[key] !== null) {
                    if (!target[key]) target[key] = Array.isArray(source[key]) ? [] : {};
                    deepAssign(target[key], source[key]);
                } else {
                    target[key] = source[key];
                }
            }
            return target;
        };
        // 1. 先清空当前实例（可选，根据需求）
        Object.keys(this).forEach(key => delete this[key]);

        // 2. 深合并数据
        deepAssign(this, data);
    }
}



export class BattleSetting{
    constructor(){
        this.teamSettingList = [];
        //武将默认等级
        this.warriorDefLevel = 50;
    }
    Init(){
        this.teamSettingList = [];
        for(let i=0;i<2;i++){
            let ts = new TeamSetting();
            this.teamSettingList.push(ts);
        }
    }
    CopyData(data){
        const deepAssign = (target, source) => {
            for (const key in source) {
                if (typeof source[key] === 'object' && source[key] !== null) {
                    if (!target[key]) target[key] = Array.isArray(source[key]) ? [] : {};
                    deepAssign(target[key], source[key]);
                } else {
                    target[key] = source[key];
                }
            }
            return target;
        };
        // 1. 先清空当前实例（可选，根据需求）
        Object.keys(this).forEach(key => delete this[key]);

        // 2. 深合并数据
        deepAssign(this, data);
    }
    //恢复默认设置
    ReturnDef(){
        //武将默认等级
        this.warriorDefLevel = 50;
        for(let i in this.teamSettingList){
            this.teamSettingList[i].ReturnDef();
        }
        return true;
    }
}