<template>
    <div class="tactics_info" ref="tactics_info">
        <div class="mask cover" @click="Close"></div>
        <div class="tactics_info_container">
            <TopicTitle title="战法详情" :closeFunc="Close" />
            <div class="tactics_info_container_panel">
                <div class="tactics_info_container_panel_t_icon">
                    <TacticsIcon :tacticsData="$store.buffer.curTactics" />
                </div>
                <div class="tactics_info_container_panel_load_warrior_link" v-if="$store.buffer.curTactics.isload != -1" 
                @click="JumpToWarriorInfo()">
                    已学习
                </div>
                <div class="tactics_info_container_panel_load_warrior_link" v-if="$store.buffer.curTactics.isload != -1" 
                @click="JumpToWarriorInfo()">
                    {{ GetWarriorName($store.buffer.curTactics.isload) }}
                </div>


                
                <div class="tactics_info_container_panel_tips1">发动几率 : {{ $store.buffer.curTactics.odds }}% &nbsp;&nbsp;|&nbsp;&nbsp; 范围 : {{ $store.buffer.curTactics.range }}</div>
                <div class="tactics_info_container_panel_tips2">适用兵种：{{ $store.buffer.curTactics.arms }}</div>
                <div class="tactics_info_container_panel_tips3">目标类型：{{ TransforTargetType($store.buffer.curTactics.tgtNumber) }}</div>
                <div class="tactics_info_container_panel_describe">{{$store.buffer.curTactics.describe[0]}}</div>
            </div>
            
            <div class="tactics_info_load_bth" ref="tactics_info_load_bth"  
            @touchstart="$refs.tactics_info_load_bth.classList.add('click')"
            @touchend="$refs.tactics_info_load_bth.classList.remove('click')"  
            @click="LoadTactics()" v-if="ShowBtnRule('load')">
            安装战法
            </div>
            
            <div class="tactics_info_upload_bth" ref="tactics_info_upload_bth"  
            @touchstart="$refs.tactics_info_upload_bth.classList.add('click')"
            @touchend="$refs.tactics_info_upload_bth.classList.remove('click')"  
            @click="UpLoadTactics()" v-if="ShowBtnRule('unload')">
            卸下战法
            </div>
        </div>
    </div>
</template>

<script>
import TopicTitle from '@/components/style/TopicTitle.vue'
import TacticsIcon from '@/components/style/TacticsIcon.vue'
export default {
    name : 'TactInfo',
    components:{
        TopicTitle,
        TacticsIcon
    },
    data(){
        return{
            model : this.$route.query.model? this.$route.query.model : 'show', //'show' ,'load', 'unload'
            routeArr : [],
        }
    },
    computed:{
        
    },
    methods:{
        ShowBtnRule(btnType){
            if(btnType == 'load' && this.model == 'load'){
                if(this.routeArr[0] == 'battle') return true
                if(this.$store.buffer.curTactics.isload == -1) return true;
                return false
            }
            if(btnType == 'unload' && this.model == 'unload'){
                if(this.routeArr[0] == 'battle') return true
                if(this.$store.buffer.curTactics.isload != -1) return true;
                return false
            }
        },
        GetWarriorName(wId){
            if(wId == -1) return
            let str = `【${this.$store.warriors[this.$store.warriorIdxTable[wId]].camp}-
            ${this.$store.warriors[this.$store.warriorIdxTable[wId]].name}-
            ${this.$store.warriors[this.$store.warriorIdxTable[wId]].arms}】`
            return str;
        },
        TransforTargetType(ipt){
            let str = ""
            if(ipt.length == 1){
                switch(ipt[0]){
                    case 0: str = "自己";break;
                    case 1: str = "单体目标";break;
                    case 2: str = "群体2目标";break;
                    case 3: str = "全体目标";break;
                }
            }
            else if(ipt.length > 1){
                str = `群体${ipt[0]}-${ipt[1]}目标`
            }
            return str
        },
        //跳转至武将详情界面
        JumpToWarriorInfo(){
            this.$store.buffer.curWarrior = this.$store.warriors[this.$store.warriorIdxTable[this.$store.buffer.curTactics.isload]]
            // 获取上一个路由
            let backTime = 0
            
            for(let i=this.routeArr.length-1;i>0;i--){
                if(this.routeArr[i] == 'tactics_info' || this.routeArr[i] == 'tactics_list' || this.routeArr[i] == 'warrior_info'){
                    backTime++;
                }
                if(this.routeArr[i] == 'warrior_info') break;
            }
            this.$router.go(-backTime)
            setTimeout(() => {
                this.$router.push({
                    path: `${this.$route.path}/warrior_info`,
                });
            }, 10);
        },
        //安装战法
        LoadTactics(){
            if(this.routeArr[0] != 'battle'){
                this.$store.buffer.curWarrior.LoadTactics(this.$store.buffer.editTactIdx,this.$store.buffer.curTactics)
                this.$store.dirty.warrior = true
                this.$store.dirty.tactics = true
            }
            else{
                this.$store.buffer.curWarrior.LoadNativeTactics(this.$store.buffer.editTactIdx,this.$store.buffer.curTactics)
            }
            
            
            if(this.routeArr[this.routeArr.length-2] == 'tactics_list'){
                this.$router.go(-2)
            }
            else this.$router.back()
            
        },
        //卸载战法
        UpLoadTactics(){
            this.$store.buffer.curWarrior.UnLoadTactics(this.$store.buffer.editTactIdx,this.$store.buffer.curTactics)
            // this.$store.state.buffer.wait_upload_tactics.UnLoad(this.$store.tactics)
            if(this.routeArr[this.routeArr.length-2] != 'battle'){
                this.$store.dirty.warrior = true
                this.$store.dirty.tactics = true
            }
            this.Close()
        },  
        Close(){
            this.$router.back()
        },
        
    },
    mounted(){
        let path = this.$route.path
        this.routeArr = path.split("/")
        this.routeArr.shift()
        this.$store.AutoStyle(this.$refs.tactics_info)
    }
}
</script>

