import { defineStore } from 'pinia'
// import { ref } from 'vue'

export const store = defineStore('app', {
    // 状态：返回初始状态的函数
    state: () => ({
        //appId，也作为数据库的名称
        DATABASE_NAME: 'AssistingBorder App',
        //设备
        device : "phone",
        //应用数据
        App: null,
        //当前用户
        user: null,
        //高级兵种图片映射
        armsMap : {
            "禁卫":"b_j",
            "重步兵":"b_z",
            "长枪兵":"b_c",
            "藤甲兵":"b_t",
            "蛮兵":"b_m",
            "死士":'g_s',
            "弩兵":'g_n',
            "长弓兵":'g_c',
            "重骑兵":"q_z",
            "轻骑兵":"q_q",
            "铁骑兵":"q_t",
            "象兵":"q_x",
            "弓骑兵":"q_g",
        },
        //所有武将数据
        warriors: [],
        //所有武将的原始数据,即json文件中的数据形式
        warriorOriginDataList: [],
        //武将的索引表
        warriorIdxTable: {},
        //所有战法数据
        tactics: {
            command: [],
            active: [],
            combo: [],
            passive: [],
        },
        //该用户定义的宝物数据
        userArtifacts: [],
        //宝物数据
        artifacts: [],
        //所有已被开发战法的武将数据和战法，用于对局模块武将选择
        allWarriors:[],
        allTactics:{
            command: [],
            active: [],
            combo: [],
            passive: [],
        },
        //所有宝物，用于对局模块的宝物选择
        allArtifacts:[],
        //所有宝物的原始数据
        artifactOriginDataList: [],
        //配将队伍
        teams: [],
        //数据缓冲区
        buffer: {
            curWarrior: null,
            curTactics: null,
            curArtifact: null,
            curConfigArt:null,
            curTeam: null,
            curBattleTeam:null,

            editTactIdx: -1,
            editTeamIdx: -1,
            editTeamWarriorIdx: -1,

        },
        dirty: {
            warrior: false,
            tactics: false,

            reportHistory: false,
            battleLineup: false
        },
        //提示面板
        prompt: {
            title: "",
            type: '',
            msg: "",
            CallBack: function () { },//挂载函数
            status: false
        },
        //提示面板（双选择提示面板）
        promptOptional:{
            title: "",
            msg : "无",
            optionBtnNames:[],
            //挂载函数
            optionCallBack :[
            ],
            status:false
        },
        alertQueue: [],
        battleStorage: null,
        battle: {
            lineupList: [],//保存的阵容总表(区别battleStorage)
            visibleLineups : [],//用于展示的阵容表
            currentGroup:null,//当前选中的组
            lineupGroups:[],//阵容分组
            teams: [],
            setting:null,
            version: "默认",

            //对战结束后武将的结果
            battleResult: [
                [],
                []
            ],
            ReportHeap: [],//战报堆
            //结果统计
            Statistics: {
                allTurn: 0,
                //数据矩阵
                dataMatrices: [],
            },
            ReportContainer: [],//战报

        }
    }),

    // 计算属性（与 Vuex 的 getters 类似）
    getters: {
        doubleCount: (state) => state.count * 2
    },

    // 方法（可同步或异步，替代 Vuex 的 actions 和 mutations）
    actions: {
        OptionalPrompt(params){
            this.promptOptional.title = params.title || "提 示"
            this.promptOptional.msg = params.msg || '默认消息';
            this.promptOptional.optionBtnNames = params.optionBtnNames || [];
            this.promptOptional.optionCallBack = params.optionCallBack || [];
            this.promptOptional.status = true;
        },
        Prompt(params) {
            this.prompt.title = params.title || '提 示';
            this.prompt.msg = params.msg || '默认消息';
            this.prompt.type = params.type || 'info';
            this.prompt.status = true;
            this.prompt.CallBack = params.CallBack;
        },
        Alert(msg,duration=1500) {
            let alert_item = {
                id: Math.floor(Math.random() * 9999),
                msg: msg || "警告",
            }
            this.alertQueue.push(alert_item)
            setTimeout(() => {
                if (this.alertQueue.length > 0) {
                    this.alertQueue.shift();
                }
            }, duration)
        },
        AutoStyle(dom){
            dom.classList.add(this.device);
        },
        // WatchWarriors() {
        //     // 监听 warriors 变化
        //     watch(
        //         () => this.warriors,
        //         (newWarriors) => {
        //             console.log('武将数据更新:', newWarriors)
        //             // 执行其他逻辑（如保存到本地存储）
        //             // localStorage.setItem('warriors', JSON.stringify(newWarriors))
        //         },
        //         { deep: true } // 监听数组内部变化
        //     )
        // },
        async fetchData() {
            const response = await fetch('/api/data')
            this.count = await response.json()
        }
    }
})

// store.init()


