import Judge from "../js/judge";
import Query from "../js/query";
import Report from "../js/report";
//状态类型块
class StatusBlock{
    constructor(applicator,tactics,type,duration,waitTime){
        //来自谁
        this.applicator = applicator;
        //施加该效果的战法
        this.tactics = tactics;
        //状态类型
        this.type = type;
        //持续时间
        this.duration = duration;
        //是否是增益
        this.isBenefit = false;
        //等待时间
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        //被执行人
        this.beExecuted =  null;
        //target是被施加该块的对象
        switch(this.type){
            case "连击" : this.isBenefit = true; break;
            case "援护" : this.isBenefit = true; break;
            case "洞察" : this.isBenefit = true; break;
            case "先手" : this.isBenefit = true; break;
            case "无视兵种" : this.isBenefit = true; break;
            case "无视规避" : this.isBenefit = true; break;
            case "免疫怯战" : this.isBenefit = true; break;
            case "免疫暴走" : this.isBenefit = true; break;
            case "免疫混乱" : this.isBenefit = true; break;
            case "免疫围困" : this.isBenefit = true; break;
            case "不受敌方指挥战法影响" : this.isBenefit = true; break;
            case "不触发反击" : this.isBenefit = true; break;
        }
    }
    //初始化
    Init(target,needReport = true,reportLevel = 3){
        let j = new Judge();
        let q = new Query()
        if(this.type == "混乱" && j.IsGetStatus(target,'免疫混乱')
        || this.type == "暴走" && j.IsGetStatus(target,'免疫暴走')
        || this.type == "怯战" && j.IsGetStatus(target,'免疫怯战')
        || this.type == "围困" && j.IsGetStatus(target,'免疫围困')){
            let report = new Report();
            report.InitExecuteReport(3,target,`免疫${this.type}`);
            return false;
        }
        this.beExecuted = target;
        //检查抵御效果(指挥战法阶段不进行抵御)
        if(target.driver.turn >= 1){
            for(let i in target.STATUS_RESIST){
                if(target.STATUS_RESIST[i].resistType == this.type){
                    let rand = Math.floor(Math.random()*100);
                    let realOdd = q.GetMoraleOdd(target,target.STATUS_RESIST[i].odd)
                    if(rand < realOdd){
                        let r = new Report();
                        r.InitTwoTargetReport(3,target.STATUS_RESIST[i].applicator,target,`【${target.STATUS_RESIST[i].tactics.name}】令`,`抵御了${this.type}状态`);
                        return false;
                    }
    
                    else{
                        let r = new Report();
                        r.InitExecuteReport(3,target.STATUS_RESIST[i].applicator,`【${target.STATUS_RESIST[i].tactics.name}】的抵御效果没有生效。生效几率为${realOdd}%`);
                    }
                }
            }
        }
        //执行即将陷入特殊状态时执行块
        for(let i in target.BEFORE_FALL_INTO_STATUS){
            //获取数据信息
            target.BEFORE_FALL_INTO_STATUS[i].GetData(this.applicator,this.tactics,this.type,this.duration,this.isBenefit,this.waitTime,this);
            //执行
            target.BEFORE_FALL_INTO_STATUS[i].Execute();
        }
        //检查冲突
        for(let i in target.STATUS){
            //找到同类型效果，冲突
            if(target.STATUS[i].type == this.type && this.type != '无法普通攻击' && this.type != '无法施法'){
                let report = new Report();
                report.InitExecuteReport(3,target,`已存在${target.STATUS[i].type}效果`);

                return false;
            }
        }
        target.STATUS.push(this);
        if(needReport){
            let str = ``;
            if(this.type == '怯战' || this.type == '犹豫' || this.type == '混乱' || this.type == '暴走')str = `陷入${this.type}中`;
            else if(this.type == "围困")    str = `不可回复兵力`;
            else if(this.type == "无视兵种" || this.type == '无视规避' 
                || this.type == '免疫怯战' || this.type == '免疫犹豫' || this.type == '免疫暴走' || this.type == '免疫混乱' || this.type == '免疫围困' 
                || this.type == "连击")    str = `${this.type}效果已施加`;
            else if(this.type == "先手" || this.type == "洞察")    str = `获得${this.type}`;
            else if(this.type == '挑衅' || this.type == '援护') str = `受到${this.type}`
            else if(this.type == '不受指挥战法影响' || this.type == '不受敌方指挥战法影响') str = `${this.type}`;
            let report = new Report();
            report.InitExecuteReport(reportLevel,target,str);
        }
        //执行特殊状态时执行块
        for(let i in target.FALL_INTO_STATUS){
            //获取数据信息
            target.FALL_INTO_STATUS[i].GetData(this.applicator,this.tactics,this.type,this.duration,this.isBenefit,this.waitTime,this);
            //执行
            target.FALL_INTO_STATUS[i].Execute();
        }
        return true;
    }
    SelfDestroy(){
        let temp = []
        for(let i in this.beExecuted.STATUS){
            if(this.beExecuted.STATUS[i] != this) temp.push(this.beExecuted.STATUS[i]);
        }
        this.beExecuted.STATUS = temp;
    }
}

export default StatusBlock;