import Judge from "./judge";
import Query from "./query";
import Util from "./util";
import Report from "./report";
import Manage from "./manage";
//计算
class Count {
    constructor() {

    }

    //获取一个武将的属性
    GetAttr(target, attrType) {
        let res = 0;
        //基础属性
        switch (attrType) {
            case "攻击": res = target.attr.ad; break;
            case "谋略": res = target.attr.tact; break;
            case "防御": res = target.attr.de; break;
            case "速度": res = target.attr.speed; break;
        }
        //读取属性堆加区的块
        for (let i in target.ATTR) {
            if (target.ATTR[i].attrType == attrType) {
                res += target.ATTR[i].value;
            }
        }
        return res;
    }
    AttackDamageIndex(attacker, damageType, isCommonAttack, originTactics) {
        let res = 0;
        let count1 = [];
        //攻击方结算
        for (let i in attacker.DAMAGE_VAR) {
            if (attacker.DAMAGE_VAR[i].isMakeDamage && attacker.DAMAGE_VAR[i].waitTime <= 0) {
                //先检同一个一次性战法只可以被加载一次
                let unlock = true;
                if (attacker.DAMAGE_VAR[i].isOnceAttack) {
                    for (let j in count1) {
                        if (count1[j].tactics == attacker.DAMAGE_VAR[i].tactics) unlock = false;
                    }
                }
                if (unlock) {
                    //伤害来源于战法
                    if (originTactics != null) {
                        //指定战法增伤减伤
                        if (attacker.DAMAGE_VAR[i].tacticsItem != null && attacker.DAMAGE_VAR[i].tacticsItem == originTactics) {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                        //考虑常规攻击还是谋略加成，和主动还是追击战法加成
                        else if ((attacker.DAMAGE_VAR[i].damageType == damageType || attacker.DAMAGE_VAR[i].damageType == originTactics.type) && attacker.DAMAGE_VAR[i].tacticsItem == null) {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                        else if (attacker.DAMAGE_VAR[i].damageType == '所有') {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                    }
                    //伤害来源于普通攻击
                    else if (originTactics == null && isCommonAttack) {
                        if (attacker.DAMAGE_VAR[i].damageType == damageType || attacker.DAMAGE_VAR[i].damageType == '普通攻击') {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                        else if (attacker.DAMAGE_VAR[i].damageType == '所有') {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                    }
                    //伤害来源于反击或其他
                    else {
                        if (attacker.DAMAGE_VAR[i].damageType == damageType) {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                        else if (attacker.DAMAGE_VAR[i].damageType == '所有') {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                    }
                    if (attacker.DAMAGE_VAR[i].isOnceAttack) {
                        count1.push(attacker.DAMAGE_VAR[i]);
                    }
                }
            }
        }
        if (res < -90 && res >= - 666) res = 0.1;
        else if (res < -666) res = 0.09;//大幅减伤
        else res = 1 + res / 100;
        // let r = new Report();
        // r.InitExecuteReport(3,attacker,`${res}`);
        return res;
    }
    /**
     * 获取攻防双方的增减伤差值
     * @param {*} attacker 攻击者
     * @param {*} receiver 防御者
     * @param {*} damageType 伤害类型
     * @param {*} isCommonAttack 是否是普通攻击
     * @param {*} originTactics 伤害来源战法
     * @returns 
     */
    GetIndexOfDamageVar(attacker, receiver, damageType, isCommonAttack, originTactics) {
        let res = 0;
        let count1 = [];
        let count2 = [];
        //攻击方结算
        for (let i in attacker.DAMAGE_VAR) {
            if (attacker.DAMAGE_VAR[i].isMakeDamage && attacker.DAMAGE_VAR[i].waitTime <= 0) {
                //先检同一个一次性战法只可以被加载一次
                let unlock = true;
                if (attacker.DAMAGE_VAR[i].isOnceAttack) {
                    for (let j in count1) {
                        if (count1[j].tactics == attacker.DAMAGE_VAR[i].tactics) unlock = false;
                    }
                }
                if (unlock) {
                    //伤害来源于战法
                    if (originTactics != null) {
                        //指定战法增伤减伤
                        if (attacker.DAMAGE_VAR[i].tacticsItem != null && attacker.DAMAGE_VAR[i].tacticsItem == originTactics) {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                        //考虑常规攻击还是谋略加成，和主动还是追击战法加成
                        else if ((attacker.DAMAGE_VAR[i].damageType == damageType || attacker.DAMAGE_VAR[i].damageType == originTactics.type) && attacker.DAMAGE_VAR[i].tacticsItem == null) {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                        else if (attacker.DAMAGE_VAR[i].damageType == '所有') {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                    }
                    //伤害来源于普通攻击
                    else if (originTactics == null && isCommonAttack) {
                        if (attacker.DAMAGE_VAR[i].damageType == damageType || attacker.DAMAGE_VAR[i].damageType == '普通攻击') {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                        else if (attacker.DAMAGE_VAR[i].damageType == '所有') {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                    }
                    //伤害来源于反击或其他
                    else {
                        if (attacker.DAMAGE_VAR[i].damageType == damageType) {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                        else if (attacker.DAMAGE_VAR[i].damageType == '所有') {
                            res += attacker.DAMAGE_VAR[i].value;
                        }
                    }
                    if (attacker.DAMAGE_VAR[i].isOnceAttack) {
                        count1.push(attacker.DAMAGE_VAR[i]);
                    }
                }
            }
        }
        //防御方结算
        for (let i in receiver.DAMAGE_VAR) {
            if (!receiver.DAMAGE_VAR[i].isMakeDamage && receiver.DAMAGE_VAR[i].waitTime <= 0) {
                //先检同一个一次性战法只可以被加载一次
                let unlock = true;
                if (receiver.DAMAGE_VAR[i].isOnceAttack) {
                    for (let j in count2) {
                        if (count2[j].tactics == receiver.DAMAGE_VAR[i].tactics) unlock = false;
                    }
                }
                if (unlock) {
                    //伤害来源于战法
                    if (originTactics != null) {
                        //指定战法增伤减伤
                        if (receiver.DAMAGE_VAR[i].tacticsItem != null && receiver.DAMAGE_VAR[i].tacticsItem == originTactics) {

                            res += receiver.DAMAGE_VAR[i].value;
                        }
                        //考虑常规攻击还是谋略加成，和主动还是追击战法加成
                        if ((receiver.DAMAGE_VAR[i].damageType == damageType || receiver.DAMAGE_VAR[i].damageType == originTactics.type) && receiver.DAMAGE_VAR[i].tacticsItem == null) {
                            res += receiver.DAMAGE_VAR[i].value;
                        }
                        else if (receiver.DAMAGE_VAR[i].damageType == '所有') {
                            res += receiver.DAMAGE_VAR[i].value;
                        }
                    }
                    //伤害来源于普通攻击
                    else if (originTactics == null && isCommonAttack) {
                        if (receiver.DAMAGE_VAR[i].damageType == damageType || receiver.DAMAGE_VAR[i].damageType == '普通攻击') {
                            res += receiver.DAMAGE_VAR[i].value;
                        }
                        else if (receiver.DAMAGE_VAR[i].damageType == '所有') {
                            res += receiver.DAMAGE_VAR[i].value;
                        }
                    }
                    //伤害来源于反击或其他
                    else {
                        if (receiver.DAMAGE_VAR[i].damageType == damageType) {
                            res += receiver.DAMAGE_VAR[i].value;
                        }
                        else if (receiver.DAMAGE_VAR[i].damageType == '所有') {
                            res += receiver.DAMAGE_VAR[i].value;
                        }
                    }
                    if (receiver.DAMAGE_VAR[i].isOnceAttack) {
                        count2.push(receiver.DAMAGE_VAR[i]);
                    }
                }

            }
        }
        if (res < -90 && res >= - 666) res = 0.1;
        else if (res < -666) res = 0.09;//大幅减伤
        else res = 1 + res / 100;
        // let r = new Report();
        // r.InitExecuteReport(3,attacker,`${res}`);
        return res;
    }
    GetIndexOfStatusDamageVar(attacker,receiver,statusDamageType,originTactics){
        let res = 0;
        for(let i in attacker.STATUS_DAMAGE_VAR){
            if(attacker.STATUS_DAMAGE_VAR[i].isMakeDamage && attacker.STATUS_DAMAGE_VAR[i].statusType == statusDamageType){
                if(attacker.STATUS_DAMAGE_VAR[i].workTactics){
                    if(originTactics == attacker.STATUS_DAMAGE_VAR[i].workTactics) res += attacker.STATUS_DAMAGE_VAR[i].value;
                }
                else res += attacker.STATUS_DAMAGE_VAR[i].value;
            }
        }
        for(let i in receiver.STATUS_DAMAGE_VAR){
            if(!receiver.STATUS_DAMAGE_VAR[i].isMakeDamage && receiver.STATUS_DAMAGE_VAR[i].statusType == statusDamageType){
                if(receiver.STATUS_DAMAGE_VAR[i].workTactics){
                    if(originTactics == receiver.STATUS_DAMAGE_VAR[i].workTactics) res += receiver.STATUS_DAMAGE_VAR[i].value;
                }
                else res += receiver.STATUS_DAMAGE_VAR[i].value;
            }
        }
        if (res < -90) res = -90;
        return res / 100;
    }
    GetIndexOfCureVar(initiator, receiver, originTactics) {
        let res = 0;
        for (let i in initiator.CURE_VAR) {
            if (initiator.CURE_VAR[i].isOutput){
                if(initiator.CURE_VAR[i].workTactics){
                    if(originTactics == initiator.CURE_VAR[i].workTactics) res += initiator.CURE_VAR[i].value;
                } 
                else res += initiator.CURE_VAR[i].value;
            }
        }
        for (let i in receiver.CURE_VAR) {
            if (!receiver.CURE_VAR[i].isOutput) res += receiver.CURE_VAR[i].value;
        }
        if (res < -90) res = -90;
        return 1 + res / 100;
    }
    //获取最终伤害或治疗量的调整
    GetFinalDamageOrCureValue(attacker, receiver, isDamageOrCure, value) {
        if (isDamageOrCure != '伤害' && isDamageOrCure != '治疗') {
            console.log('this.isDamageOrCure字段出错', isDamageOrCure);
            return;
        }
        let res = value;
        let ratio = 0;
        let fix = 0;
        for (let i in attacker.DISCOUNT) {
            if (attacker.DISCOUNT[i].isDamageOrCure == isDamageOrCure && attacker.DISCOUNT[i].output && attacker.DISCOUNT[i].isRatio) {
                ratio += attacker.DISCOUNT[i].value;
            }
            else if (attacker.DISCOUNT[i].isDamageOrCure == isDamageOrCure && attacker.DISCOUNT[i].output && !attacker.DISCOUNT[i].isRatio) {
                fix += attacker.DISCOUNT[i].value;
            }
        }
        for (let i in receiver.DISCOUNT) {
            if (receiver.DISCOUNT[i].isDamageOrCure == isDamageOrCure && !receiver.DISCOUNT[i].output && receiver.DISCOUNT[i].isRatio) {
                ratio += receiver.DISCOUNT[i].value;
            }
            else if (receiver.DISCOUNT[i].isDamageOrCure == isDamageOrCure && !receiver.DISCOUNT[i].output && !receiver.DISCOUNT[i].isRatio) {
                fix += receiver.DISCOUNT[i].value;
            }
        }
        res += fix;
        if (ratio < -90) ratio = -90;
        res = Math.floor(res * (1 + ratio / 100));
        return res < 0 ? 0 : res;
    }



    
    //计算伤害
    CountDamage(attacker, receiver, value, damageType = "攻击", isCommonAttack = true, originTactics = null, statusDamageType = null) {
        //originTactics表示伤害来源的战法
        //statusDamageType表示状态类型伤害
        let j = new Judge();
        let u = new Util();
        let m = new Manage();
        let q = new Query();
        let isCommonAtk = isCommonAttack;
        if (originTactics) {
            isCommonAtk = false;
        }
        //即将造成伤害之前调用执行函数
        for (let i in attacker.BEFORE_MAKE_DAMAGE) {
            if (attacker.BEFORE_MAKE_DAMAGE[i].isMakeDamage) {
                attacker.BEFORE_MAKE_DAMAGE[i].GetDamageData(damageType, isCommonAtk, originTactics, statusDamageType);
                attacker.BEFORE_MAKE_DAMAGE[i].Execute(attacker, receiver);
            }
        }
        //即将受到伤害之间调用执行函数
        for (let i in receiver.BEFORE_MAKE_DAMAGE) {
            if (!receiver.BEFORE_MAKE_DAMAGE[i].isMakeDamage) {
                receiver.BEFORE_MAKE_DAMAGE[i].GetDamageData(damageType, isCommonAtk, originTactics, statusDamageType);
                receiver.BEFORE_MAKE_DAMAGE[i].Execute(attacker, receiver);
            }
        }

        //基础权值
        let damage_num = 0;
        let atkAd = this.GetAttr(attacker, "攻击");
        let recDe = this.GetAttr(receiver, "防御");
        let atkTact = this.GetAttr(attacker, "谋略");
        let recTact = this.GetAttr(receiver, "谋略");


        //攻防差
        let indexAd = atkAd - recDe * (1 - q.GetIgnoreValue(attacker, '防御'));
        if (indexAd > 0) indexAd = (3 - 500 / (250 + indexAd));
        else indexAd = (100 / (100 - indexAd));
        //谋略差
        let indexTact = recTact * (1 - q.GetIgnoreValue(attacker, '谋略'));
        //增减伤数值
        let varValue = this.GetIndexOfDamageVar(attacker, receiver, damageType, isCommonAtk, originTactics);
        let statusDmgVarValue = 0;
        if(statusDamageType != null){
            statusDmgVarValue = this.GetIndexOfStatusDamageVar(attacker,receiver,statusDamageType,originTactics);
        }
        if (indexTact < 50) indexTact = 1;
        else {
            indexTact = (1 - (75 - 9375 / (75 + indexTact)) / 100);
        }
        //兵力基础伤害
        let base = 0;
        if (damageType == '攻击') {
            if (statusDamageType == '动摇')
                base = 0.33 * (373 * attacker.hp) / (7700 + attacker.hp);
            else base = (373 * attacker.hp) / (7700 + attacker.hp);
        }
        else {
            if (statusDamageType == '燃烧' || statusDamageType == '妖术' || statusDamageType == '恐慌')
                base = 0.33 * (178 * attacker.hp) / (6459 + attacker.hp);
            else base = (178 * attacker.hp) / (6459 + attacker.hp);
        }
        //处于大幅降低情况下base伤害也降低
        if (varValue == 0.09) {
            base *= varValue;
        }
        //策略/攻击基础伤害
        let plus = 0;
        if (damageType == '攻击') {
            if (statusDamageType == '动摇') plus = atkAd * 0.4;
            else plus = atkAd * 0.33;
        }
        else {
            if (statusDamageType == '燃烧' || statusDamageType == '妖术' || statusDamageType == '恐慌') plus = atkTact * 0.25;
            else plus = atkTact * 0.5 * indexTact;
        }
        plus = plus * (varValue + statusDmgVarValue);
        //主要伤害
        let main = 0;
        if (damageType == '攻击') {
            main = (value / 100) * (300 * attacker.hp) / (3500 + attacker.hp) * indexAd;
        }
        else {
            main = (value / 100) * (300 * attacker.hp) / (3500 + attacker.hp) * indexTact;
        }
        main = main * (varValue + statusDmgVarValue);
        //总伤害
        damage_num = base + plus + main;
        //标准伤害，攻击力：250 伤害率120 base:210 plus:89 main:370 总669
        //标准策略伤害，谋略：250 伤害率120 base:108 。。。
        //兵种克制
        if (!j.IsGetStatus(attacker, "无视兵种")) {
            let atkArms = u.TransforArmForDamage(attacker.upArms[attacker.choseUpArmsIdx]);
            let recArms = u.TransforArmForDamage(receiver.upArms[receiver.choseUpArmsIdx]);
            //步兵打骑兵
            if (atkArms == 'b') {
                if (recArms == 'q') {
                    damage_num = damage_num * 0.7;
                }
            }
            //骑兵打弓兵
            else if (atkArms == 'q') {
                if (recArms == 'g') {
                    damage_num = damage_num * 0.7;
                }
            }
            //弓兵打步兵
            else if (atkArms == 'g') {
                if (recArms == 'b') {
                    damage_num = damage_num * 0.7;
                }
            }
        }
        if (damage_num > receiver.hp) {
            damage_num = receiver.hp
        }
        let isDodge = j.IsDodge(receiver);
        if (isDodge && !j.IsGetStatus(attacker, "无视规避")) {
            //先消耗掉概率最高的规避块
            let heighestOddDodge = receiver.DODGE[0]
            for(let i in receiver.DODGE){
                if(receiver.DODGE[i].odd > heighestOddDodge.odd){
                    heighestOddDodge = receiver.DODGE[i];
                }
            }
            let realOdd = q.GetMoraleOdd(receiver, heighestOddDodge.odd);
            let odd = Math.floor(Math.random() * 100);
            if (odd < realOdd) {
                let reportLevel = 3
                let r = new Report();
                r.InitExecuteReport(reportLevel, receiver, `处于规避状态`);
                damage_num = 0;
                m.ManageDodgeBlock(receiver,heighestOddDodge);
            }
            else {
                let reportLevel = 3
                let r = new Report();
                r.InitExecuteReport(reportLevel, receiver, `规避(预备)没有生效，生效几率为${realOdd}%`);
                m.ManageDodgeBlock(receiver,heighestOddDodge);
            }
        }
        else if (j.IsGetStatus(attacker, "无视规避") && isDodge) {
            let reportLevel = 0;
            if (isCommonAtk) reportLevel = 3;
            else reportLevel = 3;
            let r = new Report();
            r.InitExecuteReport(reportLevel, attacker, `无视规避，规避无效`);
        }
        damage_num = this.GetFinalDamageOrCureValue(attacker, receiver, '伤害', damage_num);
        //返回伤害
        if (damage_num < 0) damage_num = 0;
        return Math.floor(damage_num);

    }
    //计算治疗量
    CountCure(initiator, receiver, cureValue, cureIndex, tacticsItem = null,fixedCureValue=null) {
        //initiator发起该恢复效果的武将
        //receiver是接受恢复效果的武将
        let cure = 0;
        if(fixedCureValue){
            cure = fixedCureValue;
        }
        else{
            let q = new Query();
            let index = (q.GetAttr(initiator, "谋略") - 80) < 0 ? 0 : (q.GetAttr(initiator, "谋略") - 80);
            let base = cureValue + cureIndex * index;
            cure = (300 * receiver.hp) / (3500 + receiver.hp);
            cure *= (base / 100);
        }
        //获取治疗增减效果
        let cureVarIndex = this.GetIndexOfCureVar(initiator, receiver, tacticsItem);
        cure = cure * cureVarIndex;
        cure = this.GetFinalDamageOrCureValue(initiator, receiver, '治疗', cure);
        cure = Math.floor(Math.abs(cure));
        //伤兵和最大兵力上限控制
        if ((receiver.hp + cure) > receiver.hpMax) cure = receiver.hpMax - receiver.hp;
        if (cure > receiver.wound) cure = receiver.wound;
        return cure;
    }

}


export default Count;