<template>
    <div class="battle_result_panel">
        <div class="result_warrior_container" ref="result_warrior_container">
            <div class="report_image" ref="report_image">
                <div class="team_container" v-for="t,t_index in 2" :key="t.index">
                    <div class="troops_groove_left" v-if="t_index == 0">
                        <div class="wound_blue cover" ref="wound_blue"></div>
                        <div class="blood_blue cover" ref="blood_blue"></div>
                        <div class="troops_number_blue"> {{ $store.battle.battleResult[t_index][0].hp + $store.battle.battleResult[t_index][1].hp + $store.battle.battleResult[t_index][2].hp }}/
                            <span style="color: gray;">{{ TotalHp($store.battle.battleResult[t_index])}}</span>
                        </div>
                        
                    </div>
                    <div class="troops_groove_right" v-if="t_index == 1">
                        <div class="wound_red cover" ref="wound_red"></div>
                        <div class="blood_red cover" ref="blood_red"></div>
                        <div class="troops_number_red">{{ $store.battle.battleResult[t_index][0].hp + $store.battle.battleResult[t_index][1].hp + $store.battle.battleResult[t_index][2].hp }}/
                            <span style="color: gray;">{{TotalHp($store.battle.battleResult[t_index])}}</span>
                        </div>
                    </div>
                    <div class="warrior_card" v-for="(w,w_index) in $store.battle.battleResult[t_index]" :key="w.index">
                        <WarriorCard :warriorData="w" cardStyle="2_3" :filterGray="w.hp==0" :showUpArms="true"/>
                        <div class="artifact_box" v-if="w.artifact">
                            <ArtifactElCard :artifactData="w.artifact" />
                        </div>
                        <div class="warrior_position">
                            <span v-if="w_index == 0">大营</span>
                            <span v-if="w_index == 1">中军</span>
                            <span v-if="w_index == 2">前锋</span>
                        </div>
                    </div>
                    <div class="result_icon" ref="result_icon" v-if="t_index == 0">
                        <div class="bg_icon1"></div>
                        <div class="text_icon">
                            <img src="../../assets/ui/ui (119).png"  class="cover" v-if="teams[0][0].hp != 0 && teams[1][0].hp == 0">
                            <img src="../../assets/ui/ui (120).png"  class="cover" v-if="teams[0][0].hp != 0 && teams[1][0].hp != 0">
                            <img src="../../assets/ui/ui (131).png"  class="cover" v-if="teams[0][0].hp == 0 && teams[1][0].hp != 0">
                        </div>
                    </div>
                </div>
                <div class="statistics_btn" ref="statistics_btn" 
                @touchstart="$refs.statistics_btn.classList.add('click')"
                @touchend="$refs.statistics_btn.classList.remove('click')" 
                @click="OpenStatisticsPanel">统计</div>
                <div class="download_report_btn" ref="download_report_btn" 
                @touchstart="$refs.download_report_btn.classList.add('click')"
                @touchend="$refs.download_report_btn.classList.remove('click')" 
                @click="DownloadReport()">下载战报</div>
            </div>
            <div class="report_text" ref="report_text">
                <div class="report" ref="report" v-for="r in $store.battle.ReportHeap" v-show="ShowRule(r)">
                    <ReportItem :reportData="r" />
                </div>
            </div>
        </div>
        
        
        <!-- 战报显示控制 -->
        <div class="report_show_control" ref="report_show_control">
            <div class="show_begin_info" ref="show_begin_info"
            @touchstart="$refs.show_begin_info.classList.add('click')"
            @touchend="$refs.show_begin_info.classList.remove('click')"
            @click="InfoReportShow()"
            >
                <div :class="`txt ${reportShowRule['开局信息']?'current':''}`">{{ reportShowRule['开局信息']?"隐藏状态信息":"显示状态信息" }}</div>
            </div>
            <div class="show_artifact_report_btn" ref="show_artifact_report_btn"
            @touchstart="$refs.show_artifact_report_btn.classList.add('click')"
            @touchend="$refs.show_artifact_report_btn.classList.remove('click')"
            @click="reportShowRule['宝物特性'] = !reportShowRule['宝物特性']"
            >
                <div :class="`txt ${reportShowRule['宝物特性']?'current':''}`">{{ reportShowRule['宝物特性']?"隐藏宝物战报":"显示宝物战报" }}</div>
            </div>
            <div class="show_arm_report_btn" ref="show_arm_report_btn"
            @touchstart="$refs.show_arm_report_btn.classList.add('click')"
            @touchend="$refs.show_arm_report_btn.classList.remove('click')"
            @click="reportShowRule['兵种特性'] = !reportShowRule['兵种特性']"
            >
                <div :class="`txt ${reportShowRule['兵种特性']?'current':''}`">{{ reportShowRule['兵种特性']?"隐藏兵种战报":"显示兵种战报" }}</div>
            </div>
        </div>
        


        <div class="navigation_lift_btn" ref="navigation_lift_btn"
        @touchstart="$refs.navigation_lift_btn.classList.add('click')"
        @touchend="$refs.navigation_lift_btn.classList.remove('click')"
        @click="NavigateList()"
        >{{TransferChineseWords(game.Statistics.allTurn)}}回合</div>

    
        <div class="navigation_panel" v-show="show.navigation">
            <div class="mask cover" @click="CloseNavigateList"></div>
            <div class="navigation_list">
                <li ref="navigation_list_top"
                @touchstart="$refs.navigation_list_top.classList.add('click')"
                @touchend="$refs.navigation_list_top.classList.remove('click')"
                @click="NavLift(-1)">顶 部</li>
                <li ref="navigation_list"
                @touchstart="$refs.navigation_list[index].classList.add('click')"
                @touchend="$refs.navigation_list[index].classList.remove('click')"
                v-for="(l,index) in NAV_HEAP" :key="l.index" 
                @click="NavLift(index)">
                {{ l.name }}
                </li>
                <li ref="navigation_list_bottom"
                @touchstart="$refs.navigation_list_bottom.classList.add('click')"
                @touchend="$refs.navigation_list_bottom.classList.remove('click')"
                @click="NavLift(99)">底 部</li>

            </div>
        </div> 
   

        
        <TopNav title="战 报" />
        <router-view/>
    </div>
</template>

<script>
import ReportItem from '@/components/style/ReportItem.vue';
import Report from '@/battle/js/report';
import TopNav from '@/components/container/TopNav.vue';
import WarriorCard from '@/components/style/WarriorCard.vue';
import ArtifactElCard from '@/components/style/ArtifactElCard.vue';
import html2canvas from 'html2canvas';
// import Statistics from './Statistics.vue';
export default {
    name :'BattleReport',
    components : {
        // Statistics
        WarriorCard,
        ReportItem,
        TopNav,
        ArtifactElCard
    },
    data(){
        return{
            teams : this.$store.battle.battleResult,
            game : this.$store.battle,
            show : {
                statistics : false,
                navigation : false,
            },
            NAV_HEAP : [],
            viewHeight : window.innerHeight/100,//对应1vw的px
            viewWidth : window.innerWidth/100,//对应1vw的px

            reportShowRule:{
                "兵种特性":false,
                "宝物特性":false,
                "战斗":true,
                "开局信息":false,
                "状态信息":false
            }
            
        }
    },
    beforeCreate(){

    },
    methods:{
        //下载战报
        async DownloadBattleReport(target){
            // const target = this.$refs.result_warrior_container;
            if (!target) {
                console.error('未找到目标元素');
                return;
            }
            target.classList.add('download');
            try {
                // 关键：等待所有字体加载完成
                await document.fonts.ready; 
                // 2. 调用 html2canvas 转换 DOM 为 canvas
                const canvas = await html2canvas(target, {
                    useCORS: true, // 允许跨域图片（关键！如果有外部图片必须加）
                    scale: 2, // 缩放比例，2 表示高清（避免模糊）
                    backgroundColor: '#393b3e', // 背景色（默认白色，透明设为 null）
                    logging: false, // 关闭控制台日志
                    letterRendering: true,
                    allowTaint: true
                });

                // 3. 将 canvas 转为图片 URL
                const imgUrl = canvas.toDataURL('image/png'); // 可选 'image/jpeg'

                // 4. 创建下载链接并触发下载
                const link = document.createElement('a');
                link.href = imgUrl;
                link.download = `战报截图-${new Date().getTime()}.png`; // 文件名带时间戳
                link.click(); // 触发下载
                this.$store.Alert(`战报截图 下载完成`)

            } catch (error) {
                console.error('截图失败：', error);
                this.$store.Alert(`战报截图 下载失败`)
            } finally{
                // 5. 恢复目标元素原有的背景样式
                target.classList.remove('download');
            }
        
        },
        DownloadReport(){
            let that = this;
            let f1 = function(){
                that.DownloadBattleReport(that.$refs.report_image)
            }
            let f2 = function(){
                that.DownloadBattleReport(that.$refs.result_warrior_container)
            }
            let params = {
                title:"下载战报",
                msg : `选择一种战报形式下载`,
                optionBtnNames : ['结果战报','完整战报'],
                optionCallBack : [
                    f1,
                    f2
                ]
            }
            this.$store.OptionalPrompt(params)
        },
        
        InfoReportShow(){
            this.reportShowRule['开局信息'] = !this.reportShowRule['开局信息'];
            this.reportShowRule['状态信息'] = !this.reportShowRule['状态信息']
        },
        ShowRule(report){
            return this.reportShowRule[report.fromType];
        },  
        //获取队伍的原始总兵力
        TotalHp(team){
            let res = 0;
            for(let i in team){
                res += team[i].hpMax;
            }
            return res;
        },
        //获得对应的战报层数
        GetReportHeight(){
            this.NAV_HEAP = [];
            for(let i=0;i<this.game.ReportHeap.length;i++){
                if(this.game.ReportHeap[i].type == '系统' && this.game.ReportHeap[i].text != '游戏结束'){
                    let item = {
                        name : this.game.ReportHeap[i].text,
                        offsetIdx : i
                    }
                    this.NAV_HEAP.push(item);
                }
            }
            // this.NAV_HEAP.push(0);
            console.log(this.NAV_HEAP);
        },
        //导航电梯
        NavLift(position){
            if(position == 99){
                this.$refs.result_warrior_container.scrollTo(0,1000000);
                this.CloseNavigateList();
            }
            else if(position == -1){
                this.$refs.result_warrior_container.scrollTo(0,0);
                this.CloseNavigateList();
            }
            else{
                let reports = document.querySelectorAll('.report');
                let distance = reports[this.NAV_HEAP[position].offsetIdx].offsetTop;
                let high = 150;
                distance += high * this.viewWidth;
                this.$refs.result_warrior_container.scrollTo(0,distance);
                this.CloseNavigateList();
            }
            
        },
        //回合导航
        NavigateList(){
            this.show.navigation = true;
            this.$refs.result_warrior_container.classList.add('filter');
            this.$refs.report_show_control.classList.add('filter');
        },
        CloseNavigateList(){
            this.show.navigation = false;
            this.$refs.result_warrior_container.classList.remove('filter');
            this.$refs.report_show_control.classList.remove('filter');
        },
        //汉字
        TransferChineseWords(number){
            switch(number){
                case 1 : return "一";
                case 2 : return "二";
                case 3 : return "三";
                case 4 : return "四";
                case 5 : return "五";
                case 6 : return "六";
                case 7 : return "七";
                case 8 : return "八";
            }
        },
        LoadingTeamsData(){
            
        },
        //初始化血量
        LoadingBloodTape(){
            let blueTotal = this.TotalHp(this.teams[0]);
            let redTotal = this.TotalHp(this.teams[1]);
            let blue_w = Math.floor(((this.teams[0][0].hp + this.teams[0][1].hp + this.teams[0][2].hp) / blueTotal)*100);
            let red_w = Math.floor(((this.teams[1][0].hp + this.teams[1][1].hp + this.teams[1][2].hp) / redTotal)*100);
            let blue_wound_w = 0;
            for(let i in this.teams[0]){
                blue_wound_w += this.teams[0][i].hp + this.teams[0][i].wound;
            }
            blue_wound_w = Math.floor((blue_wound_w/blueTotal)*100);
            let red_wound_w = 0;
            for(let i in this.teams[1]){
                red_wound_w += this.teams[1][i].hp + this.teams[1][i].wound;
            }
            red_wound_w = Math.floor((red_wound_w/blueTotal)*100);
            if(blue_w < 0){
                blue_w = 0
            }else if(blue_w > 100){
                blue_w = 100;
            }
            if(red_w < 0){
                red_w = 0;
            }else if(red_w > 100){
                red_w = 100;
            }
            // let size = this.$store.state.equipment == 'phone' ? 50 : 25;
            let size = 50;
            this.$refs.blood_blue[0].style.width = (blue_w/100) * size + 'vw';
            this.$refs.wound_blue[0].style.width = (blue_wound_w/100) * size + 'vw';
            this.$refs.blood_red[0].style.width = (red_w/100) * size + 'vw';
            this.$refs.wound_red[0].style.width = (red_wound_w/100) * size + 'vw';
        },
        //打开统计面板
        OpenStatisticsPanel(){
            this.$router.push({
                path : `${this.$route.path}/report_statistics`,
            })
        }   
    },
    mounted(){
        setTimeout(() => {
            this.LoadingBloodTape();
        }, 100);
        //加载战报
        // let report = new Report();
        // report.BuildReport(this.$refs.report_text,this.$store.battle.ReportHeap);
        this.GetReportHeight();
    }
}
</script>

<style lang="less" scoped>
.battle_result_panel{
    position: absolute;left: 0; top: 0;
    width: 100vw;
    height: 100vh;
    background: url('../../assets/background/bg (1).jpg')right bottom no-repeat;
    background-size: cover;
    
    //蓝红环
    .blue_loop,.red_loop{
        position: absolute; left: 50%; top : 50%;
        transform : translate(-50%,-50%);
