                    }
                    .wound_blue{
                        background: rgb(0, 60, 106);
                    }
                    .wound_red{
                        background: rgb(83, 0, 0);
                    }
                    .troops_number_blue,.troops_number_red{
                        left: -25vw;bottom: -1vw;
                        width: 25vw;height: 5vw;
                        line-height: 6vw;
                        // background: #000;

                    }
                    .troops_number_red{
                        
                        left: 50vw;
                    }
                }
                .troops_groove_left{
                    left: 23vw;
                }
                .warrior_card{
                    position: relative; float: left;
                    width: 22vw;height: 33vw;
                    margin: 17vw 0 0 2.5vw;
                    background: url('../../assets/background/bg (23).png')no-repeat;
                    background-size: cover;
                    .artifact_box{
                        bottom: -4.5vw;left: 0;
                        width: 22vw; height: 4.4vw;
