                        box-shadow: 0 0 3px black;
                    }
                    .name{
                        position: absolute; left: 0; top: 5%;
                        width: 20%;
                        height: 60%;
                        line-height: 3.8vw;
                        font-size: 100%;
                        transform: scale(.6);
                    }
                    .death_mask{
                        position: absolute; left: 0; top : 0;
                        width : 100%; height : 100%;
                        background: rgba(0, 0, 0, 0.7);
                    }
                    .warrior_position{
                        position: absolute; left: 50%; top: -5vw;
                        margin: 0 0 0 -8vw;
                        width: 16vw;
                        height: 4vw;
                        line-height: 4vw;
                        font-size: 2vw;
                        // background: rgb(78, 78, 78);
                        background: url('../../assets/ui/ui (133).png')no-repeat;
                        background-size: 100% 100%;
                        color: rgb(180, 180, 180);
                        font-family: 'FZLBJW';
                    }
                }
                .result_icon{
                    position: absolute;
                    top: 60vw; left: 0;
                    width: 76vw;height: 16vw;position: relative;
                    // background: url('../../assets/background/bg (8).png')black bottom center no-repeat;
                    background: rgba(0, 0, 0, 0.2);
                    background-size: 100% 120%;
                    // margin: 0 0 0 8vw;
                    .game_time{
                        left: 0;top: 0;
                        width: 20vw;height: 6vw;
                        line-height: 6vw;
                        color: rgb(190, 190, 190);
                    }
                    .text_icon{
                        position: absolute; left: 50%; top : 50%;
                        transform : translate(-50%,-50%);
                        width: 14vw;height: 8vw;
                        
                    }
                    .bg_icon1{
                        position: absolute; left: 50%; top : 50%;
                        transform : translate(-50%,-50%);
                        width: 14vw;height: 14vw;
                        background: url('../../assets/ui/ui (132).png')no-repeat;
                        background-size: 100% 100%;
                        opacity: .2;
                    }
                }
            }
            .team_container:nth-of-type(2){
                margin: 20vw 0 0 7vw;
                .warrior_card{
                    float: right;position: relative;
                    margin: 17vw 2.5vw 0 0;
                }
            }
            
            .statistics_btn,.download_report_btn{
                right: 15vw; bottom: 4vw;
                width: 20vw;height: 5vw;
                line-height: 5vw;
                background: white;
                font-weight: bold;
                color: black;
                
            }
            .download_report_btn{
                right : auto; left: 10vw;
            }
            &.download{
                .statistics_btn,.download_report_btn{
                    display: none;
                }
            }
        }
        .report_text{
            position: relative;
            margin: 5vw 0 0 0;
            width: 95vw;
            background: #000;
            .report{
                position: relative; float: left;
                // background: #000;
                // width: 95vw; height: 5vw;
                // margin: 1vw 0 0 0;
                width: 95vw;
                min-height: 4vw;
                line-height: 4vw;
                margin: .4vw 0;
                // line-height: 4vw;
            }
            // background: blueviolet;
        }
        &.download{
            overflow-y: none;
            overflow-x: none;
            height: auto;
        }
    }
    .result_warrior_container.filter{
        filter: blur(5px);
    }
    .navigation_lift_btn{
        position: fixed;
        right: 5vw; bottom: 5vh;
        padding: 5vw 1vw;
        font-size: 3vw;
        width: 4vw;height: 12vw;
        line-height: 4vw;
        opacity: 0.7;
        background: url('../../assets/ui/ui (145).png')no-repeat;
        background-size: 100% 100%;
        box-shadow: 0 0 3px black;
    }
    .navigation_lift_btn.click{
        transform: scale(.8);
    }

    .navigation_panel{
        position: fixed;
        left: 0; top: 0;
        width: 100vw;height: 100vh;
        
        .navigation_list{
            position: fixed;
            right: 5vw; bottom: 5vh;
            width: 28vw;
            // height: 83vw;
            background:rgba(93, 93, 93, 0.7);
            li{
                float: left;position: relative;
                width: 27vw;height: 7vw;
                line-height: 7vw;
                background: rgb(37, 36, 36);
                color: white;
                margin: .5vw 0 0 .5vw;
                
            }
            li.click{
                background: rgb(165, 162, 162);
            }
        }
    }
    .report_show_control{
        position: absolute; bottom: 2vh;
        left: 5vw;
        width: 60vw; height: 6vw;
        // background: #000;
        .show_artifact_report_btn,.show_arm_report_btn,.show_begin_info{
            left: 0; top: 0;
            width: 25vw; height: 6vw;
            background: url('../../assets/ui/ui\ \(170\).png');
            background-size: 100% 100%;
            color: rgb(173, 173, 173);
            .txt{
                left: 0; top: 0;
                width: 25vw; height: 6vw;
                line-height: 6vw;
                text-align: center;
            }
            .txt.current{
                color: white;
            }
        }
        .show_arm_report_btn{
            left: 28vw;
        }
        .show_artifact_report_btn{
            left: 56vw;
        }
    }
    .report_show_control.filter{
        filter: blur(5px);
    }
}
</style>