<template>
    <div class="lineup_group">
        <div class="group_container">

            <div class="group_box" 
            v-for="g in $store.battle.lineupGroups" :key="g.index"
            @click="SelectThisGroup(g)">
                <div class="g_name">{{ g.groupName }}</div>
                <div class="g_describe">{{ g.groupDescribe==""?"无描述": g.groupDescribe}}</div>
                <div class="g_color" :style="`background-color:${g.groupColor==''?'black':g.groupColor};`"></div>
            </div>


        </div>
        <div class="bottom">
            <div class="add_new_group_btn" ref="add_new_group_btn"
            @touchstart="$refs.add_new_group_btn.classList.add('click')"
            @touchend="$refs.add_new_group_btn.classList.remove('click')"
            @click="AddNewGroup()">新增分组</div>
        </div>

        <div class="group_panel" v-if="groupPanel.display">
            <div class="mask cover"></div>

            <div class="group_panel_container" v-if="currentGroup">
                <div class="base_info">
                    <div class="group_name">
                        <div class="title">分组名称</div>
                        <input type="text" placeholder="请输入分组名称" v-model="currentGroup.groupName" class="value">
                    </div>
                    <div class="group_describe">
                        <div class="title">分组描述</div>
                        <input type="text" placeholder="描述该分组" v-model="currentGroup.groupDescribe" class="value">
                    </div>
                </div>

                <div class="import_group" ref="import_group"
                @touchstart="$refs.import_group.classList.add('click')"
                @touchend="$refs.import_group.classList.remove('click')"
                @click="ImportGroupTeam()">
                    导入分组
                </div>
                <div class="delete_btn" ref="delete_btn"
                @touchstart="$refs.delete_btn.classList.add('click')"
                @touchend="$refs.delete_btn.classList.remove('click')"
                @click="DeleteGroup()">删除分组</div>

                <TopicTitle title="分组" :titleFontSize="4.5" :closeFunc="CloseGroupPanel"/>
            </div>

        </div>
        
        <div class="group_add_panel" v-if="groupCreatePanel.display">
            <div class="mask cover"></div>
            <div class="group_create_panel_container">
                <div class="optional">
                    <div class="group_name">
                        <div class="title">分组名称</div>
                        <input type="text" placeholder="请输入分组名称" v-model="groupData.groupName" class="value">
                    </div>
                    <div class="group_describe">
                        <div class="title">分组描述</div>
                        <input type="text" placeholder="描述该分组" v-model="groupData.groupDescribe" class="value">
                    </div>
                    <div class="group_color_select">
                        <div class="top">
                            <div class="title">颜色选择</div>
                            <div class="tip">选择一种颜色来直观区别组别</div>
                        </div>
                        <div class="color_bar">
                            <div class="color_item" ref="color_item"
                            @touchstart="$refs.color_item[cIndex].classList.add('click')"
                            @touchend="$refs.color_item[cIndex].classList.remove('click')"
                            v-for="c,cIndex in colorList" :key="c.index"
                            @click="groupData.groupColor = c">
                                <div class="color_circle" :style="`background-color:${c};`"></div>
                                <div class="current_frame" v-if="c == groupData.groupColor"></div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="add_btn" ref="add_btn"
                @touchstart="$refs.add_btn.classList.add('click')"
                @touchend="$refs.add_btn.classList.remove('click')"
                @click="ConfirmAdd()">确认新建</div>
                <div class="return_def_btn" ref="return_def_btn"
                @touchstart="$refs.return_def_btn.classList.add('click')"
                @touchend="$refs.return_def_btn.classList.remove('click')"
                @click="ReturnDefConfig()">恢复默认</div>

                <TopicTitle title="新建分组" :titleFontSize="5.5" :closeFunc="CloseGroupPanel"/>
            </div>
        </div>

        
        <TopNav title="阵容分组" />
    </div>
</template>


<script>
import TopNav from '@/components/container/TopNav.vue';
import TopicTitle from '@/components/style/TopicTitle.vue';
import { BattleUtil } from '@/util/battle';
import Save from '@/util/save';
export default {
    name : 'LineupGroup',
    components:{
        TopNav,
        TopicTitle
    },
    data(){
        return{
            groupPanel:{
                display:false,
            },
            groupCreatePanel:{
                display : false
            },

            groupData:{
                groupName : "",
                groupDescribe : "",
                groupColor : "cyan"
            },

            colorList : [
                "green",
                "red",
                "yellow",
                "cyan",
                "teal",
                "white",
                "maroon",
                "seagreen",
                "purple",
                "magenta",
                "pink",
                "brown",
                "orange",
                "skyblue",
                "silver",
                "royalblue",
                "indigo",
                "lime"
            ],

            currentGroup:null,
            // 用于存储定时器ID
            saveTimer:null,
        }
    },
    watch:{
        // 监听深层属性
        'currentGroup.groupName': {
            handler(newVal, oldVal) {
                clearTimeout(this.saveTimer)
                this.saveTimer = setTimeout(() => {
                    this.SaveData(this.currentGroup);
                }, 2000);
            },
            deep: true // 开启深层监听
        },
        'currentGroup.groupDescribe': {
            handler(newVal, oldVal) {
                clearTimeout(this.saveTimer)
                this.saveTimer = setTimeout(() => {
                    this.SaveData(this.currentGroup);
                }, 2000);
            },
            deep: true // 开启深层监听
        }
    },
    methods:{
        SaveData(whichGroup){
            for(let i in this.$store.battleStorage.lineupGroups){
                if(this.$store.battleStorage.lineupGroups[i].gid == whichGroup.gid){
                    this.$store.battleStorage.lineupGroups[i].groupName = whichGroup.groupName
                    this.$store.battleStorage.lineupGroups[i].groupDescribe = whichGroup.groupDescribe
                }
            }
            let s = new Save(this.$store)
            s.SaveBattleData()
        },
        //选中组
        SelectThisGroup(group){
            this.groupPanel.display = true;
            this.currentGroup = group
        },
        
        //恢复默认
        ReturnDefConfig(){
            this.groupData = {
                groupName : "默认组",
                groupDescribe : "",
                groupColor : "cyan"
            }
        },
        AddNewGroup(){
            this.groupCreatePanel.display = true;
        },
        //确认新建分组
        ConfirmAdd(){
            if(this.groupData.groupName == ""){
                this.$store.Alert('分组名称不能为空!');
                return;
            }
            for(let i in this.$store.battle.lineupGroups){
                if(this.$store.battle.lineupGroups[i].groupName == this.groupData.groupName){
                    this.$store.Alert('分组名称已存在，请重新命名!');
                    return;
                }
            }
            let b = new BattleUtil(this.$store);
            let res = b.CreateNewGroup(this.groupData);
            if(res){
                this.$store.Alert('分组创建成功');
                this.CloseGroupPanel();
            }
        },
        //删除分组
        DeleteGroup(){
            let that = this;
            let f = function(){
                let b = new BattleUtil(that.$store);
                let res = b.DeleteGroup(that.currentGroup.gid);
                if(res){
                    that.CloseGroupPanel();
                    that.$store.Alert('删除成功');
                }
                else{
                    that.$store.Alert('删除失败');
                }
            }
            let params = {
                msg : `确定删除该队伍吗？`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        },
        //导入分组队伍
        ImportGroupTeam(){
            let that = this;

            let f = function(){
                let arr = [];
                //把组内队伍整合
                for(let i in that.$store.battle.lineupList){
                    if(that.$store.battle.lineupList[i].groupIds.includes(that.currentGroup.gid)){
                        arr.push(that.$store.battle.lineupList[i]);
                    }
                }
                that.$store.battle.visibleLineups =  arr;
                that.$store.battle.currentGroup = that.currentGroup;
                that.$store.Alert('导入成功');
                that.$router.back();
            }

            let params = {
                msg : `确定将阵容列表导入为该分组下的阵容列表吗？`,
                CallBack : f,
            }
            this.$store.Prompt(params)

        },
        CloseGroupPanel(){
            this.groupPanel.display = false;
            this.groupCreatePanel.display = false;
        }
    }
}

</script>


