                        &:focus{
                            box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.2);
                            outline: none;
                        }
                    }
                }
                .group_color_select{
                    position: relative; float: left;
                    width: 90vw; 
                    background: rgba(0, 0, 0, 0.3);
                    .top{
