                            }
                            .current_frame{
                                position: absolute;
                                left: 0; top: 0;
                                width: 7vw; height: 7vw;
                                background: url('../../assets/ui/ui\ \(198\).png')center no-repeat;
                                background-size: 100% 100%;
                                border-radius: 50%;
                                // border:1vw  1vw yellow ;
                                box-shadow: 0 0 1vw rgb(255, 255, 255);
                            }
                        }
                    }
                }
            }
            .add_btn,.return_def_btn{
                position: absolute; right: 5vw; bottom: 5vw; left: auto;
                width: 20vw; height: 5vw;
                display: flex;
                align-items: center;
                justify-content: center;
                color: black;
                font-weight: bold;
                background: white;
            }
            .return_def_btn{
                right: 30vw;
            }
        }
    }
    .group_panel{
        position: absolute;left: 0; top: 0;
        width: 100vw;
        height: 100vh;
        z-index: 1;
        .mask{
            background: rgba(0, 0, 0, 0.5);
        }
        .group_panel_container{
            left: 0; top: 50%;
            width: 100vw; height: 60vw;
            margin: -30vw 0 0 0;
            background: url('../../assets/background/bg (7).png')#393b3e bottom right no-repeat;
            background-size: cover;
            // background: red;
            .base_info{
                left: 5vw; top: 18vw;
                width: 90vw; height: 30vw;
                .group_name,.group_describe{
                    position: relative; float: left;
                    width: 90vw; height: 7vw;
                    background: rgba(0, 0, 0, 0.3);
                    margin: 0 0 3vw 0;
                    .title{
                        position: relative;float: left;
                        width: 20vw; height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                    .value{
                        position: relative;float: left;
                        width: 65vw; height: 5vw;
                        margin: 1vw 0 0 0;
                        border: 0;
                        display: flex;
                        align-items: center;
                        text-align: left;
                        justify-content: left;
                        background: transparent;
                        color: white;
                        &:focus{
                            box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.2);
                            outline: none;
                        }
                    }
                }
            }
            
            .delete_btn,.import_group{
                right: 5vw; bottom: 5vw;left: auto;
                width: 20vw; height: 5vw;
                display: flex;
                align-items: center;
                justify-content: center;
                color: black;
                background: white;
                font-weight: bold;
            }
            .delete_btn{
                right : 30vw;
            }
        }
    }
}
</style>