            <img src="../../assets/ui/ui (66).png" alt="" v-show="warriorData.sign == 's2'" class="cover">
            <img src="../../assets/ui/ui (67).png" alt="" v-show="warriorData.sign == 's3'" class="cover">
            <img src="../../assets/ui/ui (68).png" alt="" v-show="warriorData.sign == 'sp'" class="cover">
            <img src="../../assets/ui/ui (69).png" alt="" v-show="warriorData.sign == 'xp'" class="cover">
        </div>

        <img src="../../assets/ui/ui (65).png" alt="" class="warrior_elongated_card_warrior_upmark" v-if="warriorData.isUp">
        <img src="../../assets/ui/ui (84).png" alt="" class="warrior_elongated_card_warrior_upmark" v-if="warriorData.isUpWork">
        <img src="../../assets/ui/ui (73).png" alt="" class="warrior_elongated_card_warrior_upmark" v-if="warriorData.isUp && warriorData.isUpWork">

        <div class="warrior_elongated_card_bottom">
            <div class="warrior_elongated_card_bottom_level">Lv.{{ warriorData.level }}</div>
            <div class="warrior_elongated_card_bottom_cost_img"></div>
            <div class="warrior_elongated_card_bottom_cost_num">{{warriorData.cost}}</div>

            <div class="warrior_elongated_card_bottom_arm" v-if="!showUpArms && warriorData.id != -1">
                <img src="../../assets/ui/ui (81).png"  v-if="!warriorData.isUpArms" class="cover">
                <img src="../../assets/ui/ui (82).png"  v-if="warriorData.isUpArms" class="cover">
                <img src="../../assets/ui/ui (79).png"  v-if="warriorData.arms == '步' && !warriorData.isUpArms" class="warrior_elongated_card_bottom_arm_icon">
                <img src="../../assets/ui/ui (70).png"  v-if="warriorData.arms == '骑' && !warriorData.isUpArms" class="warrior_elongated_card_bottom_arm_icon">
                <img src="../../assets/ui/ui (62).png"  v-if="warriorData.arms == '弓' && !warriorData.isUpArms" class="warrior_elongated_card_bottom_arm_icon">

                <img src="../../assets/ui/ui (71).png"  v-if="warriorData.arms == '骑' && warriorData.isUpArms" class="warrior_elongated_card_bottom_arm_icon">
                <img src="../../assets/ui/ui (80).png"  v-if="warriorData.arms == '步' && warriorData.isUpArms" class="warrior_elongated_card_bottom_arm_icon">
                <img src="../../assets/ui/ui (63).png"  v-if="warriorData.arms == '弓' && warriorData.isUpArms" class="warrior_elongated_card_bottom_arm_icon">
                <div class="arm_num">{{warriorData.range}}</div>
            </div>

            <div class="up_bottom_arm" v-if="showUpArms && warriorData.id != -1">
                <img src="../../assets/ui/ui (81).png"  v-if="!warriorData.isUpArms" class="cover">
                <img src="../../assets/ui/ui (82).png"  v-if="warriorData.isUpArms" class="cover">
                <img :src="UpArmsIconImg"  alt="" class="cover">
                <div class="arm_num">{{warriorData.range}}</div>
            </div>
        </div>
        
    </div>
</template>

<script>
export default {
    name : 'WarriorElongatedCard',
    props: {
        warriorData: null,
        cardStyle: {
            default : "3_1",
            validator(value) {
                return ["3_1", "2_1"].includes(value);
            }
        },
        showUpArms:{
            type:Boolean,
            default : false,
        }
    },
    computed:{
        UpArmsIconImg(){
            return require(`../../assets/${this.warriorData.isUpArms?"up_arms":"arms"}/${this.$store.armsMap[this.warriorData.upArms[this.warriorData.choseUpArmsIdx]]}.png`)
        },
    },
    methods:{
        GetWarriorNameColor(camp){
            let class_name = 'warrior_elongated_card_name '
            switch(camp){
                case "魏" : class_name += "warrior_elongated_card_wei";break
                case "蜀" : class_name += "warrior_elongated_card_shu";break
                case "吴" : class_name += "warrior_elongated_card_wu";break
                case "群" : class_name += "warrior_elongated_card_qun";break
                case "汉" : class_name += "warrior_elongated_card_han";break
                case "晋" : class_name += "warrior_elongated_card_jin";break
            }   
            return class_name
        }
    }
}
</script>

