<template>
    <div class="nav_component" ref="nav_component" v-if="$route.path != '/home' && $route.path != '/'">
        <div class="nav_component_title">
            <div class="txt">{{ title }}</div>
        </div>
        <div class="back_btn" ref="back_btn" 
        @touchstart="$refs.back_btn.classList.add('click')"
        @touchend="$refs.back_btn.classList.remove('click')"
        @mouseenter="$refs.back_btn.classList.add('hover')"
        @mouseleave="$refs.back_btn.classList.remove('hover')"
        @click="Back()"></div>
    </div>
</template>

<script>
import Save from '../../util/save';
export default {
    name : 'TopNav',
    props: {
        title: "",
        // card_style: {
        //     default : "2_3",
        //     validator(value) {
        //         return ["2_3", "2_3_sml_pic"].includes(value);
        //     }
        // },
    },
    data(){
        return{
            
        }
    },
    methods:{
        //返回上一级路由
        Back(){
            if(this.$store.dirty.warrior || this.$store.dirty.tactics){
                let save = new Save(this.$store);
                if(this.$store.dirty.warrior){
                    save.SaveWarriorData()
                }
                if(this.$store.dirty.tactics){
                    save.SaveTacticsData()
                }
                this.$store.dirty.tactics = false
                this.$store.dirty.warrior = false
            }
            this.$router.back();
        },
        
    },
    mounted(){
        this.$store.AutoStyle(this.$refs.nav_component)
    }
}
</script>

<style lang="less" scoped>
.nav_component{
    position: absolute; left: 0; top: 0;
    width: 100vw;height: 12vh;
    .nav_component_title{
        left: 0; bottom: 1vw;
        width: 52vw;height: 11vw;
        background: url('../../assets/background/bg (5).png')no-repeat;
        background-size: 100% 100%;
        .txt{
            width: 100%; height: 11vw;
            line-height: 11vw;
            font-size: 5.4vw;
            text-align: center;
            color: rgb(193, 193, 82);
            text-shadow: 0 0 2px black;
            text-indent: -15vw;
            transition: all .3s;
            font-family: 'FZLBJW';
        }
    }
    .back_btn{
        right:7vw; bottom: 3vw;
        width: 7vw;
        height: 7vw;
        background: url('../../assets/ui/ui\ \(85\).png')no-repeat;
        background-size: 100% 100%;
    }
}
.nav_component.pad{
    position: absolute; left: 0; top: 0;
    width: 100vw;height: 10vh;
    .nav_component_title{
        left: 0; bottom: 0;
        width: 26vh;height: 5vh;
        background: url('../../assets/background/bg (5).png')no-repeat;
        background-size: 100% 100%;
        .txt{
            width: 100%; height: 5vh;
            line-height: 5vh;
            font-size: 2.7vh;
            text-align: center;
            color: rgb(193, 193, 82);
            text-shadow: 0 0 2px black;
            text-indent: -7vh;
            transition: all .3s;
            font-family: 'FZLBJW';
        }
    }
    .back_btn{
        right:7vw; bottom: 1vh;
        width: 3.5vh;
        height: 3.5vh;
        background: url('../../assets/ui/ui\ \(85\).png')no-repeat;
        background-size: 100% 100%;
        cursor: pointer;
    }
    .back_btn:hover{
        transform: scale(1.1);
    }
}

</style>