<template>
    <!-- <div class="report_item" v-if="ShowRule()">
        
        
        
    </div> -->
    <div class="sys_info" v-if="reportData.type == '系统'">
            <span style="color: darkgray;">—————&nbsp;&nbsp;&nbsp;</span>
            {{ reportData.text}}
            <span style="color: gray;">&nbsp;&nbsp;&nbsp;—————</span>
    </div>
    <div class="warrior_action" v-if="reportData.type == '行动'">
        <div class="warrior_head">
            <div :class="reportData.initBelong+'_loop'"></div>
            <div class="head_img">
                <img :src="reportData.initImg" alt="" class="img">
            </div>
        </div>
        <div class="title">行动阶段</div>
        <div class="hp">{{ reportData.text }}</div>
    </div>
    <div class="normal" v-if="reportData.type == '执行' || reportData.type == '两目标'">
        <div class="left_padding" v-if="reportData.level > 1"></div>
        <div class="left_padding_dot" v-if="reportData.level >= 3"></div>
        <div class="warrior_head">
            <div :class="reportData.initBelong+'_loop'"></div>
            <div class="head_img">
                <img :src="reportData.initImg" alt="" class="img">
            </div>
        </div>
        <div :class="'text_block '+t.type" v-for="t in text1Arr" :key="t.index">
            {{ t.value }}
        </div>
        <div class="warrior_head" v-if="reportData.type == '两目标'">
            <div :class="reportData.recBelong+'_loop'"></div>
            <div class="head_img">
                <img :src="reportData.recImg" alt="" class="img">
            </div>
        </div>
        <div :class="'text_block '+t.type" v-for="t in text2Arr" :key="t.index">
            {{ t.value }}
        </div>
    </div>
</template>

<script>
export default {
    name : 'ReportItem',
    props: {
        reportData: null,
    },
    data(){
        return{
            text1Arr : [],
            text2Arr : [],
        }
    },
    methods:{
        ShowRule(){
            if(this.reportData.fromType == '兵种特性' || this.reportData.fromType != '宝物特性') return false;
            // reportData.fromType != '兵种特性'
            return true;
        },        
        //输入一个字符串，返回
        ParseString(str){
            // 正则表达式：匹配数字、百分比符号和其他文本
            let regex = /(\d+)|(%+)|([^0-9%]+)/g;
            let matches = [...str.matchAll(regex)].filter(m => m[0].trim() !== '');
            
            let result = [];
            let prevType = null;
            
            matches.forEach((match, index) => {
                let item;
                
                if (match[1]) { // 数字
                let value = Number(match[1]);
                let prevText = index > 0 && result[index-1].type === 'text' 
                    ? result[index-1].value 
                    : '';
                
                if (prevText.includes('损失了')) {
                    let damageLevel = ""
                    if(value < 120) damageLevel = "low_damage"
                    if(value >= 120 && value < 1200) damageLevel = "damage"
                    if(value >= 1200) damageLevel = "high_damage"
                    item = { type: damageLevel, value };
                } else if (prevText.includes('恢复了')) {
                    item = { type: 'cure', value };
                } else {
                    item = { type: 'number', value };
                }
                } 
                else if (match[2]) { // 百分比符号
                    item = { type: 'percent', value: match[2] };
                } 
                else { // 普通文本
                    item = { type: 'text', value: match[3] };
                }
                
                result.push(item);
                prevType = item.type;
            });
            return result;
        }
    },
    mounted(){
        this.text1Arr = this.ParseString(this.reportData.text)
        this.text2Arr = this.ParseString(this.reportData.text1)
    }
}
</script>

