<template>
    <div class="team_info_view">
        <!-- 背景黑板 -->
        <div class="edit_single_team_bg_mask cover"></div>
        <div class="edit_container">
            <!-- 队伍名称 -->
            <TopicTitle title="部队预览" :closeFunc="Close" />
            <!-- 队伍配置容器 -->
            <div class="team_exposition">
                <div class="warrior_card_ipt" v-for="(w,wIdx) in $store.buffer.curTeam" :key="w.index">
                    <WarriorCard :warriorData="w" :showUpArms="true" />
                    <div class="warrior_position" v-show="wIdx == 0">大 营</div>
                    <div class="warrior_position" v-show="wIdx == 1">中 军</div>
                    <div class="warrior_position" v-show="wIdx == 2">前 锋</div>

                    <div class="cover" @click="ConfigWarrior(wIdx)" v-if="w.id == -1"></div>
                    <div class="cover" @click="UnloadWarrior(w,wIdx)" v-if="w.id != -1"></div>

                    <div class="config_container" v-if="w.id != -1">
                        <div class="tactics_container">
                            <div class="tactics_box" v-for="t,tIdx in w.tacticsArr" :key="t.index" @click="TacticsConfig(w,t,tIdx)">
                                <TacticsIcon :tacticsData="t" />
                                <div class="divider" v-if="tIdx != 2"></div>
                            </div>
                        </div>
                    </div>
                    <div class="artifact_box" v-if="w.artifact"
                    @click="UnloadArtifact(w)">
                        <img :src="AritifactImgIcon(w.artifact)" alt="" class="art_up_sign">
                        <div class="name">{{ w.artifact.name }}</div>
                        <div class="forge_eff">
                            <img :src="ForgeEffImgIcon(w.artifact)" class="cover">
                            <div class="text cover">{{ w.artifact.forgeEffArr[w.artifact.forgeChoseIdx].name }}</div>
                        </div>
                    </div>
                    <div class="wait_load_artifact" v-if="w.id != -1 && !w.artifact"
                    @click="SelectArtifact(w)">
                        <img src="../../assets/ui/ui (190).png" class="add_sign">
                        <div class="name">添加宝物</div>
                    </div>
                </div>
                
                
            </div>
            <!-- 删除队伍按钮 -->
            <div class="delete_team_btn" ref="delete_team_btn" @touchstart="$refs.delete_team_btn.classList.add('click')" 
            @touchend="$refs.delete_team_btn.classList.remove('click')" @click="IsDeleteThisTeam()">删除队伍</div>

            <!-- 模拟对战按钮 -->
            <div class="simulateBattle_btn" ref="simulateBattle_btn" @touchstart="$refs.simulateBattle_btn.classList.add('click')" 
            @touchend="$refs.simulateBattle_btn.classList.remove('click')" @click="TurnToSimBattle">模拟对战</div>
        </div>


        

        <!-- 路由 -->
        <router-view/>
    </div>
</template>

<script>
import WarriorCard from '../../components/style/WarriorCard.vue';
import TacticsIcon from '../../components/style/TacticsIcon.vue';
import TopicTitle from '@/components/style/TopicTitle.vue';
import Warrior from '@/util/warrior';
import Save from '@/util/save';
// import Team from '../../util/team';
export default {
    name : 'TeamInfo',
    components:{
        WarriorCard,
        TopicTitle,
        // WarriorHeadIcon,
        TacticsIcon,
    },
    data(){
        return{
            
        }
    },
    methods:{
        UnloadArtifact(w){
            this.$store.buffer.curWarrior = w
            let that = this;
            let f = function(){
                that.$store.buffer.curWarrior.UnloadArtifact();
                that.$store.dirty.warrior = true;
            }
            let params = {
                msg : `确定移除【${w.camp}-${w.name}-${w.arms}】佩戴的宝物【${w.artifact.name}】？`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        },
        SelectArtifact(w){
            this.$store.buffer.curWarrior = w
            this.$router.push({
                path : `${this.$route.path}/artifact_list`,
            })
        },
        AritifactImgIcon(art){
            switch(art.effQualityIdx){
                case 0 :
                case 1 : return require('../../assets/ui/ui (65).png');
                case 2 : return require('../../assets/ui/ui (73).png');
            }
        },
        ForgeEffImgIcon(art){
            switch(art.effQualityIdx){
                case 0 : return require('../../assets/ui/ui (182).png');
                case 1 : return require('../../assets/ui/ui (184).png');
                case 2 : return require('../../assets/ui/ui (183).png');
            }
        },
        //关闭
        Close(){
            this.$router.back()
        },
        //配置战法
        TacticsConfig(w,t,tIdx){
            this.$store.buffer.curWarrior = w
            this.$store.buffer.curTactics = t
            this.$store.buffer.editTactIdx = tIdx
            //调整战法详情界面的设置
            let model = "show"
            if(tIdx != 0 && t.id == -1) model = "load"
            else if(tIdx != 0 && t.id != -1) model = "unload"
            // 表示卸载战法
            if(t.id != -1){
                this.$router.push({
                    path : `${this.$route.path}/tactics_info`,
                    query : {
                        model : model,
                    }
                })
            }
            // 表示安装战法
            else if(t.id == -1){
                this.$router.push({
                    path : `${this.$route.path}/tactics_list`,
                })
            }
        },
        //配置武将
        ConfigWarrior(wIdx){
            this.$store.buffer.editTeamWarriorIdx = wIdx
            this.$router.push({
                path : `${this.$route.path}/warrior_list`,
                query : {
                    showLoadWarrior : false,
                    showStar : true,
                    showSign : true
                }
            })
        },
        //卸载武将
        UnloadWarrior(warriorData,wIdx){
            let that = this;
            let f = function(){
                that.$store.teams.UnloadWarrior(that.$store.buffer.editTeamIdx,wIdx)
            }
            let params = {
                msg : `确定移除【${warriorData.camp}-${warriorData.name}-${warriorData.arms}】？`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        },
        //是否删除队伍
        IsDeleteThisTeam(){
            if(this.$store.teams.list.length == 0) return false;
            let that = this;
            let f = function(){
                for(let i in that.$store.teams.list){
                    if(that.$store.teams.list[i] == that.$store.buffer.curTeam){
                        that.$store.teams.list.splice(i,1);
                        let s = new Save(that.$store);
                        s.SaveMyTeam()
                        that.$router.back();
                        that.$store.Alert('删除队伍成功');
                    }
                }
            }
            let params = {
                msg : `确定删除该队伍？`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        },
        //转到模拟对局
        TurnToSimBattle(){
            //检查配置
            for(let i in this.$store.buffer.curTeam){
                if(this.$store.buffer.curTeam[i].id == -1){
                    this.$store.Alert('请至少配置三个武将');
                    return false;
                }
            }
            let that = this;
            let f = function(){
                for(let i in that.$store.battle.teams.list[0]){
                    let newWarrior = new Warrior();
                    newWarrior.CopyData(that.$store.buffer.curTeam[i]);
                    newWarrior.ChangePortrayal(newWarrior.skinChoseIdx)
                    that.$store.battle.teams.list[0][i] = newWarrior;
                    //武将战法的装载位置为-1
                    for(let j in newWarrior.tacticsArr){
                        newWarrior.tacticsArr[j].isload = -1;
                    }
                    //宝物装载位置
                    if(newWarrior.artifact && newWarrior.artifact.warriorId != -1) newWarrior.artifact.warriorId = -1;
                }
                that.$router.go(-1);
                setTimeout(() => {
                    that.$router.push({
                        path : `/battle`,
                    })
                }, 10);
            }
            
            let params = {
                msg : `确定使用该队伍？`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        }
    },
    
}
</script>

<style lang="less">
//编辑队伍
.team_info_view{
    position: absolute; left: 0; top : 0;
    width : 100vw; height : 100vh;
    z-index: 20;
    .edit_single_team_bg_mask{
        background: rgba(0, 0, 0, 0.679)
    }
    .edit_container{
        position: absolute; top: 50%;
        margin-top: -70vw;
        width: 100vw; height: 140vw;
        background: url('../../assets/background/bg (7).png')#393b3e bottom right no-repeat;
        background-size: cover;
        overflow: hidden;
        
        
        .team_exposition{
            position: absolute; left: 0; top: 20vw;
            width: 100vw;
            height: 40vw;
            .warrior_card_ipt{
                position: relative;float: left;
                width: 24vw;height: 36vw;
                background: url('../../assets/background/bg (24).png')no-repeat;
                background-size: 100% 100%;
                margin: 5vw 0 0 2vw;
                transition: all .1s;
                .warrior_position{
                    position: absolute; top: -5.5vw; left: 0;
                    width: 100%;
                    height: 4vw;
                    // background: url('../assets/main/bg.png')rgb(75, 75, 75) no-repeat;
                    background: url('../../assets/ui/ui (133).png')center no-repeat;
                    background-size: 100% 170%;
                    text-align: center;
                    font-size: 2vw;
                    line-height: 4vw;
                    color: rgb(175, 175, 175);
                }
                .config_container{
                    position: absolute; left: 0; top: 36vw;
                    width: 24vw; height: 50vw;
                    background: rgba(0, 0, 0, 0.4);
                    .tactics_container{
                        left: 6vw; top: 2vw;
                        width: 12vw; height: 36vw;
                        .tactics_box{
                            position: relative; float: left;
                            width: 12vw; height: 12vw;
                            font-size: 3vw;
                            margin: 0 0 4vw 0;
                            .divider{
                                top: 15vw; left: 0;
                                width: 12vw; height: .1vw;
                                background: gray;
                            }
                        }
                    }
                    
                }
                .artifact_box,.wait_load_artifact{
                    position: absolute; left: 0; top: 86vw;
                    width: 24vw; height: 6vw;
                    line-height: 6vw;
                    // background: url('../../assets/ui/ui\ \(172\).png')rgba(20, 0, 0, 0.8);
                    background: url('../../assets/ui/ui\ \(189\).png');
                    background-size: 100% 100%;
                    box-shadow: 0 10px 50px black;
                    .name{
                        left: 4.5vw; top: 0;
                        line-height: 6vw;
                        font-family: 'FZLBJW';
                    }
                    .art_up_sign{
                        left: .5vw; top: 1.3vw;
                        width: 3.4vw; height: 3.4vw;
                    }
                    .forge_eff{
                        right: .5vw; top: 1.6vw;
                        width: 10vw; height: 2.8vw;
                        line-height: 2.8vw;
                        .text{
                            width: 100%; height: 100%;
                            font-size: 3vw;
                            line-height: 2.8vw;
                            font-family: 'FZLBJW';
                        }
                    }
                    .add_sign{
                        right: 2vw; top: 1vw;
                        width: 4vw; height: 4vw;
                    }
                }
                .wait_load_artifact{
                    .name{
                        color: rgb(190, 190, 190);
                    }
                }
                
            }
            .warrior_card_ipt:nth-of-type(1){
                margin: 5vw 0 0 13vw;
            }
        }
        

        .delete_team_btn,.simulateBattle_btn{
            bottom: 5vw; left: 50%;
            width: 24vw;height: 6vw;
            margin: 0 0 0 -29vw;
            line-height: 6vw;
            background: url('../../assets/ui/ui (10).png')no-repeat;
            background-size: 100% 100%;
            color: black;
            font-weight: bold;
        }
        .delete_team_btn.click,.simulateBattle_btn.click{
            transform: scale(.8);
        }
        .simulateBattle_btn{
            margin: 0 0 0 5vw;
        }
    }

}
</style>