
<template>
    <div class="topic_title_container" ref="topic_title_container">
        <div class="topic_title_container_texture">
            <div class="title" :style="`font-size:${titleFontSize}vw`">{{ title }}</div>
        </div>
        <div class="topic_title_container_close_btn" ref="topic_title_container_close_btn"
        @touchstart="$refs.topic_title_container_close_btn.classList.add('click')"
        @touchend="$refs.topic_title_container_close_btn.classList.remove('click')" 
        @click="Close()"></div>
    </div>


</template>
<script>
export default {
    name : 'TopicTitle',
    props: {
        title: {
            type : String,
            default : "标 题"
        },
        titleFontSize:{
            type : Number,
            default : 4.7,
        },
        closeFunc : {
            type : Function,
            require : true,
        }
    },
    methods:{
        Close(){
            this.closeFunc()
        }
    },
    mounted(){
        this.$store.AutoStyle(this.$refs.topic_title_container)
    }
}
</script>
<style lang="less" scoped>
.topic_title_container{
    top: 0; left: 0;
    width: 100vw; height: 11vw;
    .topic_title_container_texture{
        left: 0; top: 3vw;
        width: 52vw;height: 11vw;
        background: url('../../assets/background/bg (5).png')no-repeat;
        background-size: 100% 100%;
        .title{
            left: 5vw; top: 0;
            width: 45vw; height: 11vw;
            line-height: 11vw;
            font-size: 4.5vw;
            text-align: left;
            color: rgb(193, 193, 82);
            text-shadow: 0 0 2px black;
            transition: all .3s;
            font-family: 'FZLBJW';
        }
    }
    .topic_title_container_close_btn{
        left: auto;right: 3vw; top: 4vw;
        height: 7vw; width: 7vw;
        background: url('../../assets/ui/ui (85).png')no-repeat;
        background-size: cover;
    }
    
}


.topic_title_container.pad{
    .topic_title_container_close_btn{
        cursor: pointer;
        &:hover{
            opacity: .8;
        }
    }
}
</style>