<template>
    <div class="artifact_info">
        <div class="mask cover" @click="Close"></div>
        <div class="container">
            <TopicTitle title="宝物详情" :closeFunc="Close" />

            <div class="work_place" v-if="$store.buffer.curArtifact">
                <div class="art_pre_view" >
                    <div class="art_card">
                        <ArtifactCard :artifactData="$store.buffer.curArtifact" />
                    </div>
                    <div class="art_info">
                        <div class="art_name">
                            <img :src="AritifactImgIcon" alt="" class="art_up_sign">
                            宝物名称：{{ $store.buffer.curArtifact.name }}
                        </div>
                        <div class="art_describe">
                            {{ $store.buffer.curArtifact.describe }}
                        </div>
                        <div class="art_type">
                            <div class="title">宝物类型</div>
                            <div class="value">{{ $store.buffer.curArtifact.type }}类</div>
                        </div>
                        <div class="art_eff_container">
                            <div class="eff_item" v-for="eff in $store.buffer.curArtifact.baseEffArr" :key="eff.index">
                                <div class="name">
                                    <img src="../../assets/ui/ui (182).png" class="cover" alt="">
                                    <div class="text">{{ eff.name }}</div>
                                </div>
                                <div class="eff_info">
                                    {{ eff.eff }}
                                </div>
                            </div>
                            <div class="eff_item">
                                <div class="name">
                                    <img :src="ForgeEffImgIcon" alt="" class="cover">
                                    <div class="text">{{ $store.buffer.curArtifact.forgeEffArr[$store.buffer.curArtifact.forgeChoseIdx].name }}</div>
                                </div>
                                <div class="eff_info">
                                    {{ $store.buffer.curArtifact.forgeEffArr[$store.buffer.curArtifact.forgeChoseIdx].eff[$store.buffer.curArtifact.effQualityIdx] }}
                                </div>
                            </div>
                        </div>
                       
                        
                        
                    </div>
                    
                </div>
                <div class="warrior_load" 
                v-if="$store.buffer.curArtifact.warriorId != -1">
                    <WarriorElongatedCard :warriorData="$store.warriors[$store.warriorIdxTable[$store.buffer.curArtifact.warriorId]]" card-style="2_1" />
                    <div class="text">配置中</div>
                </div>
                <div class="exchange_btn" ref="exchange_btn"
                @touchstart="$refs.exchange_btn.classList.add('click')"
                @touchend="$refs.exchange_btn.classList.remove('click')"
                v-if="routeArr[0] == 'battle' || $store.buffer.curArtifact.warriorId != -1 && routeArr.length > 1 && routeArr[routeArr.length-2] == 'warrior_info'"
                @click="ExchangeArtifact()">切换宝物</div>
                <div class="unload_btn" ref="unload_btn"
                @touchstart="$refs.unload_btn.classList.add('click')"
                @touchend="$refs.unload_btn.classList.remove('click')"
                v-if="ShowUnloadRule()"
                @click="UnloadArtifact()">卸载宝物</div>  
                <div class="delete_btn" ref="delete_btn"
                @touchstart="$refs.delete_btn.classList.add('click')"
                @touchend="$refs.delete_btn.classList.remove('click')"
                @click="DeleteArt()"
                v-show="routeArr[0] == 'my_inventory'"
                >移除宝物</div> 
            </div>
        </div>
    </div>
</template>

<script>
import ArtifactCard from '@/components/style/ArtifactCard.vue';
import WarriorElongatedCard from '@/components/style/WarriorElongatedCard.vue';
import Save from '@/util/save';
import TopicTitle from '@/components/style/TopicTitle.vue';
export default {
    name : 'ArtifactInfo',
    components:{
        TopicTitle,
        ArtifactCard,
        WarriorElongatedCard
    },
    data(){
        return{
            routeArr : [],
        }
    },
    computed:{
        AritifactImgIcon(){
            switch(this.$store.buffer.curArtifact.effQualityIdx){
                case 0 :
                case 1 : return require('../../assets/ui/ui (65).png');
                case 2 : return require('../../assets/ui/ui (73).png');
            }
        },
        ForgeEffImgIcon(){
            switch(this.$store.buffer.curArtifact.effQualityIdx){
                case 0 : return require('../../assets/ui/ui (182).png');
                case 1 : return require('../../assets/ui/ui (184).png');
                case 2 : return require('../../assets/ui/ui (183).png');
            }
        }
    },
    methods:{
        async DeleteArt(){
            for(let i in this.$store.userArtifacts){
                if(this.$store.userArtifacts[i] == this.$store.buffer.curArtifact){
                    this.$router.back();
                    if(this.$store.buffer.curArtifact.warriorId != -1){
                        this.$store.warriors[this.$store.warriorIdxTable[this.$store.buffer.curArtifact.warriorId]].artifact = null
                    }
                    this.$store.userArtifacts.splice(i,1);
                    let s = new Save(this.$store);
                    let success = await s.SaveArtifactData();
                    if(success){
                        this.$store.Alert('宝物删除成功');
                    }
                    else{
                        this.$store.Alert('宝物删除失败');
                    }
                }
            }
        },
        ShowUnloadRule(){
            if(this.routeArr[0] == 'my_inventory'){
                if(this.$store.buffer.curArtifact.warriorId != -1) return true;
                return false;
            }
            return true;
        },
        ExchangeArtifact(){
            // if(this.routeArr[this.routeArr.length -2] == '')
            this.$router.back();
            setTimeout(() => {
                this.$router.push({
                    path : `${this.$route.path}/artifact_list`,
                })
            }, 10);
        },
        UnloadArtifact(){
            this.$store.buffer.curWarrior.UnloadArtifact();
            this.$store.dirty.warrior = true;
            this.Close();
        },
        Close(){
            this.$router.back()
        },
    },
    mounted(){
        let path = this.$route.path
        this.routeArr = path.split("/")
        this.routeArr.shift()

        // console.log('aaa',this.$store.warriors[this.$store.warriorIdxTable[88]])
        // console.log('aaa',this.$store.buffer.curArtifact.warriorId)
    }
}
</script>

<style lang="less" scoped>
.artifact_info{
    position: absolute;
    left: 0; top : 0;
    width: 100vw;height: 100vh;
    .mask{
        background: radial-gradient(rgba(0, 0, 0, 0.5),rgb(0, 0, 0));
    }
    .container{
        position: absolute;
        left: 0; top: 50%;
        width: 100vw;height: 100vw;
        margin: -50vw 0 0 0;
        background: url('../../assets/background/bg\ \(7\).png')#393b3e bottom right no-repeat;
        background-size: cover;
        .work_place{
            position: absolute; left: 0; top: 12vw;
            width: 100vw; height: 82vw;
            .art_pre_view{
                position: absolute;
                left: 5vw; top: 5vw;
                width: 90vw; height: 60vw;
                .art_card{
                    position: absolute;
                    left: 0; top: 2vw;
                    width: 24vw; height: 42vw;
                    font-size: 4vw;
                }
                
                .art_info{
                    position: absolute;
                    left: 30vw; top: 0;
                    width: 60vw; height: 100%;
                    // background: green;
                    
                    .art_name{
                        position: relative; float: left;
                        // position: absolute;left: 0; top: 0;
                        width: 100%; height: 8vw;
                        line-height: 8vw;
                        text-align: left;
                        font-size: 4vw;
                        border-bottom: .1vw solid gray;
                        // background: #000;
                        font-family: 'FZLBJW';
                        .art_up_sign{
                            position: absolute;right: 0; top: 1vw;
                            width: 6vw; height: 6vw;
                        }
                    }
                    .art_describe{
                        position: relative; float: left;
                        margin: 2vw 0 2vw 0;
                        width: 100%;  
                        font-family: 'FZLBJW';
                        text-align: left;
                        font-size: 2.5vw;
                        color: white;
                        // background: #000;
                    }
                    .art_type{
                        position: relative; float: left;
                        width: 100%; height: 5vw;
                        .title,.value{
                            position: relative; float: left;
                            text-align: left;
                            width: 12vw; height: 5vw;
                            font-size: 2.5vw;
                        }
                        .value{
                            color: white;
                        }
                    }
                    .art_eff_container{
                        position: relative; float: left;
                        // top: 8vw; left: 0; position: absolute;
                        margin: 3vw 0 0 0;
                        width: 100%; 
                        // height: 36vw;
                        // background: #000;
                        
                        .eff_item{
                            position: relative; display: inline-block;
                            width: 100%; 
                            margin: 0 0 1vw 0;
                            color: white;
                            .name{
                                margin: .5vw 1vw 0 0;
                                position: relative; float: left;
                                width: 11vw; height: 3vw;
                                .text{
                                    width: 100%; height: 100%;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    font-size: 3vw;
                                    font-family: 'FZLBJW';
                                }
                            }
                            .eff_info{
                                position: relative; float: left;
                                
                                width: 48vw;
                                line-height: 4vw;
                                text-align: left;
                                font-size: 2.8vw;
                            }
                        }
                    }
                    
                }
                
            }
            .warrior_load{
                bottom: 12vw; left: 5vw;
                width: 24vw; height: 12vw;
                background: #000;
                .text{
                    top: 12vw;
                    width: 100%; height: 3vw;
                    line-height: 3vw;
                    font-family: "FZLBJW";
                    color: rgb(0, 178, 0);
                    text-shadow: 1px 1px 1px black;
                    // background: radial-gradient(rgba(0, 0, 0, 0.8),rgba(0, 0, 0,0));
                }
            }
            .exchange_btn,.unload_btn,.delete_btn{
                position: absolute; bottom: 0; left: 5vw;
                width: 16vw; height: 5vw;
                background: white;
                color: black;
                font-weight: bold;
                line-height: 5vw;
            }
            .exchange_btn{
                left: 27vw;
            }
            .delete_btn{
                right: 5vw; left: auto;
            }
        }
    }
    
}

</style>