<script>
import WarriorElongatedCard from '@/components/style/WarriorElongatedCard.vue';
import TopNav from '@/components/container/TopNav.vue';
import Save from '@/util/save';
export default {
    name :'BattleReport',
    components : {
        TopNav,
        WarriorElongatedCard
    },
    data(){
        return{
            warriorLevelList:[20,25,30,35,40,45,50],
            
        }
    },
    beforeCreate(){

    },
    methods:{
        ReturnDefConfig(){
            let that = this;
            let f = function(){
                that.$store.battle.setting.ReturnDef();
                that.$store.Alert('已恢复默认设置');
                let s = new Save(that.$store);
                s.SaveBattleSettingData();
            }
            let params = {
                msg : `是否将所有配置恢复到默认状态`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        },
        SaveConfig(){
            let s = new Save(this.$store);
            s.SaveBattleSettingData();
            this.$store.Alert('保存成功');
        },
        AdjustGiantWarriorStatueLevel(target,isAdd){
            if(isAdd && target.giantWarriorStatueLevel < 5) target.giantWarriorStatueLevel += 1;
            else if(!isAdd && target.giantWarriorStatueLevel > 0){
                target.giantWarriorStatueLevel -= 1;
            }
        },
        AdjustTacticsFormationLevel(target,isAdd){
            if(isAdd && target.tacticsFormationLevel < 5) target.tacticsFormationLevel += 1;
            else if(!isAdd && target.tacticsFormationLevel > 0){
                target.tacticsFormationLevel -= 1;
            }
        },
        AdjustBarracksLevel(target,isAdd){
            if(isAdd && target.barracksLevel < 20) target.barracksLevel += 1;
            else if(!isAdd && target.barracksLevel > 0){
                target.barracksLevel -= 1;
            }
        },
        AdjustFourMajorCampsLevel(target,whichMajor,isAdd){
            if(isAdd && target.fourMajorCampsLevel[whichMajor] < 10) target.fourMajorCampsLevel[whichMajor] += 1;
            else if(!isAdd && target.fourMajorCampsLevel[whichMajor] > 0){
                target.fourMajorCampsLevel[whichMajor] -= 1;
            }
        },
        AdjustMoral(target,isAdd){
            if(isAdd && target.defMorale < 160) target.defMorale += 10;
            else if(!isAdd && target.defMorale > 0){
                target.defMorale -= 10;
            }
        }
    },
    mounted(){
        
    }
}
</script>

