/* eslint-disable */
import innateTacticsData from '../data/innate_tactics.json'
import optionalTactics from '../data/optional_tactics.json'
import Tactics from './tactics.js'
import armsSpecData from '../data/arms_spec.json'
import armsCommonSpecData from '../data/arms_common_spec.json'
import Artifact from './artifact'
//app的核心武将对象
class Warrior {
    constructor(data) {
        //--------------------------------------------标识信息----------------------------------------------
        //ID
        this.id = data ? data.id : -1;
        //阵营
        this.camp = data ? data.camp : "";
        //名字
        this.name = data ? data.name : "";
        //统帅
        this.cost = data ? data.cost : 0;
        //性别
        this.gender = data ? data.gender : 0;
        //兵种
        this.arms = data ? data.arms : "";
        //攻击范围
        this.range = data ? data.range : 0;
        //高级兵种数组
        this.upArms = data ? data.up_arms : [];
        //高级兵种特性
        this.armsSpec = []
        this.armsCommonSpec = []
        //标记
        this.sign = data ? data.sign : "";

        //--------------------------------------------战法宝物信息----------------------------------------------
        this.tacticsArr = [
            {} = new Tactics(),
            {} = new Tactics(),
            {} = new Tactics()
        ]
        this.artifact = null;
        //--------------------------------------------对战属性信息----------------------------------------------
        //等级
        this.level = 50;
        //属性
        this.attr = {
            ad: data ? data.attr.ad : 0,
            de: data ? data.attr.de : 0,
            tact: data ? data.attr.tact : 0,
            speed: data ? data.attr.speed : 0,
            demolition: data ? data.attr.demolition : 0,
            moral: 100,
        }
        //成长属性
        this.grow = {
            ad: data ? data.grow.ad : 0,
            de: data ? data.grow.de : 0,
            tact: data ? data.grow.tact : 0,
            speed: data ? data.grow.speed : 0,
            demolition: data ? data.grow.demolition : 0,
            moral: 0,
        }
        //加点属性
        this.plus = {
            ad: 0,
            de: 0,
            tact: 0,
            speed: 0,
        }
        //--------------------------------------------图像信息----------------------------------------------
        //皮肤
        this.skin = data ? data.skin : [];
        //图像
        this.img = {
            body: data ? require(`../static/card_0/${data.img}`) : require(`../static/card_0/warrior (1000).jpeg`),
            preview: data ? require(`../static/card_1/${data.img}`) : require(`../static/card_1/warrior (1000).jpeg`),
            head: data ? require(`../static/card_2/${data.img}`) : require(`../static/card_2/warrior (1000).jpeg`),
            banner: data ? require(`../static/card_3/${data.img}`) : require(`../static/card_3/warrior (1000).jpeg`),
            post: data ? require(`../static/card_4/${data.img}`) : require(`../static/card_4/warrior (1000).jpeg`)
        }
        //原画
        this.originSkin = this.img;
        //--------------------------------------------资产信息----------------------------------------------
        //是否拥有
        this.own = false;
        //是否觉醒
        this.isUp = false;
        //是否深造
        this.isUpWork = false;
        //是否进阶兵种
        this.isUpArms = false;
        //红度
        this.red = 0;


        //--------------------------------------------控制信息----------------------------------------------
        //是否展示
        this.isShow = true;
        //是否被配置到队伍中
        this.configTeamIdx = -1;
        //当前选择的画像
        this.skinChoseIdx = 0;//0表示选择原画
        //高级兵种的选择索引
        this.choseUpArmsIdx = 0;
        //兵种特性选择则
        this.choseArmsCharacterIdx = [0, 1];
        //宝物选择
        this.choseArtifactId = "";
    }
    InitData(data) {
        this.id = data ? data.id : -1;
        //阵营
        this.camp = data ? data.camp : "";
        //名字
        this.name = data ? data.name : "";
        //统帅
        this.cost = data ? data.cost : 0;
        //性别
        this.gender = data ? data.gender : 0;
        //兵种
        this.arms = data ? data.arms : "";
        //攻击范围
        this.range = data ? data.range : 0;
        //高级兵种数组
        this.upArms = data ? data.up_arms : [];
        //高级兵种特性
        this.armsSpec = []
        this.armsCommonSpec = []
        //标记
        this.sign = data ? data.sign : "";

        //--------------------------------------------战法信息----------------------------------------------
        this.tacticsArr = [
            {} = new Tactics(),
            {} = new Tactics(),
            {} = new Tactics()
        ]
        //--------------------------------------------对战属性信息----------------------------------------------
        //等级
        this.level = 50;
        //属性
        this.attr = {
            ad: data ? data.attr.ad : 0,
            de: data ? data.attr.de : 0,
            tact: data ? data.attr.tact : 0,
            speed: data ? data.attr.speed : 0,
            demolition: data ? data.attr.demolition : 0,
            moral: 100,
        }
        //成长属性
        this.grow = {
            ad: data ? data.grow.ad : 0,
            de: data ? data.grow.de : 0,
            tact: data ? data.grow.tact : 0,
            speed: data ? data.grow.speed : 0,
            demolition: data ? data.grow.demolition : 0,
            moral: 0,
        }
        //加点属性
        this.plus = {
            ad: 0,
            de: 0,
            tact: 0,
            speed: 0,
        }
        //--------------------------------------------图像信息----------------------------------------------
        //皮肤
        this.skin = data ? data.skin : [];
        //图像
        this.img = {
            body: data ? require(`../static/card_0/${data.img}`) : require(`../static/card_0/warrior (1000).jpeg`),
            preview: data ? require(`../static/card_1/${data.img}`) : require(`../static/card_1/warrior (1000).jpeg`),
            head: data ? require(`../static/card_2/${data.img}`) : require(`../static/card_2/warrior (1000).jpeg`),
            banner: data ? require(`../static/card_3/${data.img}`) : require(`../static/card_3/warrior (1000).jpeg`),
            post: data ? require(`../static/card_4/${data.img}`) : require(`../static/card_4/warrior (1000).jpeg`)
        }
        //原画
        this.originSkin = this.img;
        //--------------------------------------------资产信息----------------------------------------------
        //是否拥有
        this.own = false;
        //是否觉醒
        this.isUp = false;
        //是否深造
        this.isUpWork = false;
        //是否进阶兵种
        this.isUpArms = false;
        //红度
        this.red = 0;


        //--------------------------------------------控制信息----------------------------------------------
        //是否展示
        this.isShow = true;
        //是否被配置到队伍中
        this.configTeamIdx = -1;
        //当前选择的画像
        this.skinChoseIdx = 0;//0表示选择原画
        //高级兵种的选择索引
        this.choseUpArmsIdx = 0;
        //兵种特性选择则
        this.choseArmsCharacterIdx = [0, 1];
    }
    CopyData(data) {
        const isObject = (value) => typeof value === 'object' && value !== null;

        const deepAssign = (target, source) => {
            // 处理数组情况：先清空目标数组，再逐个深复制元素
            if (Array.isArray(source)) {
                // 确保目标是数组
                if (!Array.isArray(target)) {
                    target = [];
                } else {
                    // 清空目标数组（保留引用但清除旧元素）
                    target.length = 0;
                }
                // 逐个深复制数组元素
                source.forEach(item => {
                    target.push(isObject(item)
                        ? deepAssign(Array.isArray(item) ? [] : {}, item)
                        : item
                    );
                });
                return target;
            }

            // 处理对象情况
            if (isObject(source)) {
                // 确保目标是对象
                if (!isObject(target) || Array.isArray(target)) {
                    target = {};
                }

                // 遍历对象属性
                for (const key in source) {
                    // 只处理自身属性（排除原型链）
                    if (source.hasOwnProperty(key)) {
                        if (isObject(source[key])) {
                            // 递归深合并子对象/数组
                            target[key] = deepAssign(target[key], source[key]);
                        } else {
                            // 基本类型直接赋值
                            target[key] = source[key];
                        }
                    }
                }
                return target;
            }

            // 基本类型直接覆盖
            return source;
        };

        // 使用方式：先清空实例再合并
        Object.keys(this).forEach(key => delete this[key]);
        deepAssign(this, data);


        // 3. 兜底默认值（确保关键属性存在）
        this.attr = this.attr || {
            ad: 0, de: 0, tact: 0, speed: 0, demolition: 0, moral: 100
        };
        this.grow = this.grow || {
            ad: 0, de: 0, tact: 0, speed: 0, demolition: 0, moral: 100
        };

    }
    Init(data) {
        this.GetInnateTactics(data.tactics_idx)
        this.GetArmsSpec()
        this.GetSkinArr(data)
    }
    CalculateRemainingPoints(){
        let sum = 10
        let attrMap = {
            "攻击":"ad",
            "防御":"de",
            "策略":"tact",
            "速度":"speed",
            "攻城":"demolition",
        }
        if(this.gender == '女') sum = 15
        sum = sum * Math.floor(this.level/10)
        sum += this.red * 10
        for(let attr in attrMap){
            sum -= this.plus[attrMap[attr]] || 0
        }
        return sum;
    }
    //初始化模拟对局中武将
    InitMockBattleConfig(data, setting) {
        this.red = 0;
        this.level = setting.warriorDefLevel;
        this.isUp = true;
        this.isUpArms = true;
        this.isUpWork = false;
        for (let i = 0; i < this.tacticsArr.length; i++) {
            if (i > 0) {
                this.tacticsArr[i] = new Tactics()
            }
        }
        this.GetArmsSpec()
        this.GetSkinArr(data)
    }
    //获取自带战法
    GetInnateTactics(tactIdx) {
        let tact = new Tactics(innateTacticsData[tactIdx])
        this.tacticsArr[0] = tact
    }
    //获取兵种特性
    GetArmsSpec() {
        this.armsSpec = []
        this.armsCommonSpec = []
        //安装高级兵种自带特性
        for (let j = 0; j < 2; j++) {
            let tempArr = []
            for (let i in armsSpecData) {
                if (armsSpecData[i].arm_belong == this.upArms[j]) {
                    let item = {
                        name: armsSpecData[i].name,
                        type: armsSpecData[i].arm_type,
                        belong: armsSpecData[i].arm_belong,
                        describe: armsSpecData[i].describe,
                    }
                    tempArr.push(item)
                }
            }
            this.armsSpec.push(tempArr)
        }
        //安装高级兵种可选特性
        for (let j = 0; j < 2; j++) {
            let tempArr = []
            let dataArr = armsCommonSpecData[this.armsSpec[j][0].type]
            for (let i in dataArr) {
                let item = {
                    name: dataArr[i].name,
                    type: dataArr[i].arm_type,
                    belong: dataArr[i].arm_belong,
                    describe: dataArr[i].describe,
                }
                tempArr.push(item)
            }
            this.armsCommonSpec.push(tempArr)
        }
    }
    //获取画像列表
    GetSkinArr(warriorData) {
        this.skin = []
        this.skin.push(warriorData.img);
        for (let i in warriorData.skin) {
            let newStr = warriorData.skin[i];
            this.skin.push(newStr);
        }
    }
    //切换画像
    ChangePortrayal(idx) {
        let tgt = this.skin[idx];
        this.skinChoseIdx = idx;
        //图像
        this.img = {
            body: require(`../static/card_0/${tgt}`),
            preview: require(`../static/card_1/${tgt}`),
            head: require(`../static/card_2/${tgt}`),
            banner: require(`../static/card_3/${tgt}`),
            post: require(`../static/card_4/${tgt}`)
        }
    }
    //切换高级兵种选择
    ExchangeArms(index) {
        this.choseUpArmsIdx = index
    }
    //配置战法
    LoadTactics(idx, tactData, link = true) {
        if (link) {
            tactData.isload = this.id
            this.tacticsArr[idx] = tactData
        }
        else {
            this.tacticsArr[idx].CopyData(tactData)
        }
    }
    //配置初始状态战法
    LoadNativeTactics(idx, tactData) {
        for (let i in optionalTactics) {
            if (optionalTactics[i].id == tactData.id) {
                let new_tact = new Tactics(optionalTactics[i]);
                this.tacticsArr[idx] = new_tact;
            }
        }

    }
    //卸载战法
    UnLoadTactics(idx, tactData) {
        tactData.isload = -1
        this.tacticsArr[idx] = new Tactics()
    }
    //卸载宝物
    UnloadArtifact() {
        this.artifact.warriorId = -1;
        this.artifact = null;
    }
    //加载配置数据
    LoadConfigData(data, inheritTactics = true) {
        // this = data
        this.CopyData(data)
        this.red = 0
        if (!inheritTactics) {
            this.tacticsArr[1] = new Tactics()
            this.tacticsArr[2] = new Tactics()
        }
    }

    //从主数据库中配将数据恢复数据
    DataRecovery(data, originTacticsList) {
        this.own = true;
        this.red = data.red;
        this.isUp = data.isUp;
        this.isUpWork = data.isUpWork;
        this.isUpArms = data.isUpArms;
        this.choseUpArmsIdx = data.choseUpArmsIdx || 0;
        this.choseArmsCharacterIdx = data.choseArmsCharacterIdx || [0, 1];
        data.skinChoseIdx != 0 ? this.ChangePortrayal(data.skinChoseIdx) : null;
        //恢复战法
        for (let i = 0; i < data.tacticsConfig.length; i++) {
            if (data.tacticsConfig[i] != -1) {
                for (let j in optionalTactics) {
                    if (data.tacticsConfig[i] == optionalTactics[j].id) {
                        for (let k in originTacticsList[optionalTactics[j].type]) {
                            if (originTacticsList[optionalTactics[j].type][k].id == data.tacticsConfig[i]) {
                                this.LoadTactics(i + 1, originTacticsList[optionalTactics[j].type][k])
                            }
                        }
                    }
                }
            }
        }
    }
    //从BATLLE数据库中阵容数据恢复数据
    DataRecoveryFromLineup(linuepData, warriorData, artifactOriginDataList, baseeEffMap, forgeEffMap) {
        this.Init(warriorData)
        for (let key in linuepData.plus) {
            this.plus[key] = linuepData.plus[key];
        }
        this.red = linuepData.red;
        this.isUp = linuepData.isUp;
        this.isUpArms = linuepData.isUpArms;
        this.choseUpArmsIdx = linuepData.choseUpArmsIdx || 0;
        this.choseArmsCharacterIdx = linuepData.choseArmsCharacterIdx || [0, 1];
        //恢复战法
        for (let i = 0; i < linuepData.tacticsConfig.length; i++) {
            if (linuepData.tacticsConfig[i].id != -1) {
                for (let j in optionalTactics) {
                    if (linuepData.tacticsConfig[i].id == optionalTactics[j].id) {
                        let newTactics = new Tactics(optionalTactics[j]);
                        this.tacticsArr[i + 1] = newTactics;
                    }
                }
            }
            else if (linuepData.tacticsConfig[i].id == -1) {
                let newTactics = new Tactics();
                this.tacticsArr[i + 1] = newTactics;
            }
        }
        //恢复宝物的引用
        if (linuepData.artifactConfig != null) {
            for (let i in artifactOriginDataList) {
                if (artifactOriginDataList[i].name == linuepData.artifactConfig.name) {
                    let newArt = new Artifact(artifactOriginDataList[i], baseeEffMap, forgeEffMap)
                    newArt.effQualityIdx = linuepData.artifactConfig.effQualityIdx
                    newArt.forgeChoseIdx = linuepData.artifactConfig.forgeChoseIdx
                    this.artifact = newArt;
                }
            }
        }
        // if()
    }
}

export default Warrior



