                        background-size: cover;
                    }
                }
                .tactics_box{
                    left: 48vw; top: 1.5vw;
                    width: 48vw; height: 14vw;
                    // background: red;
                    .tactics_slot{
                        position: relative; float: left;
                        width: 12vw; height: 12vw;
                        margin: 0 0 0 1vw;
                    }
                }
                .artifact_config{
                    right: 0; top: 0;
                    width: 12vw; height: 16vw;
                    background: url('../../assets/ui/ui\ \(187\).png');
                    background-size:100% 100% ;
                    font-size: 1.7vw;
                    .text{
                        left: 0; bottom: 1vw;
                        width: 100%; height: 5vw;
                        line-height: 5vw;
                        font-family: 'FZLBJW';
                        color: rgb(154, 154, 154);
                    }
                }
            }
        }
    }
    .camp_addition_panel{
        position: absolute; left: 0;top: 12vh;
        transform: translateY(77vw);
