            font-weight: bold;
            background: rgb(226, 226, 226);
            color: black;
        }
        .setting_btn{
            left: 3vw; right: auto;
        }
        .clear_team_btn{
            right: 25vw;
        }
        .pic{
            right: 0;top: 0;
            width: 50vw; height: 12vw;
        }
        .camp_bg{
            position: absolute;
            left: 0; top: 0;
            width: 100vw; height: 12vw;
            background: #000;
            &.wei_bg{
                background: linear-gradient(to right bottom,@color_wei_dark,rgba(0,0,0,0),rgba(0,0,0,0));
            }
            &.shu_bg{
                background: linear-gradient(to right bottom,@color_shu_dark,rgba(0,0,0,0),rgba(0,0,0,0));
            }
            &.wu_bg{
                background: linear-gradient(to right bottom,@color_wu_dark,rgba(0,0,0,0),rgba(0,0,0,0));
