}



.battle_main.pad{
    .select_my_team,.select_enemy_team{
        position: absolute; left: 0; top: 15vh;
        margin: 0 0 0 0;
        width: 22vh;height: 4vh;
        line-height: 4vh;
        font-size: 1.8vh;
        cursor: pointer;
        &:hover{
            color: white;
        }
    }
    .select_enemy_team{
        background: url('../../assets/ui/ui (57).png')no-repeat;
        background-size: 100% 100%;
        left: auto; right: 0;
    }
    .select_my_team.current,.select_enemy_team.current{
        color: white;
        background-size: 100% 100%;
    }
    .texture{
        position: absolute; left: 50%; top : 13vh;
        margin: 0 0 0 -2.5vh;
        width: 5vh;height: 7.5vh;
        background: url('../../assets/background/bg (34).png')no-repeat;
        background-size: 100% 100%;
    }
    //武将调整容器
    .visual_container{
        position: absolute; left: 0; top: 10vh;
        margin: 11vh 0 0 0;
        width: 200vw;height: 60vw;
        transition: all .2s;
        .team_container{
            float: left;position: relative;
            width: 100vw;height: 30vh;
            .warrior_config{
                position: relative;float: left;
                margin-top:1vh;
                width: 100vw;height: 8vh;
                background: rgb(37, 37, 37);
                background-size: cover;
                box-shadow: 0 0 2px black;
                .warrior_box{
                    position: absolute; left: 0; top: 0vw;
                    width: 23vh;height: 8vh;
                    overflow: hidden;
                    background: url('../../assets/background/bg (21).png') center no-repeat;
                    background-size: cover;
                    transition: all .1s;
