<template>
    <div class="art_page" ref="art_page">
        <div class="work_place" v-if="$store.buffer.curConfigArt">
            <div class="art_pre_view" >
                <div class="art_card">
                    <img :src="$store.buffer.curConfigArt.img" alt="" class="cover">
                    <img src="../../assets/ui/ui (185).png" alt="" class="cover">
                </div>
                <div class="arm_describe">
                    {{ $store.buffer.curConfigArt.describe }}
                </div>
                <div class="art_info">
                    <div class="art_name">
                        <img :src="AritifactImgIcon" alt="" class="art_up_sign">
                        宝物名称：{{ $store.buffer.curConfigArt.name }}
                    </div>
                    <div class="arm_eff_container">
                        <div class="eff_item" v-for="eff in $store.buffer.curConfigArt.baseEffArr" :key="eff.index">
                            <div class="name">
                                <img src="../../assets/ui/ui (182).png" class="cover" alt="">
                                <div class="text">{{ eff.name }}</div>
                            </div>
                            <div class="eff_info">
                                {{ eff.eff }}
                            </div>
                        </div>
                        <div class="eff_item">
                            <div class="name">
                                <img :src="ForgeEffImgIcon" alt="" class="cover">
                                <div class="text">{{ $store.buffer.curConfigArt.forgeEffArr[$store.buffer.curConfigArt.forgeChoseIdx].name }}</div>
                            </div>
                            <div class="eff_info">
                                {{ $store.buffer.curConfigArt.forgeEffArr[$store.buffer.curConfigArt.forgeChoseIdx].eff[$store.buffer.curConfigArt.effQualityIdx] }}
                            </div>
                        </div>
                    </div>
                    <div class="forge_control">
                        <div :class="`forge_item ${$store.buffer.curConfigArt.forgeChoseIdx != fgIndex ? 'filter_gray' : ''}`" v-for="fg,fgIndex in $store.buffer.curConfigArt.forgeEffArr" :key="fg.index"
                        @click="$store.buffer.curConfigArt.forgeChoseIdx = fgIndex"
                        >
                            <img :src="ForgeEffImgIcon" alt="" class="cover">
                            <div class="text">{{ fg.name }}</div>
                            <div class="mask"></div>
                        </div>
                    </div>
                    <div class="forge_quality_btn_container">
                        <div :class="`btn ${bId == $store.buffer.curConfigArt.effQualityIdx?'current':''}`" ref="btn"
                        @touchstart="$refs.btn[bId].classList.add('click')"
                        @touchend="$refs.btn[bId].classList.remove('click')" 
                        v-for="b,bId in $store.buffer.curConfigArt.effQuality" :key="b.index"
                        @click="$store.buffer.curConfigArt.effQualityIdx = bId">
                            {{ b }}
                        </div>
                        <div class="push_btn" ref="push_btn"
                        @touchstart="$refs.push_btn.classList.add('click')"
                        @touchend="$refs.push_btn.classList.remove('click')" 
                        @click="AddArtifact()">添加宝物</div>
                    </div>
                </div>
                
            </div>
        </div>
        <div :class="`all_art_container ${$store.buffer.curConfigArt ? '':'action'}`">
            <div class="art" ref="art"
            @touchstart="$refs.art[aIndex].classList.add('click')"
            @touchend="$refs.art[aIndex].classList.remove('click')"
            v-for="a,aIndex in $store.artifacts" :key="a.index"
            @click="SelectArtifact(a)">
                <div class="art_img_box">
                    <img :src="a.img" alt="" class="art_img">
                    <img src="../../assets/background/bg (37).png" alt="" class="cover">
                </div>
                
            </div>
        </div>
        <TopNav title="自定义宝物" />
    </div>
</template>

<script>
import TopNav from '@/components/container/TopNav.vue';
import Artifact from '@/util/artifact';
import Save from '@/util/save';
export default {
    name : 'ArtifactsConfig',
    components:{
        TopNav
    },
    data(){
        return{
           
        }
    },
    computed:{
        AritifactImgIcon(){
            switch(this.$store.buffer.curConfigArt.effQualityIdx){
                case 0 :
                case 1 : return require('../../assets/ui/ui (65).png');
                case 2 : return require('../../assets/ui/ui (73).png');
            }
        },
        ForgeEffImgIcon(){
            switch(this.$store.buffer.curConfigArt.effQualityIdx){
                case 0 : return require('../../assets/ui/ui (182).png');
                case 1 : return require('../../assets/ui/ui (184).png');
                case 2 : return require('../../assets/ui/ui (183).png');
            }
        }
    },
    methods:{
        SelectArtifact(art){
            this.$store.buffer.curConfigArt = art;
        },
        async AddArtifact(){
            let art = new Artifact();
            art.CopyData(this.$store.buffer.curConfigArt);
            art.GetId();
            art.warriorId = -1;
            this.$store.userArtifacts.unshift(art);
            this.$router.back();
            let s = new Save(this.$store);
            let success = await s.SaveArtifactData();
            if(success){
                this.$store.Alert('宝物添加成功');
            }
            else{
                this.$store.Alert('宝物添加失败');
            }
        }
    },
    mounted(){
        this.$store.AutoStyle(this.$refs.art_page)
    }

}
</script>

<style lang="less" scoped>
.art_page{
    position: absolute;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: url('../../assets/background/bg (1).jpg') no-repeat;
    background-size: cover;
    //变灰
    .filter_gray{
        filter: grayscale(1);
    }
    .work_place{
        position: absolute;
        top: 12vh; left: 0;
        margin-top: 0vw;
        width: 100vw; height: 80vw;
        .art_pre_view{
            position: absolute;
            left: 5vw; top: 5vw;
            width: 90vw; height: 60vw;
            // background: red;
            .art_card{
                position: absolute;
                left: 0; top: 0;
                width: 24vw; height: 42vw;
                background: #000;
            }
            .arm_describe{
                position: absolute;
                left: 0; top: 45vw;
                width: 26vw;  
                font-family: 'FZLBJW';
                text-align: left;
                font-size: 2.5vw;
                // background: #000;
            }
            
            .art_info{
                position: absolute;
                left: 30vw; top: 0;
                width: 60vw; height: 100%;
                // background: green;
                
                .art_name{
                    position: relative; float: left;
                    // position: absolute;left: 0; top: 0;
                    width: 100%; height: 8vw;
                    line-height: 8vw;
                    text-align: left;
                    font-size: 4vw;
                    border-bottom: .1vw solid gray;
                    // background: #000;
                    font-family: 'FZLBJW';
                    .art_up_sign{
                        position: absolute;right: 0; top: 1vw;
                        width: 6vw; height: 6vw;
                    }
                }
                .arm_eff_container{
                    position: relative; float: left;
                    // top: 8vw; left: 0; position: absolute;
                    margin: 3vw 0 0 0;
                    width: 100%; 
                    // height: 36vw;
                    // background: #000;
                    
                    .eff_item{
                        position: relative; display: inline-block;
                        width: 100%; 
                        margin: 0 0 1vw 0;
                        color: white;
                        .name{
                            margin: .5vw 0 0 0;
                            position: relative; float: left;
                            width: 12vw; height: 3vw;
                            .text{
                                width: 100%; height: 100%;
                                line-height: 3vw;
                                font-size: 3vw;
                                font-family: 'FZLBJW';
                            }
                        }
                        .eff_info{
                            position: relative; float: left;
                            // position: absolute; left: 12vw; top: 0;
                            width: 48vw;
                            line-height: 4vw;
                            text-align: left;
                            font-size: 2.8vw;
                        }
                    }
                }
                .forge_control{
                    position: relative; float: left;
                    margin-top: 2vw;
                    width: 100%; height: 12vw;
                    background: #0000004d;
                    color: white;
                    .forge_item{
                        position: relative; float: left;
                        width: 12vw; height: 3vw;
                        line-height: 3vw;
                        margin: 2vw 0 0 2.4vw;
                        .text{
                            position: absolute; left: 0; top: 0;
                            width: 12vw; height: 3vw;
                            line-height: 3vw;
                            font-family: 'FZLBJW';
                        }
                        
                    }
                }
                .forge_quality_btn_container{
                    position: relative; float: left;
                    margin-top: 2vw;
                    width: 100%; height: 12vw;
                    .btn{
                        position: relative; float: left;
                        width: 12vw; height: 4vw;
                        line-height: 4vw;
                        margin: 2vw 3vw 0 0;
                        background: white;
                        color: rgb(45, 45, 45);
                    }
                    .btn.current{
                        font-weight: bold;
                        color: black;
                    }
                    .push_btn{
                        position: relative; float: left;
                        margin: 2vw 0 0 0;
                        padding: 3vw 3vw;
                        width: 8vw; height: 6vw;
                        color: white;
                        line-height: 3vw;
                        background: url('../../assets/ui/ui.jpg');
                        background-size: 100% 100%;
                    }
                }
            }
            
        }
    }
    .all_art_container{
        position: absolute;
        top: 12vh; left: 5vw;
        margin-top: 85vw;
        padding: 2vw 0;
        width: 90vw; height: 95vw;
        overflow-y: scroll;
        overflow-x: hidden;
        transition: all .3s ease-out;
        .art{
            position: relative; float: left;
            width: 16vw; height: 20vw;
            margin: 0 0 1.6vw 1.6vw;
            background: #000;
            .art_img_box{
                left: 0; top: 0; position: absolute;
                width: 100%; height: 100%;
                overflow: hidden;
                .art_img{
                    position: absolute;
                    top: -4vw; left: 0;
                    width: 16vw; height: 28vw;
                }
            }
        }

    }
    .all_art_container.action{
        margin-top: 15vw;
        height: 125vw;
    }
}
.art_page.pad{
    .work_place{
        top: 10vh;
        .art_pre_view{
            .art_info{
                .forge_control{
                    .forge_item{
                        cursor: pointer;
                    }
                    .forge_item:hover{
                        color: rgb(213, 213, 213);
                    }
                }
                .forge_quality_btn_container{
                    position: relative; float: left;
                    margin-top: 2vw;
                    width: 100%; height: 12vw;
                    .btn{
                        cursor: pointer;
                    }
                    .btn:hover{
                        color: white;
                        background: gray;
                    }
                    .push_btn{
                        cursor: pointer;
                    }
                    .push_btn:hover{
                        transform: scale(1.1);
                    }
                }
            }
            
        }
    }
    .all_art_container{
        margin-top: 70vw;
        width: 90vw; height: 35vh;
        .art{
            position: relative; float: left;
            width: 11vw; height: 16vw;
            margin: 0 0 1.6vw 1.6vw;
            background: #000;
            .art_img_box{
                width: 100%; height: 100%;
                overflow: hidden;
                cursor: pointer;
                .art_img{
                    position: absolute;
                    top: -1vw; left: 0;
                    width: 11vw; height: 19vw;
                }
            }
            .art_img_box:hover{
                box-shadow: 0 0 10px white;
            }
        }
    }
    .all_art_container.action{
        margin-top: 15vw;
        height: 30vh;
    }
}
</style>