<template>
    <div class="tactics_icon_container">
        <img src="../../assets/ui/ui (103).png" class="tactics_icon_container_tactics_image" alt="">
        <div class="tactics_icon_container_tactics_name" v-if="tacticsData.id == -1">
            <div class="tactics_icon_container_tactics_name_text">未装配</div>
        </div>
        <img :src="GetTactImgUrl(tacticsData)" alt="" class="tactics_icon_container_tactics_image" v-if="tacticsData.id != -1">
        <img :src="GetQualityFrame(tacticsData)" class="tactics_icon_container_tactics_frame" v-if="tacticsData.id != -1">
        <img :src="GetQualitySign(tacticsData)" class="tactics_icon_container_tactics_sign" v-if="tacticsData.id != -1">
        <div class="tactics_icon_container_tactics_name" v-if="tacticsData.id != -1">
            <img :src="GetQualityIcon(tacticsData)" class="cover" alt="">
            <div class="tactics_icon_container_tactics_name_text">{{ tacticsData.name }}</div>
        </div>
    </div>
</template>

<script>
export default {
    name : 'TacticsIcon',
    props: {
        tacticsData: null,
    },
    
    methods:{
        GetQualityFrame({ quality, sign }) {
            const frames = {
                'A': sign === '典藏' ? 'ui (99).png' : 'ui (100).png',
                'S': sign === '典藏' ? 'ui (98).png' : 'ui (101).png',
                'B': sign === '典藏' ? 'ui (163).png' : 'ui (102).png',
                'C': sign === '典藏' ? 'ui (165).png' : 'ui (175).png'
            };
            return require(`../../assets/ui/${frames[quality]}`);
        },
        GetQualityIcon({ quality }) {
            const icons = {
                'A': 'ui (114).png',
                'S': 'ui (113).png',
                'B': 'ui (115).png',
                'C': 'ui (177).png'
            };
            return require(`../../assets/ui/${icons[quality]}`);
        },
        GetQualitySign({ quality }) {
            const signs = {
                'S': 'ui (95).png',
                'A': 'ui (96).png',
                'B': 'ui (97).png',
                'C': 'ui (176).png'
            };
            return require(`../../assets/ui/${signs[quality]}`);
        },
        //战法的图像
        GetTactImgUrl(t){
            switch(t.type){
                case "command" : return require('../../assets/ui/ui (92).png');
                case "active" : return require('../../assets/ui/ui (93).png');
                case "combo" : return require('../../assets/ui/ui (94).png');
                case "passive" : return require('../../assets/ui/ui (91).png');
            }
        },
    }
}
</script>

<style lang="less">
.tactics_icon_container{
    position: absolute;
    left: 0; top: 0;
    width: 100%; height: 100%;
    font-size: 100%;
    .tactics_icon_container_tactics_image{
        position: absolute;
        left: 5%; top: 0;
        width: 90%; height: 90%;
        transform: scale(.9);
    }
    .tactics_icon_container_tactics_frame{
        position: absolute;
        left: 5%; top: 0;
        width: 90%; height: 90%;
        
    }
    .tactics_icon_container_tactics_sign {
        left: 0;
        top: 0;
        width: 28%;
        height: 28%;
    }
    .tactics_icon_container_tactics_name {
        position: absolute;
        bottom: -13%;
        left: 0;
        top: auto;
        width: 100%;
        height: 26%;
        color:white;
        text-align: center;
        font-size: 100%;
        font-family: "FZLBJW";
        .tactics_icon_container_tactics_name_text {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 100%;
        }
    }
}
</style>