import Save from "./save";
import Warrior from "./warrior";
import trammels from '../data/trammels.json';

export class Lineup {
    constructor() {
        this.lineupName = "";
        this.describe = "";
        this.createTime = "";
        //存放武将信息
        this.list = [];
        //分组号列表
        this.groupIds = []
        
    }
    InitName(teamData){
        let armTypeMap = {
            "禁卫": "步",
            "长枪兵": "步",
            "重步兵": "步",
            "藤甲兵": "步",
            "蛮兵": "步",
            "长弓兵": "弓",
            "弩兵": "弓",
            "死士": "弓",
            "轻骑兵": "骑",
            "重骑兵": "骑",
            "铁骑兵": "骑",
            "弓骑兵": "骑",
            "象兵": "骑"
        }
        //初始化队伍名称

        
        let finalName = "【"
        let traName = ""
        for(let i in trammels){
            let count = 0;
            for(let j in trammels[i].member){
                for(let k in teamData){
                    if(teamData[k].camp == trammels[i].member[j].camp && teamData[k].name == trammels[i].member[j].name){
                        count += 1;
                    }
                }
            }
            if(count == 3) traName = trammels[i].name
        }
        if(traName != "") finalName += traName
        else{
            //判断是否构成国家队
            let campMap = {}
            let armMap = {}
            let cpl = 0
            let nationalName = ""
            for(let i in teamData){
                if(campMap[teamData[i].camp]){
                    campMap[teamData[i].camp] += 1;
                }
                else campMap[teamData[i].camp] = 1;
            }
            for(let key in campMap){
                if(campMap[key]==3 && key){
                    nationalName += key
                    cpl+=1;
                }
            }
            for(let i in teamData){
                if(teamData[i].id != -1){
                    let armStr = armTypeMap[teamData[i].upArms[teamData[i].choseUpArmsIdx]]
                    if(armMap[armStr]){
                        armMap[armStr] += 1;
                    }
                    else armMap[armStr] = 1;
                }
            }
            for(let key in armMap){
                if(armMap[key]>1){
                    nationalName += key;
                    cpl+=1;
                }
            }
            if(cpl == 2) finalName += teamData[0].name + nationalName
            else finalName += teamData[0].name
        }
        //后缀
        let commandMark = 0;
        let comboMark = 0;
        let markMap = {
            "行兵" : false,
            "神赏" : false,
            "菜刀" : false
        }
        for(let i in teamData){
            if(teamData[i].camp == "群" && teamData[i].name == '马超' && teamData[i].arms == '骑'
            || teamData[i].camp == "魏" && teamData[i].name == '张辽' && teamData[i].arms == '骑'
            ) comboMark += 3;

            for(let j in teamData[i].tacticsArr){
                if(teamData[i].tacticsArr[j].name == "神兵天降" || teamData[i].tacticsArr[j].name == "大赏三军") commandMark += 3.5;
                else if(teamData[i].tacticsArr[j].type == 'command') commandMark += 1;
                if(teamData[i].tacticsArr[j].type == 'combo') comboMark += 1;
                if(teamData[i].tacticsArr[j].name == "行兵之极") markMap['行兵'] = true;
            }
        }
        if(commandMark > 9) markMap["神赏"] = true
        if(comboMark > 3) markMap["菜刀"] = true
        for(let key in markMap){
            if(markMap[key]) finalName += key;
        }
        this.lineupName = finalName + `】队`;
    }
    Init(teamData) {
        this.InitName(teamData)
        this.GetCreateTime();
        this.BuildList(teamData);
        
    }
    GetCreateTime() {
        let currentTime = new Date(); // 创建一个表示当前时间的Date对象
        let year = currentTime.getFullYear(); // 获取年份
        let month = (currentTime.getMonth() + 1).toString().padStart(2, '0'); // 获取月份（注意要加上1）
        let day = currentTime.getDate().toString().padStart(2, '0'); // 获取天数
        let hours = currentTime.getHours().toString().padStart(2, '0'); // 获取小时
        let minutes = currentTime.getMinutes().toString().padStart(2, '0'); // 获取分钟
        let seconds = currentTime.getSeconds().toString().padStart(2, '0'); // 获取秒数

        // 组合为完整的日期和时间字符串
        this.createTime = `${year}/${month}/${day} ${hours}:${minutes}:${seconds}`;
    }
    BuildList(teamData) {
        for (let i in teamData) {
            let item = {
                red: teamData[i].red,
                level: teamData[i].level,
                id: teamData[i].id,
                name: teamData[i].name,
                tacticsConfig: [],
                artifactConfig: null,
                plus: teamData[i].plus,
                isUp: teamData[i].isUp,
                isUpArms: teamData[i].isUpArms,
                choseUpArmsIdx: teamData[i].choseUpArmsIdx,
                choseArmsCharacterIdx: teamData[i].choseArmsCharacterIdx,
            }
            //保存战法配置
            for (let j = 0; j < teamData[i].tacticsArr.length; j++) {
                if (j > 0) {
                    let tactItem = {
                        id: teamData[i].tacticsArr[j].id,
                        name: teamData[i].tacticsArr[j].name,
                        type: teamData[i].tacticsArr[j].type,
                    }
                    item.tacticsConfig.push(tactItem);
                }
            }
            //保存宝物信息
            if (teamData[i].artifact != null) {
                let artItem = {
                    name: teamData[i].artifact.name,
                    effQualityIdx: teamData[i].artifact.effQualityIdx,
                    forgeChoseIdx: teamData[i].artifact.forgeChoseIdx
                }
                item.artifactConfig = artItem;
            }
            this.list.push(item);
        }
    }
}

export class LineupGroup{
    constructor(){
        this.gid = 0;
        this.groupName = "";
        this.groupDescribe = "";
        this.createTime = "";
        this.groupColor = "";
    }
    InitGid(){
        this.gid = Date.now().toString(36) + Math.floor(Math.random() * 9999999)
    }
    Init(data){
        this.groupName = data.groupName
        this.groupDescribe = data.groupDescribe
        this.groupColor = data.groupColor

        this.InitGid();
        let currentTime = new Date(); // 创建一个表示当前时间的Date对象
        let year = currentTime.getFullYear(); // 获取年份
        let month = (currentTime.getMonth() + 1).toString().padStart(2, '0'); // 获取月份（注意要加上1）
        let day = currentTime.getDate().toString().padStart(2, '0'); // 获取天数
        let hours = currentTime.getHours().toString().padStart(2, '0'); // 获取小时
        let minutes = currentTime.getMinutes().toString().padStart(2, '0'); // 获取分钟
        let seconds = currentTime.getSeconds().toString().padStart(2, '0'); // 获取秒数

        // 组合为完整的日期和时间字符串
        this.createTime = `${year}/${month}/${day} ${hours}:${minutes}:${seconds}`;
    }
}

export class BattleSetting {
    constructor(){
        defWarriorLevel = 50;//武将默认等级
        defMoral = [100,100];//默认士气
    }
}



export class BattleUtil {
    constructor(store) {
        this.store = store
    }
    //保存阵容数据
    AddLineup(teamData) {
        let lineup = new Lineup();
        lineup.Init(teamData)
        //用于保存数据
        this.store.battleStorage.battleLineups.unshift(lineup);
        let emptyTeam = {
            lineupName: lineup.lineupName,
            describe: lineup.describe,
            createTime: lineup.createTime,
            groupIds : lineup.groupIds,
            //存放武将信息
            list: [],
        }
        for (let i in teamData) {
            let warrior = new Warrior();
            warrior.CopyData(teamData[i]);
            emptyTeam.list.push(warrior);
        }
        //新增到实时界面
        this.store.battle.lineupList.unshift(emptyTeam);
        let s = new Save(this.store);
        s.SaveBattleData();
        return true;
    }
    //删除阵容
    DeleteLineup(delTeam) {
        for (let i in this.store.battle.lineupList) {
            if (this.store.battle.lineupList[i].list == delTeam) {
                this.store.battle.lineupList.splice(i, 1);
                this.store.battleStorage.battleLineups.splice(i, 1);
                let s = new Save(this.store);
                s.SaveBattleData();
                return true;
            }
        }
        return false;
    }
    //清空所有阵容
    ClearAllLineup() {
        this.store.battle.lineupList = [];
        this.store.battleStorage.battleLineups = [];
        let s = new Save(this.store);
        s.SaveBattleData();
    }
    //新建分组
    CreateNewGroup(data){
        let g = new LineupGroup();
        g.Init(data);
        //用于保存
        this.store.battleStorage.lineupGroups.unshift(g);
        //用于界面展示
        this.store.battle.lineupGroups.unshift(g);
        let s = new Save(this.store);
        s.SaveBattleData();
        return true;
    }
    //删除分组
    DeleteGroup(groupId){
        for(let i in this.store.battleStorage.lineupGroups){
            if(this.store.battleStorage.lineupGroups[i].gid == groupId){
                //找到要删除的分组
                for(let j in this.store.battle.lineupList){
                    for(let k in this.store.battle.lineupList[j].groupIds){
                        if(this.store.battle.lineupList[j].groupIds[k] == groupId){
                            this.store.battle.lineupList[j].groupIds.splice(k,1);
                            console.log('it delete id');
                            break;
                        }
                    }
                }
                this.store.battleStorage.lineupGroups.splice(i,1);
                this.store.battle.lineupGroups.splice(i,1);
                let s = new Save(this.store);
                s.SaveBattleData();
                return true;
            }
        }
        return true;
    }
}



