//app用户
class User {
    constructor(name, createTime) {
        this.id = 0;
        this.name = name;
        this.createTime = createTime;
        this.coin = 0;
        //宝物容器
        this.artifacts = [];
        //武将的容器
        this.warriorStorage = [];
        //战法数据
        this.tacticsStorage = [];
        //配将存储
        this.teamStorage = [];
        //描述
        this.describe = "";
    }
    getUUID() {
        this.id = Math.floor(Math.random() * 999999) + Math.floor(Math.random() * 999999);
    }
}
