export default class Load {
    constructor(store) {
        this.store = store;

        this.baseeEffMap = {};
        this.forgeEffMap = {}
    }
    Init() {
        return new Promise((resolve) => {
            // 内部异步函数
            async function initAsync() {
                // 初始化数据库
                await this.InitStorage();
                this.InitTactics();
                this.InitArtifact();
                this.LoadArtifactData();
                this.InitWarriors();
                this.InitMyTeam();
                this.InitBattle();
                this.LoadTacticsData();
                this.LoadWarriorData();
                this.LoadMyTeam();
                this.LoadBattleLineup();
                this.LoadBattleLineupGroup();
                await this.LoadBattleSetting();
                resolve(true);
            }
            initAsync.call(this);
        });
    }
    //添加一个新的存档
    AddNewAccount() {
        let date = new Date();
        let dataStr = `${date.getFullYear()} ${date.getMonth() + 1}/${date.getDate()}`
        let user = new User(`存档${this.store.App.userList.length + 1}`, dataStr);
        user.getUUID();
        this.store.App.userList.push(user);
        return true;
    }
    //数据库
    InitStorage() {
        return new Promise((resolve) => {
            async function InitStorageAsync() {
                let res = await localForage.getItem(this.store.DATABASE_NAME);
                if (res == null) {
                    //如果是第一次登录app，则创建该app的数据库，使用默认用户
                    let date = new Date();
                    let dataStr = `${date.getFullYear()} ${date.getMonth() + 1}/${date.getDate()}`
                    let user = new User("默认用户", dataStr);
                    user.getUUID();
                    let app = {
                        currentUserId: null,//用来指定使用哪个用户
                        userList: [],
                    }
                    app.userList.push(user);
                    app.currentUserId = user.id;
                    localForage.setItem(this.store.DATABASE_NAME, app);
                    this.store.App = app;
                    this.store.user = user;
                }
                else {
                    //否则直接导入数据
                    this.store.App = res;
                    for (let i = 0; i < res.userList.length; i++) {
                        if (res.userList[i].id == res.currentUserId) {
                            this.store.user = res.userList[i];
                            break;
                        }
                    }
                }

                let battleData = await localForage.getItem(this.store.DATABASE_NAME + '_BATTLE');
                if (battleData == null) {
                    let date = new Date();
                    let dataStr = `${date.getFullYear()} ${date.getMonth() + 1}/${date.getDate()}`
                    let storage = new BattleStorage(dataStr);
                    localForage.setItem(this.store.DATABASE_NAME + '_BATTLE', storage)
                    this.store.battleStorage = storage
                }
                else {
                    this.store.battleStorage = new BattleStorage();
                    this.store.battleStorage.CopyData(battleData);
                }



                resolve(true);
            }
            InitStorageAsync.call(this);
        })
    }
    //初始化战法
    InitTactics() {
        this.store.tactics = {
            command: [],
            active: [],
            combo: [],
            passive: [],
        };
        this.store.allTactics = {
            command: [],
            active: [],
            combo: [],
            passive: [],
        };

        const tacticsMap = {
            "S": [],
            "A": [],
            "B": [],
            "C": [],
        };
        // 战法类型-品质映射表
        let tacticTypeMap = {
            'command': { 'S': commandS, 'A': commandA, 'B': commandB },
            'active': { 'S': activeS, 'A': activeA, 'B': activeB },
            'combo': { 'S': comboS, 'A': comboA, 'B': comboB },
            'passive': { 'S': passiveS, 'A': passiveA, 'B': passiveB }
        };



        // 根据quality和type分类
        optionalTactics.forEach(item => {
            const { quality, type } = item;
            if (tacticsMap[quality]) {
                tacticsMap[quality][type] = tacticsMap[quality][type] || [];
                let newItem = new Tactics(item)
                tacticsMap[quality][type].unshift(newItem);
            }
        });

        // 合并数组
        ['S', 'A', 'B', 'C'].forEach(quality => {
            ['command', 'active', 'combo', 'passive'].forEach(type => {
                this.store.tactics[type] = this.store.tactics[type].concat(
                    tacticsMap[quality][type] || []
                );
            });
        });
        //导入对局模拟中被开发的战法列表
        for (let key in this.store.tactics) {
            for (let i in this.store.tactics[key]) {
                let tgt = this.store.tactics[key][i]
                for (let j in tacticTypeMap[tgt.type][tgt.quality]) {
                    if (tacticTypeMap[tgt.type][tgt.quality][j].name == tgt.name) {
                        let t = new Tactics()
                        t.CopyData(tgt)
                        this.store.allTactics[key].push(t);
                        break;
                    }
                }
            }
        }
    }
    //初始化宝物
    InitArtifact() {
        this.store.artifactOriginDataList = artifactData;
        this.store.artifacts = [];
        this.store.allArtifacts = [];

        for (let i in artifactBaseEffData) {
            this.baseeEffMap[artifactBaseEffData[i].name] = artifactBaseEffData[i].eff;
        }
        for (let i in artifactForgeEffData) {
            this.forgeEffMap[artifactForgeEffData[i].name] = artifactForgeEffData[i].eff;
        }
        for (let j = 0; j < 3; j++) {
            for (let i in artifactData) {
                let art = new Artifact(artifactData[i], this.baseeEffMap, this.forgeEffMap);
                this.store.artifacts.unshift(art);
                for (let k in art.forgeEffArr) {
                    let newArt = new Artifact(artifactData[i], this.baseeEffMap, this.forgeEffMap);
                    newArt.effQualityIdx = j;
                    newArt.forgeChoseIdx = k;
                    this.store.allArtifacts.unshift(newArt);
                }
            }
        }

    }
    //初始化武将
    InitWarriors() {
        this.store.warriors = [];
        this.store.warriorOriginDataList = []
        this.store.allWarriors = [];
        const campMap = {
            "群": ['弓', '步', '骑'],
            "魏": ['弓', '步', '骑'],
            "蜀": ['弓', '步', '骑'],
            "吴": ['弓', '步', '骑'],
            "汉": ['弓', '步', '骑'],
            "晋": ['弓', '步', '骑']
        };
        // 阵营-品质映射表
        let campQualityMap = {
            '魏': { 'S': weiSArr, 'A': weiAArr, 'B': weiBArr },
            '群': { 'S': qunSArr, 'A': qunAArr, 'B': qunBArr },
            '蜀': { 'S': shuSArr, 'A': shuAArr, 'B': shuBArr },
            '吴': { 'S': wuSArr, 'A': wuAArr, 'B': wuBArr },
            '汉': { 'S': hanSArr, 'A': hanAArr, 'B': hanBArr },
            '晋': { 'S': jinSArr, 'A': jinAArr, 'B': jinBArr }
        };
        const campArr = {};
        const originCampArr = {}
        for (let camp in campMap) {
            for (let i in campMap[camp]) {
                campArr[camp + campMap[camp][i]] = [];
                originCampArr[camp + campMap[camp][i]] = [];
            }
        }
        for (let i in warriorsData) {
            let data = warriorsData[i]
            let item = new Warrior(data);
            item.Init(data)
            const key = item.camp + item.arms;
            if (campArr[key]) {
                campArr[key].unshift(item);
            }
            if (originCampArr[key]) {
                originCampArr[key].unshift(data)
            }
        }
        // 合并数组并排序
        // 1. 合并所有分组的数组
        const tempArr = [];
        const tempOriginArr = []
        for (const key in campArr) {
            // 将每个分组的数组元素添加到 tempArr 中
            tempArr.push(...campArr[key]);
            tempOriginArr.push(...originCampArr[key]);
        }

        // 2. 按 cost 降序排序
        tempArr.sort((a, b) => {
            return b.cost - a.cost; // 降序：b - a
        });
        tempOriginArr.sort((a, b) => {
            return b.cost - a.cost; // 降序：b - a
        });

        this.store.warriors = tempArr;
        this.store.warriorOriginDataList = tempOriginArr

        // 插入SP武将
        warriorSpecialData.forEach(spData => {
            const index = this.store.warriors.findIndex(w => w.id === (spData.id - 5000));
            if (index !== -1) {
                this.store.warriorOriginDataList.splice(index, 0, spData);
                const item = new Warrior(spData);
                item.Init(spData)
                this.store.warriors.splice(index, 0, item);
            }
        });

        // 插入侍卫
        warriorGuardData.forEach(guard => {
            this.store.warriorOriginDataList.push(guard)
            const item = new Warrior(guard);
            item.Init(guard)
            this.store.warriors.push(item);
        });
        //生成武将的索引
        for (let i = 0; i < this.store.warriors.length; i++) {
            this.store.warriorIdxTable[this.store.warriors[i].id] = i;
        }

        //导入被开发后的武将列表
        for (let i in this.store.warriors) {
            for (let j in campQualityMap[this.store.warriors[i].camp][this.store.warriors[i].tacticsArr[0].quality]) {
                if (campQualityMap[this.store.warriors[i].camp][this.store.warriors[i].tacticsArr[0].quality][j].id == this.store.warriors[i].tacticsArr[0].id) {
                    let warrior = new Warrior();
                    warrior.CopyData(this.store.warriors[i]);
                    this.store.allWarriors.push(warrior);
                    break;
                }
            }
        }


    }
    //初始化配将队伍
    InitMyTeam() {
        let t = new Team(this.store)
        t.Init(1)
        this.store.teams = t
    }
    //初始化对局模块
    InitBattle() {
        let t = new Team(this.store)
        let bs = new BattleSetting();
        t.Init(2)
        bs.Init();
        this.store.battle.teams = t
        this.store.battle.setting = bs;
    }
    //加载数据库战法数据到app
    LoadTacticsData() {
        if (this.store.user.tacticsStorage.length == 0) {
            return;
        }
        let p = 0;
        for (let type in this.store.tactics) {
            for (let j in this.store.tactics[type]) {
                if (p < this.store.user.tacticsStorage.length && this.store.tactics[type][j].id == this.store.user.tacticsStorage[p].id) {
                    this.store.tactics[type][j].isload = this.store.user.tacticsStorage[p].isload;
                    this.store.tactics[type][j].lock = false;
                    p++
                }
            }
        }
    }
    //加载数据库武将数据到app
    LoadWarriorData() {
        if (this.store.user.warriorStorage.length == 0) return;
        for (let p in this.store.user.warriorStorage) {
            for (let i in this.store.warriors) {
                if (this.store.warriors[i].id == this.store.user.warriorStorage[p].id) {
                    this.store.warriors[i].DataRecovery(this.store.user.warriorStorage[p], this.store.tactics)
                    //恢复宝物引用
                    if (this.store.user.warriorStorage[p].artifactId != "") {
                        for (let j in this.store.userArtifacts) {
                            if (this.store.userArtifacts[j].id == this.store.user.warriorStorage[p].artifactId) {
                                this.store.warriors[i].artifact = this.store.userArtifacts[j];
                                this.store.userArtifacts[j].warriorId = this.store.warriors[i].id;
                            }
                        }
                    }
                }
            }
        }
    }
    //加载数据库宝物数据到app
    LoadArtifactData() {
        this.store.userArtifacts = [];
        let classifyMap = {
            0: [],
            1: [],
            2: []
        }
        if (this.store.user.artifacts.length == 0) return;
        for (let i in this.store.user.artifacts) {
            let art = new Artifact();
            art.CopyData(this.store.user.artifacts[i]);
            classifyMap[art.effQualityIdx].push(art);
            // this.store.userArtifacts.push(art);
        }
        console.log('classifyMap', classifyMap[1])
        // ['S', 'A', 'B', 'C'].forEach(quality => {
        //     ['command', 'active', 'combo', 'passive'].forEach(type => {
        //         this.store.tactics[type] = this.store.tactics[type].concat(
        //             tacticsMap[quality][type] || []
        //         );
        //     });
        // });
        for (let key in classifyMap) {
            for (let i in classifyMap[key]) {
                this.store.userArtifacts.unshift(classifyMap[key][i]);
            }
        }
    }
    //加载配將队伍到面板
    LoadMyTeam() {
        if (this.store.user.teamStorage == []) return
        let res = this.store.user.teamStorage;
        this.store.teams.Init(res.length);
        for (let i in res) {
            //配置一队
            for (let j in res[i]) {
                if (res[i][j] != -1) {
                    this.store.teams.PutWarriorIntoTeam(i, j, this.store.warriors[this.store.warriorIdxTable[res[i][j]]])
                }
            }
        }
    }
    //加载对局模块保存的阵容信息
    LoadBattleLineup() {
        this.store.battle.lineupList = []
        for (let i in this.store.battleStorage.battleLineups) {
            let item = {}
            for(let key in this.store.battleStorage.battleLineups[i]){
                item[key] = this.store.battleStorage.battleLineups[i][key]
            }
            item['list'] = []
            //旧版本没有组号字段
            if(!item['groupIds']){
                this.store.battleStorage.battleLineups[i]['groupIds'] = [];
                item['groupIds'] = this.store.battleStorage.battleLineups[i]['groupIds'];
            }
            //遍历武将列表
            for (let j in this.store.battleStorage.battleLineups[i].list) {
                let warriorData = this.store.warriorOriginDataList[this.store.warriorIdxTable[this.store.battleStorage.battleLineups[i].list[j].id]];
                let warrior = new Warrior(warriorData);
                warrior.DataRecoveryFromLineup(this.store.battleStorage.battleLineups[i].list[j], warriorData, this.store.artifactOriginDataList, this.baseeEffMap, this.forgeEffMap);
                item.list.push(warrior)
            }
            this.store.battle.lineupList.push(item);
        }
        this.store.battle.visibleLineups = this.store.battle.lineupList;
    }
    //加载阵容分组信息
    LoadBattleLineupGroup(){
        this.store.battle.lineupGroups = [];
        for(let i in this.store.battleStorage.lineupGroups){
            let item = {}
            for(let key in this.store.battleStorage.lineupGroups[i]){
                item[key] = this.store.battleStorage.lineupGroups[i][key];
            }
            
            this.store.battle.lineupGroups.push(item);
        }

    }
    //加载保存的对局设置
    LoadBattleSetting() {
        return new Promise((resolve) => {
            async function InitStorageAsync() {
                let battleSetting = await localForage.getItem(this.store.DATABASE_NAME + '_BATTLE_SETTING');
                if (battleSetting) {
                    // console.log(battleSetting,this.store.battle.setting)
                    this.store.battle.setting.CopyData(battleSetting);
                    // console.log(battleSetting,this.store.battle.setting)
                }
                resolve(true);
            }
            InitStorageAsync.call(this);
        })

    }
}