<template>
    <div class="team">
        <div class="team_container" ref="team_container">
            <div class="team_box" v-for="(t,tId) in $store.teams.list" :key="t.index">
                <!-- 武将卡 -->
                <div class="team_warrior" v-for="(w,wId) in $store.teams.list[tId]" :key="w.index" >
                    <div class="cover" v-if="w.id != -1 && w.own" @click="WarriorInfoCheck(w,wId,tId)">
                        <WarriorElongatedCard :warriorData="w" cardStyle="2_1" :showUpArms="true" />
                        <div class="position_sign">
                            <span v-if="wId == 0">大营</span>
                            <span v-if="wId == 1">中军</span>
                            <span v-if="wId == 2">前锋</span>
                        </div>
                    </div>
                </div>
                <!-- 编辑队伍按钮 -->
                <div class="build_team_btn" ref="build_team_btn" 
                @touchstart="$refs.build_team_btn[tId].classList.add('click')" 
                @touchend="$refs.build_team_btn[tId].classList.remove('click')"  
                @click="EditTeam(t,tId)">
                    <div class="build_team_btn_img"></div>
                    <div class="build_team_btn_font">部队</div>
                </div>
            </div>
        </div>

        <!-- 添加队伍 -->
        <div class="add_team_btn" ref="add_team_btn"
        @touchstart='$refs.add_team_btn.classList.add("active")'  
        @touchend='$refs.add_team_btn.classList.remove("active")' 
        @click="AddNewTeam()">
            <div class="text">{{ addTeamBtnStr }}</div>
        </div>
        <!-- 保存队伍数据按钮 -->
        <div class="save_team_data_btn" ref="save_team_data_btn"  
        @touchstart='$refs.save_team_data_btn.classList.add("active")' 
        @touchend='$refs.save_team_data_btn.classList.remove("active")'
        @click="SaveData()">
            <div class="text">保存</div>
        </div>
        <!-- 请空队伍数据按钮 -->
        <div class="clear_team_data_btn" ref="clear_team_data_btn"  
        @touchstart='$refs.clear_team_data_btn.classList.add("active")' 
        @touchend='$refs.clear_team_data_btn.classList.remove("active")'
        @click="ClearTeam()">
            <div class="text">{{ clearTeamStr }}</div>
        </div>

        <TopNav title="队伍配置" />
        
        
        <!-- 路由 -->
        <router-view/>
    </div>
</template>

<script>
import WarriorElongatedCard from '@/components/style/WarriorElongatedCard.vue';
import TopNav from '@/components/container/TopNav.vue';
import Save from '../../util/save.js';
export default {
    name : 'MyTeam',
    components:{
        TopNav,
        WarriorElongatedCard
    },
    data(){
        return{
            tipStr : "",
            isAlert : false,

            addTeamBtnStr : "添加队伍",
            clearTeamStr : "清除队伍",
        }
    },
    methods:{
        //查看武将信息
        WarriorInfoCheck(warriorData,w_id,t_id){
            this.$store.buffer.curWarrior = warriorData;
            // this.$store.buffer.editTeamIdx = editTeamIdx;
            this.$router.push({
                path : `${this.$route.path}/warrior_info`,
            })
        },
        //编辑队伍
        EditTeam(editTeam,tId){
            // this.currentTeamIndex = index;
            this.$store.buffer.curTeam = editTeam
            this.$store.buffer.editTeamIdx = tId
            this.$router.push({
                path : `${this.$route.path}/team_info`,
            })
        },
        //添加一个新队伍
        AddNewTeam(){
            let distance = (window.innerWidth/5) + 10;
            setTimeout(()=>{
                this.$refs.team_container.scrollTo(0,distance*(this.$store.teams.list.length-5));
            },10)
            this.addTeamBtnStr = "添加中"
            this.$store.teams.ConfigEmptyTeam();
            setTimeout(() => {
                this.addTeamBtnStr = "添加队伍"
            }, 10);
        },
        //清除队伍
        ClearTeam(){
            this.$store.teams.ClearTeamList();
            let save = new Save(this.$store);
            save.SaveMyTeam();
            this.$store.Alert('队伍清空成功')
        },
        SaveData(){
            let save = new Save(this.$store);
            save.SaveMyTeam();
            this.$store.Alert('保存成功')
        }
    },
}
</script>

<style  lang="less" scoped>
