                    }
                }
            }
            .team_warrior:nth-of-type(1){
                margin:2vw .5vw 0 2vw;
            }
            
            //编辑队伍按钮
            .build_team_btn{
                position: absolute; right: .5vw; top: .5vw;
                
                width: 6vw;
