            .build_team_btn.click{
                opacity: 1;
                transform: scale(.9);
                
            }
        }
    }

    //添加队伍按钮
    .add_team_btn,.save_team_data_btn,.clear_team_data_btn{
        position: absolute; bottom: 10vh; left: 35vw;
