        }
    }
    .add_team_btn.active,.save_team_data_btn.active,.clear_team_data_btn.active{
        transform: scale(.8);
    }
    .clear_team_data_btn{
        left: 63vw;
    }
    //保存队伍数据按钮
    .save_team_data_btn{
