                box-shadow: 0 0 2vw rgb(64, 64, 64);
                transition: all .2s;
            }
            //标题
            .title{
                position: relative;
                width: 100%;
                height: 7vw;
                background: rgba(0, 0, 0, 0.396);
                float: left;
                margin: 5vw 0 0 0;
            }
            //战法
            .command_skill_container,.active_skill_container,.combo_skill_container,.passive_skill_container{
                position: relative;
                width: 100%;
                .skill_box{
                    position: relative;
                    width: 15vw;
                    height: 15vw;
                    float: left;
                    margin: 2.5vw 0 0 2.5vw;
                    .name{
                        position: absolute; bottom: 0; left: 0;
                        width: 100%;
                        height: 4vw;
                        font-size: 2vw;
                        color: rgb(255, 255, 200);
                        text-align: center;
                        line-height: 4vw;
                    }
                }
                

            }
            

        }
        .unload_skill_btn{
            position: absolute; left: 35vw; top : 90vh;
            width : 30vw; height : 8vw;
            text-align: center;
            line-height: 8vw;
            color: rgb(19, 19, 19);
            font-size: 2vw;
            font-weight: bold;
            background: url('../../assets/ui/ui (10).png')no-repeat;
            background-size: 100% 100%;
