        }
        .unload_skill_btn.click{
            transform: scale(.8);
        }
    }
    .skill_panel1.emerge{
        top: 0;
    }

    //提示面板
    .alert_panel{
        z-index: 5;
        position: absolute; left: 0; top : 0;
