        .mask{
            background: rgba(255, 255, 255, 0.558);
        }
        .container{
            width: 100vw;
            height: 60vw;
            position: absolute; left: 50%; top : 50%;
            margin: -30vw 0 0 -50vw;
            background: url('../../assets/background/bg (3).png')no-repeat;
            background-size: 100% 100%;
            animation: emerge .15s ease-out 1;
            .info{
                position: absolute; left: 50%; top : 55%;
                transform : translate(-50%,-50%);
                width: 80vw;height: 10vw;
