
//选择目标的执行块
export class TargetSelectExecuteBlock {
    constructor(applicator, tactics, isBefore, execute, duration) {
        //来自谁
        this.applicator = applicator;
        //施加该效果的战法
        this.tactics = tactics;
        //是在选择目标前还是选择目标后
        this.isBefore = isBefore;

        //谁在选择目标
        this.whoSelectTarget = null;
        //哪个战法在选择目标
        this.whichTact = null;
        //范围
        this.range = 0
        this.tgtNumber = 0;
        //该次选中的所有目标
        this.targetArr = [];
        //选中目标的目的
        this.forWhat = ""

        //执行
        this.execute = execute
        this.beExecuted = null;
        //执行时间
        this.duration = duration
    }
    Init(target) {
        this.beExecuted = target;
        if (this.isBefore) {
            target.BEFORE_TARGET_SELECT_EXECUTE.push(this);
        }
        else target.AFTER_TARGET_SELECT_EXECUTE.push(this);
    }
    Execute(whoSelectTarget, whichTact, range, tgtNumber, targets, forWhat) {
        this.whoSelectTarget = whoSelectTarget
        this.whichTact = whichTact
        this.range = range
        this.tgtNumber = tgtNumber
        this.targetArr = targets
        this.forWhat = forWhat

        this.execute();
    }
    SelfDestroy() {
        let temp = []
        let tgtArr = this.isBefore ? this.beExecuted.BEFORE_TARGET_SELECT_EXECUTE : this.beExecuted.AFTER_TARGET_SELECT_EXECUTE
        for (let i in tgtArr) {
            if (tgtArr[i] != this) temp.push(tgtArr[i]);
        }
        tgtArr = temp;
    }
}

//即将受到伤害前执行
export class BeforeGetHurtExecuteBlock {
    constructor(applicator, tactics, execute, duration) {
        //来自谁
        this.applicator = applicator;
        //施加该效果的战法
        this.tactics = tactics;
        this.duration = duration == undefined ? 99 : duration;

        //执行
        this.execute = execute;
        this.beExecuted = null;
        //本次受到伤害前的信息
        this.attacker = null;
        this.receiver = null;
        //受到的伤害数值
        this.damageValue = 0;
        //伤害是否是普通攻击
        this.isCommonAttack = false;
        //伤害类型
        this.damageType = ""
        //伤害来源战法
        this.damageFromTact = null;
        this.isContinueDamageType = false;
    }
    Init(target) {
        this.beExecuted = target;
        target.BEFORE_GET_HURT.push(this);
    }
    Execute(attacker, receiver, originDamageValue, isCommonAttack, damageType, damageFromTact, isContinueDamageType) {
        this.attacker = attacker
        this.receiver = receiver
        this.damageValue = originDamageValue
        this.isCommonAttack = isCommonAttack
        this.damageType = damageType;
        this.damageFromTact = damageFromTact
        this.isContinueDamageType = isContinueDamageType

        this.execute();
    }
    SelfDestroy() {
        let arr = [];
        for (let i in this.beExecuted.BEFORE_GET_HURT) {
            if (this.beExecuted.BEFORE_GET_HURT[i] != this) arr.push(this.beExecuted.BEFORE_GET_HURT[i]);
        }
        this.beExecuted.BEFORE_GET_HURT = arr;
    }
}