//查询类
class Query {
    constructor() {

    }
    /**
     * 获取武将被施加伤害类状态的数量
     * @param {object} target 目标 
     * @returns 返回数目
     */
    GetNumberOfDamageStatus(target){
        let count = 0;
        for(let i in target.SPECIAL_STATUS){
            if((target.SPECIAL_STATUS[i].type == "恐慌" || target.SPECIAL_STATUS[i].type == "妖术" 
            || target.SPECIAL_STATUS[i].type == "动摇" || target.SPECIAL_STATUS[i].type == "燃烧"
            || target.SPECIAL_STATUS[i].type == "火攻")
            && target.SPECIAL_STATUS[i].waitTime <= 0){
                count += 1
            }
        }
        return count;
    }
    /**
     * 获取武将被施加控制状态的数量
     * @param {object} target 目标
     * @returns 返回数目
     */
    GetNumberOfControlStatus(target){
        let count = 0;
        for(let i in target.STATUS){
            if((target.STATUS[i].type == "怯战" || target.STATUS[i].type == "犹豫" || target.STATUS[i].type == "混乱" || target.STATUS[i].type == "暴走")
            && target.STATUS[i].waitTime <= 0){
                count += 1
            }
        }
        return count;
    }
    /**
     * 获取self武将和target武将之间的距离
     * @param {*} self self武将，即函数调用者
     * @param {*} target 目标武将
     * @returns 返回距离
     */
    GetDistance(self, target) {
        let distance = 0;
        if (target.belong == self.belong) {
            if(self.position == 1) distance = 1
            else if(self.ally[1].alive) distance = 2
            else distance = 1
        }
        else {
            for (let i = self.position; i < 3; i++) {
                if (self.ally[i].alive && self.ally[i] != self) distance += 1;
            }
            for(let i=target.ally.length-1;i>=0;i--){
                if(target.ally[i] != target && target.ally[i].alive) distance += 1;
                else if(target.ally[i] == target){
                    distance += 1;
                    break;
                }
            }
        }
        return distance;
    }
    /**
     * 获取一个武将无视属性的值
     * @param {object} target 
     * @param {string} attrType 需要读取的属性："攻击";"谋略";"防御";"速度"
     * @returns 返回 0-1,0.5表示无视50%的某属性
     */
    GetIgnoreValue(target, attrType) {
        let res = 0;
        for (let i in target.IGNORE) {
            if (target.IGNORE[i].ignoreAttrType == attrType) {
                res += target.IGNORE[i].value;
            }
        }
        
        if (res > 99) res = 99;
        return res / 100;
    }
    /**
     * 获取一个战法的实际准备时间
     * @param {object} target  目标
     * @param {*} targetTactics 
     * @returns 
     */
    GetRealTacticsPrepareTime(target, targetTactics) {
        let base = targetTactics.wait;
        for (let i in target.ODD_VAR) {
            if (target.ODD_VAR[i].workTactics == targetTactics) {
                base -= target.ODD_VAR[i].skipTime;
            }
        }
        if (base < 0) base = 0;
        return base;
    }
    //输入基础数值和受影响的属性和index值，返回加成值
    GetAdditionValue(target, baseValue, attrType, index) {
        let res = this.GetAttr(target, attrType);
        res = res < 80 ? 0 : res - 80;
        res = Math.floor(res * index + baseValue);
        return res;
    }
    //获取一个武将的最大兵力
    GetMaxHp(target) {
        return target.level * 100 + 5000;
    }
    /**
     * 是否触发概率事件
     * @param {number} triggerChance 触发概率
     * @param {boolean} considerMoral 是否考虑士气
     * @param {object} target 需要触发的目标武将，用于计算士气对概率的影响
     * @returns 返回res = {
            status : false,
            triggerChance : realTriChance
        }
     */
    GetTriggerStatus(triggerChance,considerMoral=true,target){
        let rand = Math.floor(Math.random()*100);
        let realTriChance = triggerChance;
        //士气影响
        if(considerMoral){
            realTriChance = this.GetMoraleOdd(target,triggerChance)
        }
        let res = {
            status : false,
            triggerChance : realTriChance
        }
        if(realTriChance < rand) res.status = true
        return res;
    }
    /**
     * 输入几率，返回士气加成下的几率
     * @param {object} target 
     * @param {number} odd 几率 
     * @returns 返回实际几率
     */
    GetMoraleOdd(target, odd) {
        let res = odd;
        //士气影响
        let addition = Math.abs(target.morale - 100);
        addition = addition / (100 + target.morale / 2);
        if (target.morale < 100) {
            res -= res * addition;
        } else {
            res += res * addition;
        }
        res = Math.floor(res);
        return res;
    }
    //获得武将谋战法的实际发动几率
    GetRealTacticsOdd(target, tacticsItem) {
        let res = this.GetMoraleOdd(target, tacticsItem.odd);
        for (let i in target.ODD_VAR) {
            if (target.ODD_VAR[i].workTactics == tacticsItem) res += target.ODD_VAR[i].value;
        }
        return res > 100 ? 100 : res;
    }
    //获得战法的指定个数
    GetRealTargetNumber(baseNumber, target, tacticsItem) {
        let res = baseNumber;
        return res;
    }
    //获得一个武将的实际攻击范围
    GetRealAdRange(target) {
        let res = target.ackRange;
        for (let i in target.RANGE) {
            if (target.RANGE[i].isCommonAttack && target.RANGE[i].waitTime <= 0) res += target.RANGE[i].value;
        }
        return res;
    }
    //获取武将战法的范围
    GetTacticsRange(target, tacticsItem) {
        let res = tacticsItem.range
        for (let i in target.RANGE) {
            if(!target.RANGE[i].isCommonAttack && target.RANGE[i].waitTime <= 0){
                if(target.RANGE[i].workTactics){
                    if(target.RANGE[i].workTactics == tacticsItem) res += target.RANGE[i].value;
                }
                else res += target.RANGE[i].value;
            }
        }
        // console.log(tacticsItem.name,res);
        return res;
    }
    //获取一个武将的属性
    GetAttr(target, attrType,isNeedATTR=true) {
        let res = 0;
        //基础属性
        switch (attrType) {
            case "攻击": res = target.attr.ad; break;
            case "谋略": res = target.attr.tact; break;
            case "防御": res = target.attr.de; break;
            case "速度": res = target.attr.speed; break;
        }
        //读取属性堆加区的块
        if(isNeedATTR){
            for (let i in target.ATTR) {
                if (target.ATTR[i].attrType == attrType) {
                    res += target.ATTR[i].value;
                }
            }
        }
        return res > 0 ? res : 0;
    }
}


export default Query;