import Save from "./save";
import Warrior from "./warrior";
class Team {
    constructor(store) {
        this.store = store
        //队伍列表(2维数组)
        this.list = [
            [
                {} = new Warrior(),
                {} = new Warrior(),
                {} = new Warrior()
            ],
            [
                {} = new Warrior(),
                {} = new Warrior(),
                {} = new Warrior()
            ],
        ]
    }
    Init(baseLen = 1) {
        this.list = [];
        for (let i = 0; i < baseLen; i++) this.ConfigEmptyTeam()
    }
    //构造一个空的队伍
    ConfigEmptyTeam() {
        let newTeam = [
            {} = new Warrior(),
            {} = new Warrior(),
            {} = new Warrior()
        ]
        this.list.push(newTeam)
    }
    //放入一个武将
    PutWarriorIntoTeam(teamIdx, warriorIdx, warriorData, link = true) {
        if (link) {
            this.list[teamIdx][warriorIdx] = warriorData
            this.list[teamIdx][warriorIdx].configTeamIdx = teamIdx
        }
        else {
            this.list[teamIdx][warriorIdx].CopyData(warriorData)
            this.list[teamIdx][warriorIdx].configTeamIdx = teamIdx
        }
    }
    //安装一个原始数据的武将到队伍中
    LoadNativeWarrior(teamIdx, warriorIdx, warriorData) {
        let index = this.store.warriorIdxTable[warriorData.id]
        let newWarrior = new Warrior(this.store.warriorOriginDataList[index])
        newWarrior.GetInnateTactics(this.store.warriorOriginDataList[index].tactics_idx)
        this.list[teamIdx][warriorIdx] = newWarrior
        this.list[teamIdx][warriorIdx].InitMockBattleConfig(this.store.warriorOriginDataList[index],this.store.battle.setting)

    }
    //从队伍中卸载一个武将
    UnloadWarrior(teamIdx, warriorIdx) {
        this.list[teamIdx][warriorIdx].configTeamIdx = -1
        this.list[teamIdx][warriorIdx] = new Warrior()
    }
    //清空队伍
    ClearTeamList() {
        for(let i in this.list){
            for(let j in this.list[i]){
                if(this.list[i][j].id != -1) this.list[i][j].configTeamIdx = -1;
            }
        }
        this.list = []
    }
    //更新队伍去除掉被移除的武将
    UpdateTeamData() {
        console.log('app is', this.app)
        let flag = false;
        for (let i in this.list) {
            for (let j in this.list[i]) {
                if (this.list[i][j].id != -1 && !this.list[i][j].own) {
                    this.UnloadWarrior(i, j, true)
                    flag = true
                }
            }
        }
        if (flag) {
            let s = new Save(this.store)
            s.SaveMyTeam()
        }
    }
}


export default Team;