<template>
    <div class="statistics_panel">
        <div class="head"></div>
        <!-- 总体统计数据 -->
        <div class="container">
            <div class="title" v-if="curNvaIdx == 0">
                <li>武将</li>
                <li>普通攻击</li>
                <li>战法伤害</li>
                <li>治疗量</li>
                <li>战法次数</li>
            </div>
            <div class="teams_container" 
            v-show="curNvaIdx == 0"
            v-for="(t,t_index) in $store.battle.Statistics.dataMatrices" :key="t.index">
                <div class="warrior_info" v-for="w in t" :key="w.index">
                    <div class="warrior_head">
                        <div class="warrior_img_bg_blue" v-if="t_index == 0"></div>
                        <div class="warrior_img_bg_red" v-if="t_index == 1"></div>
                        <img :src="w.img.head" alt="" class="cover">
                    </div>
                    <div class="attack_damage">{{w.commonDamage}}</div>
                    <div class="tactics_damage damage">{{GetTacticsDamage(w)}}</div>
                    <div class="cure cure_color">{{GetTacticsCure(w)}}</div>
                    <div class="ratio">{{GetTacticsTimes(w)}}</div>
                </div>
            </div>



            <div class="title" v-if="curNvaIdx == 1">
                <li>武将</li>
                <div class="tact_li">主战法</div>
                <div class="tact_li">战法1</div>
                <div class="tact_li">战法2</div>
                <!-- <li>战法次数</li> -->
            </div>
            <div class="teams_container" 
            v-show="curNvaIdx == 1"
            v-for="(t,t_index) in $store.battle.Statistics.dataMatrices" :key="t.index">
                <div class="warrior_info" v-for="w in t" :key="w.index">
                    <div class="warrior_head">
                        <div class="warrior_img_bg_blue" v-if="t_index == 0"></div>
                        <div class="warrior_img_bg_red" v-if="t_index == 1"></div>
                        <img :src="w.img.head" alt="" class="cover">
                    </div>
                    <div class="tactics_box" v-for="tact in w.tacticsArr" :key="tact.index">
                        <div :class="`name ${tactTypeMap[tact.type]}`">
                            {{ tact.name }}
                            <div class="time">
                                <span class="dark_color">{{ tact.times }}</span>次
                                </div>
                        </div>
                        <div class="make_damage">
                            <span class="title">杀伤：</span>
                            <span :class="`${tact.damage>0?'damage':'dark_color'}`">{{ tact.damage }}</span>
                        </div>
                        <div class="make_cure">
                            <span class="title">救援：</span>
                            <span :class="`${tact.cure>0?'cure_color':'dark_color'}`">{{ tact.cure }}</span>
                        </div>
                    </div>
                    <!-- <div class="attack_damage">{{w.commonDamage}}</div>
                    <div class="tactics_damage">{{GetTacticsDamage(w)}}</div>
                    <div class="cure">{{GetTacticsCure(w)}}</div>
                    <div class="ratio">{{GetTacticsTimes(w)}}</div> -->
                </div>
            </div>
            <div class="exchange_style">
                <div :class="`btn ${curNvaIdx==bId?'current':''}`" v-for="b,bId in statisticsBtns" :key="b.index"
                @click="curNvaIdx = bId">
                    {{ b }}
                </div>
            </div>
        </div>
        
        <TopNav title="结果统计" />
    </div>
</template>

<script>
import TopNav from '@/components/container/TopNav.vue';
export default {
    name : 'ReportStatistics',
    components:{
        TopNav,
    },
    data(){
        return{
            statisticsBtns : ["总览","战法统计"],
            curNvaIdx:0,
            tactTypeMap:{
                "指挥":"command",
                "主动":"active",
                "追击":"combo",
                "被动":"passive"
            }
        }
    },
    methods:{
        
        //获取该武将造成的战法伤害
        GetTacticsDamage(target){
            let res = 0;
            for(let i in target.tacticsArr){
                res += target.tacticsArr[i].damage;
            }
            return res;
        },
        //获取战法治疗量
        GetTacticsCure(target){
            let res = 0;
            for(let i in target.tacticsArr){
                res += target.tacticsArr[i].cure;
            }
            return res;
        },
        //获取战法释放次数
        GetTacticsTimes(target){
            let res = 0;
            for(let i in target.tacticsArr){
                res += target.tacticsArr[i].times;
            }
            return res;
        },  
        //战法释放次数统计
        TacticsTime(w){
            let times = 0;
            for(let i=0;i<w.TACTICS_STACK.length;i++){
                times += w.TACTICS_STACK[i].statistics.times;
            }
            return times;
        }
    },
    mounted(){
        console.log(this.$store.battle.Statistics.dataMatrices)
    }
}
</script>

<style lang="less" scoped>
.active{
    background: #440101;
}
.command{
    background: rgb(2, 65, 2);
}
.combo{
    background: rgb(56, 0, 56);
}
.passive{
    background: rgb(0, 0, 90);
}
.damage{
    color: rgb(251, 255, 44);
}
.cure_color{
    color: greenyellow;
}
.light_color{
    color: white;
}
.dark_color{
    color: rgb(216, 216, 216);
}
.statistics_panel{
    position: absolute; left: 0; top : 0;
    width : 100%; height : 100%;
    background: url('../../assets/background/bg (1).jpg')right bottom no-repeat;
    background-size: cover;
    .head{
        left: 0; top: 0;
        width: 100vw;height: 12vh;
        background: url('../../assets/background/bg (8).png')rgb(21, 21, 21) no-repeat;
        background-size: 100% 100%;
    }
    
    .container{
        position: absolute; left: 0; top : 12vh;
        
        width: 100vw;height: 70vh;
        // background: #000;
        .title{
            left: 0; top: 0;
            width: 100vw;height: 8vw;
            background: rgba(0, 0, 0, 0.5);
            z-index: 1;
            li,.tact_li{
                position: relative;float: left;
                line-height: 8vw;
                width: 20vw;height: 8vw;
            }
            .tact_li{
                width: 25vw;
            }
        }
        .teams_container{
            position: relative;
            width: 100vw;height: 60vw;
            margin: 8vw 0 0 0;
            .warrior_info{
                position: relative;
                width: 100vw;height: 18vw;
                background: rgba(0, 0, 0, 0.1);
                border-bottom:.2vw solid rgb(33, 33, 33) ;
                .warrior_head{
                    position: relative;float: left;
                    margin: 0 0 0 2vw;
                    width: 18vw;height: 18vw;
                    .warrior_img_bg_blue,.warrior_img_bg_red{
                        position: absolute; left: 50%; top : 50%;
                        transform : translate(-50%,-50%);
                        width: 66%;height: 66%;
                        background: rgb(162, 4, 4);
                        // border-radius: 50%;
                    }
                    .warrior_img_bg_blue{
                        background: rgb(0, 84, 153);
                    }
                    img{
                        transform: scale(.6);
                        // border-radius: 50%;
                    }
                }
                .attack_damage,.tactics_damage,.cure,.ratio{
                    position: relative;float: left;
                    width: 20vw;height: 18vw;
                    line-height: 18vw;
                }
                
                .tactics_box{
                    position: relative; float: left;
                    margin: 3vw 0 0 0;
                    width: 25vw;height: 15vw;
                    .name,.make_damage,.make_cure{
                        position: relative;float: left;
                        margin: 0 0 0 1vw;
                        width: 23vw; height: 5vw;
                        text-align: left;
                        text-indent: 1vw;
                        line-height: 5vw;
                        .title{
                            color: gray;
                        }
                    }
                    .name{
                        color: rgb(213, 213, 213);
                        margin: 0 0 .5vw 1vw;
                        .time{
                            position: absolute; right: 1vw; left: auto;top: 0;
                            text-align: right;
                            height: 5vw; line-height: 5vw;
                            color: rgb(167, 167, 167);
                        }
                        // background: #2d0000;
                    }
                    .make_damage,.make_cure{
                        height: 4vw; line-height: 4vw;
                    }
                    
                }
            }
        }
        .exchange_style{
            position: relative;
            margin: 0 0 0 0;
            width: 100vw; height: 8vw;
            background: rgba(0, 0, 0, 0.4);
            .btn{
                position: relative;  float: left;
                width: 25vw; height: 8vw;
                line-height: 8vw;
                color: rgb(113, 113, 113);
                &.current{
                    color: white;
                }
            }
        }
    }
}
</style>