<template>
    <div class="inventory_view" ref="inventory_view">
        <div class="inventory_header">
            <div class="list" v-for="n,index in navList" :key="n.index" @click="ExchangeNav(index)">
                <div class="txt">{{ n }}</div>
                <img src="../../assets/background/bg (10).png" class="current_img" v-if="navCurrentIdx == index" alt="">
            </div>
        </div>
        <!-- 全部战法容器 -->
        <div class="tactics_container" v-if="navCurrentIdx == 0">
            <div class="part" v-for="(p,item,id) in $store.tactics" :key="p.index">
                <div class="border"></div>
                <div class="topic">
                    {{ topic[id] }}
                </div>
                <div class="tactics_box" v-for="t in p" :key="t.index" @click="CheckOrUnlock(t)">
                    <div class="icon">
                        <TacticsIcon :tacticsData="t" />
                        <div class="learned_mark" v-if="t.isload != -1">已学习</div>
                    </div>
                    <div class="skill_mask" v-if="t.lock"></div>
                </div>
            </div>
        </div>

        <!-- 全部宝物定义好的容器 -->
        <div class="artifacts_container" v-if="navCurrentIdx == 1">
            <div class="artifact_item" v-for="art in $store.userArtifacts" :key="art.index"
            @click="CheckArtifact(art)">
                <ArtifactCard :artifactData="art" />
                <div class="load_warrior_head" v-if="art.warriorId != -1">
                    <img :src="$store.warriors[$store.warriorIdxTable[art.warriorId]].img.head" class="cover" alt="">
                    <div class="frame"></div>
                </div>
            </div>
        </div>


        <!-- 控制是否解锁技能 -->
       <div class="unlock_skill_control" v-if="navCurrentIdx == 0">
           <div class="slide_block" ref="slide_block"></div>
           <div class="view_model" @click="ExchangeModel(false)">预览模式</div>
           <div class="unlock_model" @click="ExchangeModel(true)">解锁技能</div>
       </div>
       <div class="define_new_art_btn" ref="define_new_art_btn"
       @touchstart="$refs.define_new_art_btn.classList.add('click')"
       @touchend="$refs.define_new_art_btn.classList.remove('click')"
       @click="ConfigArtifact()"
       v-show="navCurrentIdx == 1">自定义宝物</div>
       <TopNav title="库 藏" />
       <router-view/>
    </div>
</template>

<script>
import TopNav from '@/components/container/TopNav.vue';
import TacticsIcon from '../../components/style/TacticsIcon.vue';
import ArtifactCard from '@/components/style/ArtifactCard.vue';
export default {
    name : 'Inventory',
    components:{
        TopNav,
        TacticsIcon,
        ArtifactCard
    },
    data(){
        return{
            navList : ['战法',"宝物"],
            navCurrentIdx : 0,
            topic : ['指挥战法',"主动战法","追击战法","被动战法"],
            isUnlock : false,
        }
    },
    methods:{
        //查看宝物
        CheckArtifact(art){
            this.$store.buffer.curArtifact = art;
            this.$router.push({
                path : `${this.$route.path}/artifact_info`,
            })
        },
        //查看战法详情或解锁战法
        CheckOrUnlock(tact){
            this.$store.buffer.curTactics = tact;
            //查看战法
            if(!this.isUnlock){
                this.$router.push({
                    path : `${this.$route.path}/tactics_info`,
                    query : {
                        model : 'show',
                    }
                })
            }
            else{
                //该战法已被分配给武将
                if(this.$store.buffer.curTactics.isload != -1){
                    let target = this.$store.warriors[this.$store.warriorIdxTable[this.$store.buffer.curTactics.isload]];
                    let tactIdx = -1
                    for(let i=0;i<target.tacticsArr.length;i++){
                        if(target.tacticsArr[i] == this.$store.buffer.curTactics){
                            tactIdx = i
                        }
                    }
                    this.$store.warriors[this.$store.warriorIdxTable[this.$store.buffer.curTactics.isload]].UnLoadTactics(tactIdx,this.$store.buffer.curTactics)
                    this.$store.dirty.warrior = true
                }
                this.$store.buffer.curTactics.lock = !this.$store.buffer.curTactics.lock;
                //开启脏位
                this.$store.dirty.tactics = true
            }
        },
        //切换模式
        ExchangeModel(unlock){
            if(unlock) this.$refs.slide_block.classList.add('active');
            else this.$refs.slide_block.classList.remove('active');
            this.isUnlock = unlock;
        },
        //切换导航
        ExchangeNav(idx){
            this.navCurrentIdx = idx
        },

        //配置宝物
        ConfigArtifact(){
            this.$router.push({
                path : `${this.$route.path}/config_artifact`,
            })
        }
    },
    mounted(){
        this.$store.AutoStyle(this.$refs.inventory_view)
    }
}
</script>

