<template>
    <div class="artifact_card" v-if="artifactData">
        <div class="img_container ">
            <img :src="artifactData.img" loading="lazy" alt="" class="artifact_img">
            <div class="name">
                {{ artifactData.name }}
            </div>
            <img src="../../assets/background/bg (37).png" alt="" class="cover">
            <img :src="AritifactImgIcon" alt="" class="art_up_sign">
            <div class="forge_eff">
                <img :src="ForgeEffImgIcon" alt="" class="cover">
                <div class="text">{{ artifactData.forgeEffArr[artifactData.forgeChoseIdx].name }}</div>
            </div>
        </div>
        
    </div>
</template>

<script>
export default {
    name : 'ArtifactCard',
    props: {
        artifactData: null,
        // cardStyle: {
        //     default : "4_7",
        //     validator(value) {
        //         return ["3_1", "2_1"].includes(value);
        //     }
        // },
        // showUpArms:{
        //     type:Boolean,
        //     default : false,
        // }
    },
    computed:{
        AritifactImgIcon(){
            switch(this.artifactData.effQualityIdx){
                case 0 :
                case 1 : return require('../../assets/ui/ui (65).png');
                case 2 : return require('../../assets/ui/ui (73).png');
            }
        },
        ForgeEffImgIcon(){
            switch(this.artifactData.effQualityIdx){
                case 0 : return require('../../assets/ui/ui (182).png');
                case 1 : return require('../../assets/ui/ui (184).png');
                case 2 : return require('../../assets/ui/ui (183).png');
            }
        }
    },
    methods:{
        
    }
}
</script>

<style lang="less" scoped>
.artifact_card{
    position: absolute; left: 0; top: 0;
    width: 100%; height: 100%;
    font-size: 100%;
    line-height: 100%;
    .img_container{
        position: absolute; left: 0; top: 0;
        width: 100%; height: 100%;
        overflow: hidden;
        font-size: 100%;
        .artifact_img{
            width: 100%; 
            left: 0; top: 50%;
            transform: translate(0,-50%);
        }
        .art_up_sign{
            right: 3%; top: 4%;
            width: 25%; 
            aspect-ratio: 1/1;
        }
        .name{
            position: absolute; left: 0; bottom: 0;
            width: 100%; 
            border-top: .1vw solid rgb(154, 110, 0);
            line-height: 150%;
            // aspect-ratio: 4/1; /* 宽度:高度 = 4：1 */
            background: url('../../assets/ui/ui\ \(186\).png');
            background-size: 100% 100%;
            font-family: 'FZLBJW';
            font-size: 80%;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .forge_eff{
            left: 5%; bottom: 0;
            width: 50%; 
            aspect-ratio: 4/1;
            font-family: 'FZLBJW';
            margin-bottom: 30%;
            font-size: 100%;
            .text{
                position: absolute; left: 0; top: 0;
                width: 100%; height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 80%;
                color: white;
            }
        }
    }
}

</style>